import { PlayerGemsDisplay } from "components/GemDisplay";
import Tabs from "components/Tabs";
import { opponentOf, PLAYER_SYMBOL } from "engine/types/game-state";
import { usePlayerPerspective } from "stores/ClientGameStore";
import { useIsGameSettingsEnabled } from "stores/ClientGlobalStateStore";
import { useNavStore } from "stores/NavStore";
import { useSelectedPermanent, useTurnStore } from "stores/TurnStore";
import CardActionsDisplay, {
  useSelectedPermanentCardActionData,
} from "./CardActionsDisplay";
import GameLog from "./GameLog";
import GameMenu from "./GameMenu";
import GameSettings from "./GameSettings";
import "./LeftOfFieldDisplay.css";

function LeftOfFieldDisplay() {
  const selectedPermanent = useSelectedPermanent();
  const index = useNavStore((state) => state.leftTabIndex);
  const setIndex = useNavStore((state) => state.setLeftTabIndex);
  const resetCard = useTurnStore((state) => state.resetSelectedCard);
  const isGameSettingsEnabled = useIsGameSettingsEnabled();
  const playerUs = usePlayerPerspective();
  const playerThem = opponentOf(playerUs);
  const selectedPermanentCardActionData = useSelectedPermanentCardActionData();

  return (
    <>
      <Tabs.Container
        className="game-board-left"
        index={index}
        setIndex={(index) => {
          setIndex(index);
          resetCard();
        }}
        items={[
          // keep in sync with NavStore.LeftTab
          {
            label: "üéÆ",
            tooltip: "Menu",
            content: <GameMenu />,
          },
          isGameSettingsEnabled
            ? {
                label: "‚öô",
                tooltip: "Settings",
                content: <GameSettings />,
              }
            : {
                label: "üîí",
                tooltip: "Finish the tutorial to unlock!",
                content: null,
                isDisabled: true,
              },
          {
            label: "üìú",
            tooltip: "Log",
            content: <GameLog />,
          },
        ]}
        onClick={(e) => e.stopPropagation()}
      >
        <div className="game-board-left-content">
          {selectedPermanent ? (
            <CardActionsDisplay fallback={<Tabs.Content />} />
          ) : (
            <Tabs.Content />
          )}
        </div>
        <Tabs.ButtonList className="game-board-left-buttons">
          <Tabs.Buttons
            makeProps={() => ({ style: { pointerEvents: "all" } })}
            overridden={selectedPermanentCardActionData.length > 0}
          />
        </Tabs.ButtonList>
      </Tabs.Container>
      <div className="player-gems">
        <PlayerGemsDisplay
          player={playerThem}
          style={{ flexDirection: "column-reverse" }}
        />
        {PLAYER_SYMBOL[playerThem].dark}
        <div
          style={{ writingMode: "vertical-rl", transform: "rotate(180deg)" }}
        >
          Food
        </div>
        {PLAYER_SYMBOL[playerUs].dark}
        <PlayerGemsDisplay player={playerUs} />
      </div>
    </>
  );
}

export default LeftOfFieldDisplay;
