const FAST_SYNC_DANGER_THRESHOLD = 10 * 1000; // 10s
const SLOW_SYNC_DANGER_THRESHOLD = 5 * 60 * 1000; // 5min

type BigBoardColumnData = {
  colKey: string;
  displayName: string;
  shortName?: string;
  emoji?: string;
  puzzleLink?: {
    battleGroupName: string;
    hasAnswer: boolean;
  };
};

const makeShortName = (displayName: string) => {
  if (displayName.startsWith("PvP")) return "PvP";
  if (displayName.startsWith("Make Your Own")) return "MYO";
  if (displayName.startsWith("Bovine Social Media")) return "BSM";
  if (displayName.startsWith("Console-ation")) return "CP";
  displayName = displayName.replace("(and Logs)", "");
  const ret = [];
  let isLastAlpha = false;
  for (const c of displayName) {
    if (c === "'") continue;
    const isAlpha = c.match(/^[a-zA-Z]$/) !== null;
    if (isAlpha ? !isLastAlpha : c !== " ") ret.push(c);
    isLastAlpha = isAlpha;
  }
  if (ret.length >= 7) return `${ret.slice(0, 4).join("")}...`;
  else return ret.join("");
};

const getDirtyStateEmoji = (dirtyStateType: DirtyStateType): string => {
  switch (dirtyStateType) {
    case DirtyStateType.NEW:
      return " ";
    case DirtyStateType.DIRTY:
      return "‚ö†Ô∏è";
    case DirtyStateType.SYNCING:
      return "‚è≥";
    case DirtyStateType.FAILED:
      return "üí•";
    case DirtyStateType.SYNCED:
      return "‚úÖ";
  }
};

const getPuzzleDisplayName = (
  puzName: string,
  battleGroupName: string,
  battleGroups: { [battleGroupName: string]: BattleGroupData },
  stageNumbers: { [puzName: string]: number }
): string => {
  const { displayName } = battleGroups[battleGroupName];
  const stageNum = stageNumbers[puzName];
  return stageNum === 0 ? displayName : `${displayName} ${stageNum}`;
};

const makePuzColData = (
  puzzle: PuzzleData,
  battleGroups: { [battleGroupName: string]: BattleGroupData },
  stageNumbers: { [puzName: string]: number },
  reactKeyPrefix?: string
): BigBoardColumnData => {
  const { puzName, battleGroupName, answer } = puzzle;
  return {
    colKey: `${reactKeyPrefix ?? "puz"}-${puzName}`,
    displayName: getPuzzleDisplayName(
      puzName,
      battleGroupName,
      battleGroups,
      stageNumbers
    ),
    emoji: battleGroups[battleGroupName].emoji,
    puzzleLink: {
      battleGroupName,
      hasAnswer: answer !== null,
    },
  };
};

const makePuzListColData = (
  puzzles: PuzzleData[],
  battleGroups: { [battleGroupName: string]: BattleGroupData },
  stageNumbers: { [puzName: string]: number },
  reactKeyPrefix?: string
): BigBoardColumnData[] => {
  return puzzles.map((puzzle) =>
    makePuzColData(puzzle, battleGroups, stageNumbers, reactKeyPrefix)
  );
};

