import { useEffect, useRef, useCallback, useLayoutEffect } from "react";
import {
  PVP_MASTERY_ID,
  SOLVE_COUNTS_MASTERY_ID,
} from "engine/puzzles/mastery-data";
import {
  PUZ_NAME_INTRO,
  PUZ_NAME_MASTERY_TREE,
  PUZ_NAME_MASTERY_TREE_PROMPT,
  PUZ_NAME_PVP,
  WEIRD_BATTLE_GROUPS,
} from "engine/puzzles/puzzle-data";
import {
  ClientBattleGroupGlobalState,
  getCompletionVerb,
  GlobalStateBattleRoomStatus,
} from "game-server/global-updates";
import {
  useSubscribedTeam,
  useShouldShowInitMap,
} from "stores/ClientGlobalStateStore";
import Clickable, { ClickablesProps } from "./Clickable";
import ClickableSmall from "./ClickableSmall";
import "./MapDisplay.css";
import { getIcon, scrollToPromise } from "./utils";

const INIT_CLICKABLE = {
  clickableId: PUZ_NAME_INTRO,
  displayName: "OPEN THE TREASURE CHEST",
  isNew: false,
  isMeta: false,
  isBattle: false,
  isCutscene: false,
  isLegendary: false,
  isAnyRoomActive: false,
  isSolved: false,
  mapPos: {
    x: 50,
    y: 72.39819005,
  },
  hasAnswer: false,
  linkTargetOverride: `/game/cutscene/${PUZ_NAME_INTRO}/start`,
  battleGroupName: `${PUZ_NAME_INTRO}-initial`,
  icon: "‚ùó",
};

function MapDisplay({
  windowScrollHeight,
}: {
  windowScrollHeight: number | null;
}) {
  const minimapScreenRef = useRef<HTMLDivElement>(null);
  const subscribedTeam = useSubscribedTeam();
  const shouldShowInitMap = useShouldShowInitMap();
  const puzzles = subscribedTeam?.puzzles ?? null;
  const masteries = subscribedTeam?.masteries ?? null;
  const battleGroups = subscribedTeam?.battleGroups ?? null;

  const isPuzzleStatsEnabled =
    masteries !== null && masteries[SOLVE_COUNTS_MASTERY_ID].isEnabled;
  const isPvPEnabled =
    masteries !== null && masteries[PVP_MASTERY_ID].isEnabled;
  const isFishMetaUnlocked =
    puzzles !== null && puzzles[PUZ_NAME_MASTERY_TREE_PROMPT] !== undefined;

  const getClickableState = (
    battleGroup: ClientBattleGroupGlobalState
  ): ClickablesProps | undefined => {
    if (puzzles === null) return undefined;

    const {
      battleGroupName,
      displayName,
      isCutscene = false,
      isLegendary = false,
      mapPos,
      stats,
    } = battleGroup;

    if (mapPos === undefined) return undefined;
    if (battleGroupName === PUZ_NAME_PVP && !isPvPEnabled) return undefined;
    const puzData = puzzles[battleGroupName];
    // This may become undefined when deleting unlocks.
    if (puzData === undefined) return undefined;

    const { answer, hasAnswer = false, isNew = false, solveTime } = puzData;

    const isSolved = solveTime !== undefined;
    const isMasteryTree = battleGroupName === PUZ_NAME_MASTERY_TREE;
    const isPvP = battleGroupName === PUZ_NAME_PVP;
    const isBattle = !isMasteryTree && !hasAnswer;
    const isAnyRoomActive =
      Object.values(puzzles).find(
        (puzzle) =>
          puzzle.battleGroupName === battleGroupName &&
          puzzle.roomStatus === GlobalStateBattleRoomStatus.ACTIVE
      ) !== undefined;
    const icon = getIcon(puzData, battleGroup);

    return {
      clickableId: battleGroupName,
      displayName,
      answer,
      completionText:
        !isSolved || answer !== undefined
          ? undefined
          : `${getCompletionVerb(battleGroup)}!`,
      isMeta: isMasteryTree && isFishMetaUnlocked,
      isBattle,
      isCutscene,
      isLegendary,
      isNew,
      isSolved,
      mapPos,
      isAnyRoomActive,
      hasAnswer,
      battleGroupName,
      stats:
        isPuzzleStatsEnabled &&
        !isCutscene &&
        !isPvP &&
        !(isMasteryTree && !isFishMetaUnlocked)
          ? stats
          : undefined,
      icon,
    };
  };

  const clickableProps = Object.values(battleGroups ?? {})
    .map(getClickableState)
    .filter((value): value is ClickablesProps => !!value)
    .sort((c1, c2) => c1.mapPos.y - c2.mapPos.y);

  const scrollOffset = (clickable: ClickablesProps) =>
    (clickable.mapPos.y * window.innerWidth) / 100 - window.innerHeight / 2;
  const scrollToId = useCallback(
    async (clickableId: string) => {
      const clickable = clickableProps.find(
        (obj) => obj.clickableId === clickableId
      );
      const clickableElt = document.getElementById(`clickable-${clickableId}`);
      if (!clickable || !clickableElt) return;
      await scrollToPromise(scrollOffset(clickable));
      clickableElt.parentElement?.focus();
    },
    [clickableProps]
  );
  useEffect(() => {
    const handler = ({ key }: KeyboardEvent) => {
      const fudgedOffset = (c: ClickablesProps) =>
        Math.min(
          scrollOffset(c),
          document.body.scrollHeight - window.innerHeight
        );
      const finder = {
        "`": (c: ClickablesProps) => fudgedOffset(c) > window.scrollY + 10,
        "~": (c: ClickablesProps) => fudgedOffset(c) < window.scrollY - 10,
      }[key];
      if (!finder) return;
      const unsolved = clickableProps.filter(
        (clickable) =>
          !clickable.isSolved &&
          !WEIRD_BATTLE_GROUPS.includes(clickable.battleGroupName)
      );
      if (!unsolved.length) return;
      if (key === "~") unsolved.reverse();
      const next = unsolved.find(finder) ?? unsolved[0];
      scrollToId(next.clickableId);
    };
    window.addEventListener("keypress", handler);
    return () => window.removeEventListener("keypress", handler);
  }, [clickableProps, scrollToId]);
  useEffect(() => {
    if (windowScrollHeight === null) return;
    const handler = () => {
      if (minimapScreenRef.current) {
        minimapScreenRef.current.style.top = `${
          ((window.scrollY - window.innerWidth * 0.5) /
            (windowScrollHeight - window.innerWidth * 0.5)) *
          100
        }%`;
        minimapScreenRef.current.style.height = `${
          (window.innerHeight /
            ((windowScrollHeight ?? 0) - window.innerWidth * 0.5)) *
          100
        }%`;
      }
    };

    window.addEventListener("scroll", handler, { passive: true });
    window.addEventListener("resize", handler, { passive: true });
    handler();
    return () => {
      window.removeEventListener("scroll", handler);
      window.removeEventListener("resize", handler);
    };
  }, [windowScrollHeight]);
  // Scroll preservation is already handled in React; this is specifically for
  // the case when the user leaves to e.g. a fish puzzle and comes back.
  useEffect(() => {
    const handler = () => {
      if ((windowScrollHeight ?? 0) > window.innerHeight)
        sessionStorage.mapScroll = window.scrollY;
    };
    window.addEventListener("scroll", handler, { passive: true });
    return () => window.removeEventListener("scroll", handler);
  }, [windowScrollHeight]);
  useLayoutEffect(() => {
    if (
      window.scrollY === 0 &&
      sessionStorage.mapScroll &&
      (windowScrollHeight ?? 0) > window.innerHeight
    )
      window.scrollTo(window.scrollX, +sessionStorage.mapScroll);
  }, [windowScrollHeight]);
  // This ensures the map doesn't overflow horizontally. Who knows why we have
  // to do it this way.
  useEffect(() => {
    const pageContents = document.querySelector(
      ".page-contents"
    ) as HTMLDivElement;
    if (!pageContents) return;
    pageContents.style.height = "100%";
    pageContents.style.overflow = "hidden";
    return () => {
      pageContents.style.height = "";
      pageContents.style.overflow = "";
    };
  }, []);

  if (
    subscribedTeam === null ||
    puzzles === null ||
    battleGroups === null ||
    shouldShowInitMap === null
  )
    return null;

  return (
    <>
      <div className="map">
        {shouldShowInitMap ? (
          <Clickable {...INIT_CLICKABLE} />
        ) : (
          clickableProps.map((clickable) => (
            <Clickable key={clickable.clickableId} {...clickable} />
          ))
        )}
      </div>
      {shouldShowInitMap || clickableProps.length <= 5 ? null : (
        <div className="minimap">
          <div className="minimap-background">
            <div className="minimap-note">
              press <kbd>`</kbd> to jump!
            </div>
          </div>
          <div className="minimap-screen" ref={minimapScreenRef} />
          {windowScrollHeight === null
            ? null
            : clickableProps.map((clickable) => (
                <ClickableSmall
                  key={clickable.clickableId}
                  {...clickable}
                  windowScrollHeight={windowScrollHeight}
                  scrollTo={() => scrollToId(clickable.clickableId)}
                />
              ))}
        </div>
      )}
    </>
  );
}

export default MapDisplay;
