import { useEffect, useState } from "react";
import {
  createBrowserRouter,
  RouterProvider,
  Outlet,
  useMatch,
  useSearchParams,
  useParams,
  useNavigate,
  useLocation,
  ScrollRestoration,
} from "react-router-dom";

import "./App.css";

import { TeamListDisplay, TeamPage } from "components/HuntLib";
import Modal from "components/Modal";
import { Spinner } from "components/Loading";
import { Error } from "components/Error";
import PrepPage from "pages/PrepPage";
import GamePage from "pages/GamePage";
import { useAudioEffect } from "pages/GamePageUtil/useAudioEffect";
import StartPrepPage from "pages/StartPrepPage";
import StartBattlePage from "pages/StartBattlePage";
import StartVictoryPage from "pages/StartVictoryPage";
import DeckCollectionPage from "pages/DeckCollectionPage";
import MasteryTreePage from "pages/MasteryTreePage";
import MapPage from "pages/MapPage";
import AdminPage from "pages/AdminPage";
import { ServerHealthPage, BigBoardPage } from "pages/BigBoardPage";
import KerosNotesPage from "pages/KerosNotesPage";

import settings from "settings";
import { makeCardsDB } from "engine/cards/CardsDB";
import { WSErrorCode } from "game-server/ws";
import { ServerInterfaceType } from "game-server/server-interface/ServerInterface";
import { useClientGameStore } from "stores/ClientGameStore";
import {
  useSubscribedTeam,
  useSelectedPuzName,
} from "stores/ClientGlobalStateStore";
import { useServerInteractionStore } from "stores/ServerInteractionStore";
import { useCardsDBStore } from "stores/CardsDBStore";
import { useUserLogStore } from "stores/UserLogStore";
import { useNavStore, usePuzNameIfExists } from "stores/NavStore";

import ALL_CARDS from "engine/cards/all-cards-client";
import {
  PUZ_NAME_INTRO,
  PUZ_NAME_POST_FINAL_BATTLE,
} from "engine/puzzles/puzzle-data";
import ArtCreditsPage from "pages/ArtCreditsPage";
import UnlockPage from "pages/UnlockPage";

function PageBase() {
  const [isRefreshNeeded, setIsRefreshNeeded] = useState(false);
  const [isDisconnected, setDisconnected] = useState(false);
  const [isDownForMaintenance, setIsDownForMaintenance] = useState(false);
  const addToUserLog = useUserLogStore((state) => state.addToUserLog);

  const sendStep = useClientGameStore((state) => state.sendStep);
  const setSendStep = useClientGameStore((state) => state.setSendStep);
  const getServerInterfaceController = useServerInteractionStore(
    (state) => state.getServerInterfaceController
  );

  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const setCardsDB = useCardsDBStore((state) => state.setCardsDB);
  const serverInterfaceController = getServerInterfaceController();
  const [searchParams, setSearchParams] = useSearchParams();
  const isRootPage = useMatch("/game");
  const navigate = useNavigate();
  const setNavPuzName = useNavStore((state) => state.setNavPuzName);

  useEffect(() => {
    setCardsDB(makeCardsDB(ALL_CARDS));
  }, [setCardsDB]);

  useEffect(() => {
    if (serverInterfaceController === null) return;

    const cleanupOnError = serverInterfaceController.onErrorEvent.addListener(
      (err) => {
        addToUserLog(`Received error "${err.errCode}".`);
      }
    );
    const cleanupAuthEffect = serverInterfaceController.addAuthEffect(() => {
      addToUserLog(`Connected to server.`);
    });
    const cleanupAfterDisconnect =
      serverInterfaceController.connectionControllers[
        ServerInterfaceType.MAIN
      ].afterDisconnectEvent.addListener(
        ({ numReconnections, reconnectDelayInSeconds }) => {
          setDisconnected(reconnectDelayInSeconds < 0);
          if (numReconnections === 0) {
            addToUserLog(`Disconnected from server. Reconnecting...`);
          } else if (reconnectDelayInSeconds < 0) {
            addToUserLog(`Reconnection failed; giving up.`);
          } else {
            addToUserLog(
              `Failed to reconnect. Reconnecting in ${reconnectDelayInSeconds} seconds...`
            );
          }
        }
      );

    return () => {
      cleanupOnError();
      cleanupAuthEffect();
      cleanupAfterDisconnect();
    };
  }, [serverInterfaceController, addToUserLog]);

  useEffect(() => {
    if (serverInterfaceController === null) return;

    return serverInterfaceController.addStuckEffect((err) => {
      switch (err.errCode) {
        case WSErrorCode.TEAM_TEMP_BLOCKED: {
          setIsDownForMaintenance(true);
          break;
        }
        default: {
          setIsRefreshNeeded(true);
        }
      }
    });
  }, [serverInterfaceController, setIsRefreshNeeded, setIsDownForMaintenance]);

  useEffect(() => {
    document.documentElement.classList.add("game-base");
    return () => document.documentElement.classList.remove("game-base");
  }, []);

  useEffect(() => {
    if (isRootPage) {
      navigate(
        {
          pathname: "map",
          search: searchParams.toString(),
        },
        { replace: true }
      );
    }
  }, [isRootPage, navigate, searchParams]);

  if (Object.keys(cardsDB).length === 0) return <Spinner size={128} />;

  return (
    <div className="page-contents">
      <Modal isOpen={isDisconnected || isRefreshNeeded} title="Disconnected">
        Sorry, something went wrong.
        <br />
        Please refresh the page.
      </Modal>
      <Modal isOpen={isDownForMaintenance} title="Down for Maintenance">
        The site is down for maintenance.
        <br />
        Please try again after a while, and monitor your email for updates!
      </Modal>
      <Outlet />
      <ScrollRestoration getKey={(location) => location.pathname} />
    </div>
  );
}

function NavWrapper() {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const location = useLocation();
  const setHasRouter = useNavStore((state) => state.setHasRouter);
  const routerNavigatePath = useNavStore((state) => state.routerNavigatePath);
  const setRouterNavigatePath = useNavStore(
    (state) => state.setRouterNavigatePath
  );
  const selectedPuzName = useSelectedPuzName();
  const pagePuzName = usePuzNameIfExists();
  const puzName = selectedPuzName ?? pagePuzName;
  const setNavPuzName = useNavStore((state) => state.setNavPuzName);

  useEffect(() => {
    setHasRouter(true);
  }, [setHasRouter]);

  useEffect(() => {
    if (routerNavigatePath === null) return;
    navigate({
      pathname: routerNavigatePath,
      search: searchParams.toString(),
    });
    setRouterNavigatePath(null);
  }, [routerNavigatePath, setRouterNavigatePath, searchParams, navigate]);

  useEffect(() => {
    setNavPuzName(puzName);
  }, [puzName, setNavPuzName]);

  useEffect(() => {
    const tray = document.querySelector(".tray") as HTMLElement;
    tray.style.transform = "";
  }, [location]);

  const { teamName } = useParams();
  const subscribedTeam = useSubscribedTeam();
  useEffect(() => {
    document.title = (() => {
      if (teamName) return teamName;
      if (subscribedTeam) {
        const { puzzles, battleGroups } = subscribedTeam;
        if (puzName) {
          const name =
            battleGroups[puzzles[puzName]?.battleGroupName]?.displayName;
          if (name) return name;
        }
        if (puzzles[PUZ_NAME_INTRO]?.solveTime) return "GalactiCardCaptors‚ú®Ô∏è";
      }
      return settings.documentTitle;
    })();
  }, [puzName, teamName, subscribedTeam]);

  useEffect(() => {
    if (subscribedTeam?.puzzles[PUZ_NAME_POST_FINAL_BATTLE]?.solveTime)
      (document.querySelector("link[rel=icon]") as HTMLLinkElement).href =
        "/static/images/favicon.56ccd37f.ico";
  }, [subscribedTeam]);

  useAudioEffect();

  return <Outlet />;
}

const router = createBrowserRouter([
  {
    path: "/",
    element: <NavWrapper />,
    errorElement: <Error />,
    children: [
      {
        path: "game",
        element: <PageBase />,
        children: [
          {
            path: "prep/:puzName",
            element: <PrepPage />,
          },
          {
            path: "prep/:puzName/start",
            element: <StartPrepPage />,
          },
          ...["battle", "cutscene"].flatMap((subpath) => [
            {
              path: `${subpath}/:puzName`,
              element: <GamePage />,
            },
            {
              path: `${subpath}/:puzName/start`,
              element: <StartBattlePage />,
            },
            {
              path: `${subpath}/:puzName/start/:slot`,
              element: <StartBattlePage />,
            },
            {
              path: `${subpath}/:puzName/restart/:slot`,
              element: <StartBattlePage restart={true} />,
            },
          ]),
          {
            path: "spectate/:puzName/:spectateTeamId",
            element: <GamePage />,
          },
          {
            path: "deck/:slot/:puzName",
            element: <DeckCollectionPage />,
          },
          {
            path: "mastery",
            element: <MasteryTreePage />,
          },
          {
            path: "mastery/:puzName",
            element: <MasteryTreePage />,
          },
          {
            path: "victory/start",
            element: <StartVictoryPage />,
          },
          {
            path: "map",
            element: <MapPage />,
          },
          {
            path: "admin",
            element: <AdminPage />,
          },
          {
            path: "unlock",
            element: <UnlockPage />,
          },
          {
            path: "bigboard",
            element: <BigBoardPage />,
          },
          {
            path: "health",
            element: <ServerHealthPage />,
          },
        ],
      },
      {
        path: "game/keros_notes",
        element: <KerosNotesPage />,
      },
      {
        path: "game/art_credits",
        element: <ArtCreditsPage />,
      },
      {
        path: "teams",
        element: <TeamListDisplay />,
      },
      {
        path: "teams/:mode",
        element: <TeamListDisplay />,
      },
      {
        path: "team/:teamName",
        element: <TeamPage />,
      },
    ],
  },
]);

function App() {
  return <RouterProvider router={router} />;
}

export default App;
