import "./CollectionDisplay.css";
import { useState } from "react";
import { CardSize } from "components/CardDisplay";
import ALL_CARDS from "engine/cards/all-cards-client";
import DeckCardDisplay from "./DeckCardDisplay";
import { isCardValidForDeckbuilding } from "game-server/deckbuilding";
import { useCardsDBStore } from "stores/CardsDBStore";
import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import { CardData, ColorSymbol } from "engine/types/card-data";
import { useStaticInspector } from "stores/ClientGameStore";
import {
  CollectionSort,
  useUserPreferencesStore,
} from "stores/UserPreferencesStore";
import { Spinner } from "components/Loading";

enum CollectionFilter {
  ALL = "all",
  LEGENDARIES = "legendaries",
  NON_LEGENDARIES = "non-legendaries",
  RED = "R",
  YELLOW = "Y",
  GREEN = "G",
  PURPLE = "P",
  WHITEBLACK = "W/B",
}

function colorValue(card: CardData) {
  switch (card.color) {
    case ColorSymbol.RED:
      return 0;
    case ColorSymbol.YELLOW:
      return 1;
    case ColorSymbol.GREEN:
      return 2;
    case ColorSymbol.PURPLE:
      return 3;
    case ColorSymbol.WHITE:
      return 4;
    case ColorSymbol.BLACK:
      return 5;
    case ColorSymbol.WHITEBLACK:
      return 6;
    default:
      return 7;
  }
}

function nameCompare(a: CardData, b: CardData): number {
  return a.name.localeCompare(b.name);
}

function costCompare(a: CardData, b: CardData): number {
  const aCost = a.cost.length;
  const bCost = b.cost.length;
  if (aCost !== bCost) {
    return aCost - bCost;
  }
  return nameCompare(a, b);
}

function colorCompare(a: CardData, b: CardData): number {
  const aVal = colorValue(a);
  const bVal = colorValue(b);
  if (aVal !== bVal) {
    return aVal - bVal;
  }
  return costCompare(a, b);
}

function CollectionDisplay() {
  const showAnimate = useUserPreferencesStore(
    (state) => state.showDeckbuildingAnimation
  );
  const setShowAnimate = useUserPreferencesStore(
    (state) => state.setShowDeckbuildingAnimation
  );
  const size = useUserPreferencesStore((state) => state.cardSize);
  const setSize = useUserPreferencesStore((state) => state.setCardSize);
  const sort = useUserPreferencesStore((state) => state.collectionSort);
  const setSort = useUserPreferencesStore((state) => state.setCollectionSort);
  const [filter, setFilter] = useState(CollectionFilter.ALL);

  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const subscribedTeam = useSubscribedTeam();
  const inspector = useStaticInspector();
  // TODO: don't filter all cards
  // TODO: sorting

  if (subscribedTeam === null) return <Spinner size={128} inline={true} />;

  const cardUnlocks = new Set(Object.keys(subscribedTeam.cardUnlocks));
  const hasLegendary = !!Object.values(subscribedTeam.cardUnlocks).find(
    ({ cardName }) => inspector.isLegendary(cardName)
  );

  const currentSort =
    sort === CollectionSort.ALPHABETICAL
      ? nameCompare
      : sort === CollectionSort.COLOR
      ? colorCompare
      : costCompare;

  return (
    <div className="collection-display">
      <div className="collection-header">
        <div className="collection-options">
          <label htmlFor="sort-filter">
            Filter:{" "}
            <select
              name="sort-filter"
              value={filter}
              onChange={(e) => setFilter(e.target.value as CollectionFilter)}
            >
              <option value={CollectionFilter.ALL}>All</option>
              {hasLegendary ? (
                <>
                  <option value={CollectionFilter.LEGENDARIES}>
                    Legendaries
                  </option>
                  <option value={CollectionFilter.NON_LEGENDARIES}>
                    Non-legendaries
                  </option>
                </>
              ) : null}
              <option value={CollectionFilter.RED}>Boars</option>
              <option value={CollectionFilter.YELLOW}>Bees</option>
              <option value={CollectionFilter.GREEN}>Dinosaurs</option>
              <option value={CollectionFilter.PURPLE}>Dryads</option>
              <option value={CollectionFilter.WHITEBLACK}>Cows</option>
            </select>
          </label>
          <label htmlFor="sort-select">
            Sort:{" "}
            <select
              name="sort-select"
              value={sort}
              onChange={(e) => setSort(e.target.value as CollectionSort)}
            >
              <option value={CollectionSort.ALPHABETICAL}>Alphabetical</option>
              <option value={CollectionSort.COST}>Cost</option>
              <option value={CollectionSort.COLOR}>Faction</option>
            </select>
          </label>
          <label htmlFor="size-select">
            Size:{" "}
            <select
              name="size-select"
              value={size}
              onChange={(e) => setSize(e.target.value as CardSize)}
            >
              <option value={CardSize.LARGE}>Large</option>
              <option value={CardSize.NORMAL}>Medium</option>
              <option value={CardSize.SMALL_FIELD}>Small</option>
            </select>
          </label>
          <label htmlFor="toggle-animate">
            Animate:{" "}
            <input
              type="checkbox"
              name="toggle-animate"
              checked={showAnimate}
              onChange={() => setShowAnimate(!showAnimate)}
            />
          </label>
        </div>
      </div>
      <div className={`collection-cards ${size}`}>
        {ALL_CARDS.filter((cardData) =>
          isCardValidForDeckbuilding(cardData.name, cardUnlocks, cardsDB)
        )
          .filter(
            (cardData) =>
              filter === CollectionFilter.ALL ||
              (filter === CollectionFilter.LEGENDARIES &&
                inspector.isLegendary(cardData)) ||
              (filter === CollectionFilter.NON_LEGENDARIES &&
                !inspector.isLegendary(cardData)) ||
              // @ts-expect-error: enum strings manually match
              filter === cardData.color ||
              (filter === CollectionFilter.WHITEBLACK &&
                (cardData.color === ColorSymbol.WHITE ||
                  cardData.color === ColorSymbol.BLACK))
          )
          .sort(currentSort)
          .map(({ name }) => (
            <DeckCardDisplay key={name} name={name} size={size} />
          ))}
      </div>
    </div>
  );
}

export default CollectionDisplay;
