import { motion } from "framer-motion";
import { colorHash } from "./utils";

function CursorClick({
  connId,
  x,
  y,
}: {
  connId: string;
  x: number;
  y: number;
}) {
  const color = colorHash(connId, 85, 0.08);

  return (
    <motion.div
      style={{
        borderColor: color,
        transform: `translateX(${x}px) translateY(${y}px)`,
      }}
      className="presence-cursor-click"
      initial={{ opacity: 1, scale: 0, x, y }}
      exit={{ opacity: 0, scale: 1, x, y }}
      transition={{ duration: 0.4 }}
    />
  );
}

export default CursorClick;
