import { PUZ_NAME_FINAL_BATTLE } from "engine/puzzles/puzzle-data";
import { useClientGameStore } from "stores/ClientGameStore";
import { useRoomBattleGroupName } from "stores/ClientGlobalStateStore";
import BattlerImage from "components/BattlerImage";
import KerodosAnim from "components/KerodosAnim";
import CutsceneDialogBox from "components/CutsceneDialogBox";
import "./AIBattlerDisplay.css";
import { cssIdFor, Speaker, UIElement } from "engine/types/keyframes";
import { useUserPreferencesStore } from "stores/UserPreferencesStore";

/**
 * AI battlers:
 * display image of enemy, chat with flavortext, etc.
 */
const AIBattler = () => {
  const gameState = useClientGameStore((state) => state.gameState);
  const animKeroDos = useUserPreferencesStore((state) => state.animKeroDos);

  const battleGroupName = useRoomBattleGroupName();
  if (battleGroupName === null) return null;

  const { keyframe } = gameState;
  const {
    id: keyframeId,
    dialogue,
    speaker,
    hideBattler = false,
  } = keyframe ?? {};
  const chat =
    speaker !== undefined &&
    [Speaker.ENEMY, Speaker.NARRATOR].includes(speaker) &&
    dialogue;
  const isFinalBattle = battleGroupName === PUZ_NAME_FINAL_BATTLE;

  return (
    <div className={`enemy-battler${chat ? " talking" : ""}`}>
      {hideBattler && keyframe !== undefined ? (
        <CutsceneDialogBox keyframe={keyframe} />
      ) : (
        <div
          className="enemy-battler-image-wrapper"
          id={cssIdFor.element(UIElement.AI_BATTLER)}
        >
          {isFinalBattle && animKeroDos ? (
            <KerodosAnim />
          ) : (
            <BattlerImage battleGroupName={battleGroupName} isInBattle={true} />
          )}
        </div>
      )}
    </div>
  );
};

export default AIBattler;
