import { AnimatePresence, motion } from "framer-motion";
import { useState, useEffect, memo, MouseEventHandler } from "react";

import CardDisplay, { CardSize } from "components/CardDisplay";
import { HandCard, Player } from "engine/types/game-state";
import { cssIdFor } from "engine/types/keyframes";
import { useClientGameStore } from "stores/ClientGameStore";
import { useKeyboardCursorStore } from "stores/KeyboardCursorStore";
import { SelectedCardType, useTurnStore } from "stores/TurnStore";
import {
  useResetEffectOpts,
  useResetSelectedCard,
  useSelectCardHand,
  useSelectCardHandAllCardsAvailable,
} from "stores/UserGameActionHandlers";
import "./HandCardsDisplay.css";
import { useAnimationSpeed } from "stores/AnimationStore";

export const NORMAL_CARD_LIMIT = 10;

interface HandCardsProps {
  player: Player;
  isAllCardsAvailable: boolean;
  cards: ReadonlyArray<HandCard>;
  isSmall: boolean;
  isTurnPending: boolean;
}

const HandCardsDisplay = memo((props: HandCardsProps) => {
  const { player, isAllCardsAvailable, cards, isSmall, isTurnPending } = props;
  const selectedCard = useTurnStore((state) => state.selectedCard);
  const resetSelectedCard = useResetSelectedCard();

  const resetEffectOpts = useResetEffectOpts();
  const setSelectedCardHand = useSelectCardHand();
  const setSelectedCardHandAllCardsAvailable =
    useSelectCardHandAllCardsAvailable();
  const roomId = useClientGameStore((state) => state.roomId);
  const focusHandCardInfo = useKeyboardCursorStore((state) =>
    state.getHandCardWithFocus()
  );
  const speed = useAnimationSpeed();
  const [cssWidth, setCssWidth] = useState<string | null>(null);

  const isSelected = (handCard: HandCard) =>
    selectedCard !== null &&
    selectedCard.type == SelectedCardType.HAND &&
    selectedCard.player == player &&
    ((selectedCard.handCardId !== undefined &&
      selectedCard.handCardId === handCard.id) ||
      (selectedCard.name !== undefined &&
        selectedCard.name === handCard.card.name));

  const isKeyboardFocused = (idx: number) =>
    !!focusHandCardInfo &&
    focusHandCardInfo.player === player &&
    focusHandCardInfo.index === idx;

  const onClick =
    (handCard: HandCard): MouseEventHandler<HTMLDivElement> =>
    (e) => {
      e.stopPropagation();
      if (isSelected(handCard)) {
        resetSelectedCard();
      } else if (isAllCardsAvailable && handCard.card.name !== undefined) {
        setSelectedCardHandAllCardsAvailable(player, handCard.card.name);
      } else {
        setSelectedCardHand(player, handCard.id);
      }
      resetEffectOpts();
    };

  useEffect(() => {
    if (isSmall) setCssWidth("calc(var(--card-normal-width) * 0.3)");
    else {
      setCssWidth(
        `calc(var(--card-normal-width) * (${
          4 + Math.max(cards.length - NORMAL_CARD_LIMIT, 0) * 0.25
        } / ${Math.max(cards.length - 1, 4)}))`
      );
    }
  }, [cards.length, setCssWidth, isSmall]);

  return (
    <div
      className="game-hand-center"
      style={{
        flexWrap: isSmall ? "wrap" : "nowrap",
      }}
    >
      {cssWidth === null ? null : (
        <AnimatePresence>
          {cards.map((handCard, i) => {
            const selected = isSelected(handCard);
            const keyboardFocused = isKeyboardFocused(i);

            return (
              <motion.div
                id={cssIdFor.handCard(handCard)}
                key={`${roomId ?? "none"}_${handCard.id}`}
                className={`hand-card ${isSmall ? "hand-card-small" : ""} ${
                  selected ? "selected" : ""
                }`}
                onClick={onClick(handCard)}
                animate={{
                  opacity: 1,
                  width: cssWidth,
                  y: isSmall ? 0 : selected ? "1em" : "2em",
                }}
                whileHover={{ y: 0 }}
                transition={{ duration: speed === 0 ? 0 : 0.3 / speed }}
              >
                <CardDisplay
                  cardName={handCard.card.name}
                  size={CardSize.NORMAL}
                  highlighted={selected}
                  keyboardFocused={keyboardFocused}
                  isOwnerTurnPending={isTurnPending}
                />
              </motion.div>
            );
          })}
        </AnimatePresence>
      )}
    </div>
  );
});

HandCardsDisplay.displayName = "HandCardsDisplay";

export default HandCardsDisplay;
