import "./PriorityCard.css";
import CardDisplay, { CardSize } from "components/CardDisplay";
import Tooltip from "components/Tooltip";

interface PriorityCardProps {
  priorityCard?: string;
  isEditingPriority?: boolean;
  startEditingPriority?: () => void;
  resetPriorityCard?: () => void;
}

function PriorityCard(props: PriorityCardProps) {
  const {
    priorityCard,
    isEditingPriority = false,
    startEditingPriority,
    resetPriorityCard,
  } = props;

  return (
    <div
      className={`deck-priority-display ${
        startEditingPriority ? "can-edit" : ""
      }`}
    >
      <Tooltip
        text="The Priority Card is moved to the top of the draw pile at the start of each battle."
        style={{ height: "unset", gridColumn: "2 / 3" }}
      >
        <b>Priority Card:</b>
        {!priorityCard && !isEditingPriority ? " None." : null}
      </Tooltip>
      {!priorityCard && !isEditingPriority && startEditingPriority ? (
        <button onClick={startEditingPriority} style={{ gridColumn: "2 / 3" }}>
          Choose Priority Card
        </button>
      ) : null}
      {!priorityCard && isEditingPriority ? (
        <button onClick={resetPriorityCard} style={{ gridColumn: "2 / 3" }}>
          Cancel
        </button>
      ) : null}
      {priorityCard && !isEditingPriority ? (
        <>
          {startEditingPriority ? (
            <div className="deck-card-quantity" style={{ gridColumn: "1 / 2" }}>
              <button onClick={startEditingPriority}>Change</button>
            </div>
          ) : null}
          <div className="deck-card" style={{ gridColumn: "2 / 3" }}>
            <CardDisplay size={CardSize.SMALL} cardName={priorityCard} />
          </div>
        </>
      ) : null}
      {priorityCard && isEditingPriority ? (
        <button onClick={resetPriorityCard} style={{ gridColumn: "2 /3" }}>
          Remove Priority Card
        </button>
      ) : null}
    </div>
  );
}

export default PriorityCard;
