import { StepType } from "engine/types/steps";
import { Speaker, Keyframe } from "engine/types/keyframes";
import { getKeroLayers } from "engine/cards/card-effects-shared";
import {
  useClientGameStore,
  useInspector,
  useIsLockedBesidesKeyframe,
} from "stores/ClientGameStore";
import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import { getCutsceneArtPath } from "images";
import FormattedText from "components/FormattedText";
import "./CutsceneDialogBox.css";
import Image, { KeroImage } from "./Image";
import "overlayscrollbars/overlayscrollbars.css";
import { OverlayScrollbarsComponent } from "overlayscrollbars-react";
import { defaultOverlayScrollbarsOptions } from "HuntLib";
import { useEffect, useState } from "react";

const CutsceneDialogOption = ({
  content,
  onClick,
  disabled,
}: {
  content: string;
  onClick: () => void;
  disabled: boolean;
}) => {
  const [delayed, setDelayed] = useState(true);

  useEffect(() => {
    setTimeout(() => setDelayed(false), 300);
  }, []);

  return (
    <div
      className={`cutscene-dialog-option`}
      onClick={delayed || disabled ? undefined : onClick}
      style={delayed || disabled ? { cursor: "wait" } : {}}
    >
      <div>{content}</div>
      <div>‚ñ∂</div>
    </div>
  );
};

interface StoryDialogBoxProps {
  dialogue?: string;
  speaker: Speaker | null;
  preventScroll?: boolean;
  /** If not specified, don't show dialog options. */
  dialogOptionsConfig?: {
    keyframeId: string;
    dialogOptions: string[];
  };
}

export const StoryDialogBox = (props: StoryDialogBoxProps) => {
  const {
    dialogue,
    speaker,
    preventScroll = false,
    dialogOptionsConfig,
  } = props;

  const sendStep = useClientGameStore((state) => state.sendStep);
  const subscribedTeam = useSubscribedTeam();
  const isLocked = useIsLockedBesidesKeyframe();

  if (subscribedTeam === null) return null;

  const { cardUnlocks } = subscribedTeam;

  const keroLayers = getKeroLayers(Object.keys(cardUnlocks), 0);

  const speakerImage = ((): string | null => {
    if (speaker === null) return null;
    switch (speaker) {
      case Speaker.NARRATOR:
      case Speaker.ENEMY:
      case Speaker.KERO_DOS:
        return null;
      case Speaker.KERO:
        return "kero.png";
      case Speaker.ANGRY_KERO:
        return "angry-kero.png";
      case Speaker.DINO:
        return "generic-dino.png";
      case Speaker.CAPTAIN_PI:
        return "captain-pi.png";
    }
  })();

  return (
    <div className="cutscene-dialog-box">
      {speaker === Speaker.KERO_DOS ? (
        <KeroImage className="cutscene-speaker" keroLayers={keroLayers} />
      ) : speaker === null || speakerImage === null ? null : (
        <div className="cutscene-speaker">
          <Image
            className={
              [Speaker.CAPTAIN_PI].includes(speaker)
                ? "cutscene-speaker-img-limit-width"
                : "cutscene-speaker-img-limit-height"
            }
            src={getCutsceneArtPath(speakerImage)}
          />
        </div>
      )}
      <div className="cutscene-dialog-main">
        <OverlayScrollbarsComponent
          options={{
            ...defaultOverlayScrollbarsOptions,
            overflow: { y: preventScroll ? "visible" : "scroll" },
          }}
          defer
          className="cutscene-dialogue"
          style={preventScroll ? { overflowY: "initial" } : undefined}
        >
          {dialogue === undefined ? null : (
            <FormattedText
              text={dialogue}
              opts={{
                ignoreKeywords: true,
                lineGap: "0",
              }}
            />
          )}
        </OverlayScrollbarsComponent>
        {dialogOptionsConfig !== undefined ? (
          <div className="cutscene-dialog-options">
            {dialogOptionsConfig.dialogOptions.map(
              (dialogOption, dialogOptionIndex) => (
                <CutsceneDialogOption
                  key={dialogOption}
                  onClick={() => {
                    sendStep?.({
                      type: StepType.ADVANCE_KEYFRAME,
                      keyframeId: dialogOptionsConfig.keyframeId,
                      dialogOptionIndex,
                    });
                  }}
                  content={dialogOption}
                  disabled={isLocked}
                />
              )
            )}
          </div>
        ) : null}
      </div>
    </div>
  );
};

interface CutsceneDialogBoxProps {
  keyframe: Keyframe;
}

const CutsceneDialogBox = (props: CutsceneDialogBoxProps) => {
  const { keyframe } = props;
  const sendStep = useClientGameStore((state) => state.sendStep);
  const inspector = useInspector();
  const isGameActive = inspector.isGameActive();

  const {
    id: keyframeId,
    speaker = null,
    dialogue,
    showNext = false,
    dialogOptions = ["..."],
  } = keyframe;

  // Manually prevent scroll for keyframes with kaomoji at
  // the end that causes weird element sizing overflow issues.
  const preventScroll = [
    "tutorial6-start-no-deck",
    "post-final-battle-start",
  ].includes(keyframeId);

  return (
    <StoryDialogBox
      dialogue={dialogue}
      speaker={speaker}
      preventScroll={preventScroll}
      dialogOptionsConfig={
        showNext && isGameActive
          ? {
              keyframeId,
              dialogOptions,
            }
          : undefined
      }
    />
  );
};

export default CutsceneDialogBox;
