import { useNavigate, useSearchParams } from "react-router-dom";
import { toastr } from "Globals";

import { Player } from "engine/types/game-state";
import BattleBackground from "components/BattleBackground";
import {
  CollectionDisplay,
  DeckDisplay,
  DeckSidebar,
} from "components/DeckCollection";
import { LargeSpinner } from "components/Loading";
import { useEffect } from "react";
import {
  useSubscribedTeam,
  useSelectedDeckData,
  useBattleGroup,
  useBattleGroupName,
  useIsValidBattle,
  useNeedRedirectToPrep,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import { usePuzName } from "stores/NavStore";
import { useTeamId } from "stores/ServerInteractionStore";
import "./DeckCollectionPage.css";

function DeckCollectionPage() {
  const teamId = useTeamId();
  const setSubscribedTeamDecksTeamId = useGlobalSubscriptionStore(
    (state) => state.setSubscribedTeamDecksTeamId
  );
  const setSubscribedTeamBattle = useGlobalSubscriptionStore(
    (state) => state.setSubscribedTeamBattle
  );
  const puzName = usePuzName();
  const { canSelectDeck } = useSelectedDeckData(Player.P1);
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const subscribedTeam = useSubscribedTeam();
  const battleGroup = useBattleGroup();
  const battleGroupName = useBattleGroupName();
  const isValidBattle = useIsValidBattle();
  const needRedirectToPrep = useNeedRedirectToPrep();

  useEffect(() => {
    if (teamId === null) return;
    setSubscribedTeamDecksTeamId(teamId);
  }, [teamId, setSubscribedTeamDecksTeamId]);

  useEffect(() => {
    if (teamId === null) return;
    if (!isValidBattle) return;
    setSubscribedTeamBattle({
      teamId,
      puzName,
    });
  }, [setSubscribedTeamBattle, puzName, teamId, isValidBattle]);

  useEffect(() => {
    if (needRedirectToPrep === null || canSelectDeck === null) return;
    if (needRedirectToPrep || !canSelectDeck) {
      toastr.error(`Invalid battle "${puzName}" for deckbuilding.`);
      navigate({
        pathname: `../prep/${puzName}`,
        search: searchParams.toString(),
      });
    }
  }, [needRedirectToPrep, canSelectDeck, puzName, navigate, searchParams]);

  if (
    canSelectDeck === null ||
    needRedirectToPrep === null ||
    needRedirectToPrep ||
    !canSelectDeck
  )
    return <LargeSpinner />;

  return (
    <>
      <BattleBackground />
      <div className="below-nav">
        <div className="collection-root">
          <DeckDisplay />
          <CollectionDisplay />
          <DeckSidebar />
        </div>
      </div>
    </>
  );
}

export default DeckCollectionPage;
