import "./DeckCardDisplay.css";
import CardDisplay, { CardSize } from "components/CardDisplay";
import { DeckUpdate, getDeckCardCount } from "engine/types/decks";
import { Player } from "engine/types/game-state";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useCardsDBStore } from "stores/CardsDBStore";
import {
  useSubscribedTeam,
  useSubscribedDeck,
} from "stores/ClientGlobalStateStore";
import { useSelectedCardData } from "stores/TurnStore";
import { useSlot } from "stores/NavStore";
import { WSReqType } from "game-server/ws";
import { isCardLegendary, isDeckUpdateValid } from "game-server/deckbuilding";
import { useRef, useEffect } from "react";
import { useUserPreferencesStore } from "stores/UserPreferencesStore";
import {
  useResetSelectedCard,
  useSelectCardHandAllCardsAvailable,
} from "stores/UserGameActionHandlers";
import { Spinner } from "components/Loading";

interface DeckCardDisplayProps {
  name: string;
  size: CardSize;
  isEditingPriority?: boolean;
  resetIsEditingPriority?: () => void;
}

function DeckCardDisplay(props: DeckCardDisplayProps) {
  const { name, size, isEditingPriority, resetIsEditingPriority } = props;
  const serverInterfaceController = useServerInterfaceController();
  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const subscribedTeam = useSubscribedTeam();
  const slot = useSlot();
  const deck = useSubscribedDeck(slot);

  // TODO: should we be using this store?
  const { name: selectedName } = useSelectedCardData() ?? { name: null };
  const setSelected = useSelectCardHandAllCardsAvailable();
  const resetSelected = useResetSelectedCard();

  const showAnimate = useUserPreferencesStore(
    (state) => state.showDeckbuildingAnimation
  );

  const cardRef = useRef<HTMLDivElement>(null);

  const isMouseOver = (mouseX: number, mouseY: number, rect: DOMRect) => {
    return (
      mouseX >= rect.left &&
      mouseX <= rect.right &&
      mouseY >= rect.top &&
      mouseY <= rect.bottom
    );
  };

  const isSelected = selectedName === name;
  const toggleSelected = () =>
    isSelected ? resetSelected() : setSelected(Player.P1, name);

  // WE ARE SO BACK
  const BALATRO_DAMPEN = 10;
  const BALATRO_SHINY_OPACITY = 0.4;

  // lerp a value from ranges x to ranges y
  function interpolate(input: number, x: number[], y: number[]): number {
    if (x.length !== y.length) {
      throw new Error("Input and output arrays must have the same length");
    }

    if (input <= x[0]) {
      return y[0];
    }

    if (input >= x[x.length - 1]) {
      return y[y.length - 1];
    }

    let startIndex = 0;
    while (input > x[startIndex + 1]) {
      startIndex++;
    }

    const endIndex = startIndex + 1;

    const x0 = x[startIndex];
    const x1 = x[endIndex];
    const y0 = y[startIndex];
    const y1 = y[endIndex];

    const interpolatedValue = y0 + ((input - x0) / (x1 - x0)) * (y1 - y0);

    return interpolatedValue;
  }

  // setup BALATRO listener
  useEffect(() => {
    if (!cardRef.current) return;
    if (size !== CardSize.LARGE) return;

    const card = cardRef.current;

    const resetStyle = () => {
      card.style.backgroundImage = "";
      card.style.transform = "rotateX(0deg) rotateY(0deg)";
    };
    resetStyle();

    if (!showAnimate || isSelected) {
      resetStyle();
      return;
    }

    // BALATRO animation
    const handleMouseMove = (e: MouseEvent) => {
      const rect = card.getBoundingClientRect();
      const mouseX = e.clientX;
      const mouseY = e.clientY;

      if (!isMouseOver(mouseX, mouseY, rect)) {
        resetStyle();
        return;
      }

      const rotateX = -(mouseY - rect.top - rect.height / 2) / BALATRO_DAMPEN;
      const rotateY = (mouseX - rect.left - rect.width / 2) / BALATRO_DAMPEN;
      const diagonalMovement = rotateX + rotateY;

      const sheenPosition = interpolate(diagonalMovement, [-5, 5], [-100, 200]);
      const sheenOpacity = interpolate(
        sheenPosition,
        [-100, 49, 50, 51, 200],
        [0, BALATRO_SHINY_OPACITY, 0, BALATRO_SHINY_OPACITY, 0]
      );
      // const sheenOpacity = BALATRO_SHINY_OPACITY;

      const sheenPosition1 = sheenPosition - 50;
      const sheenPosition2 = sheenPosition - 30;
      const sheenPosition3 = sheenPosition - 10;
      const sheenPosition4 = sheenPosition + 10;
      const sheenPosition5 = sheenPosition + 30;
      const sheenPosition6 = sheenPosition + 50;
      const sheenGradient = `linear-gradient(55deg, transparent, rgba(255, 100, 100, ${sheenOpacity}) ${sheenPosition1}%, rgba(255, 255, 100, ${sheenOpacity}) ${sheenPosition2}%, rgba(100, 255, 100, ${sheenOpacity}) ${sheenPosition3}%, rgba(100, 255, 255, ${sheenOpacity}) ${sheenPosition}%, rgba(100, 100, 255, ${sheenOpacity}) ${sheenPosition4}%, rgba(255, 100, 255, ${sheenOpacity}) ${sheenPosition5}%, rgba(255, 100, 100, ${sheenOpacity}) ${sheenPosition6}%, transparent)`;
      card.style.backgroundImage = isCardLegendary(name) ? sheenGradient : "";
      card.style.transform = `rotateX(${rotateX}deg) rotateY(${rotateY}deg)`;
    };

    document.addEventListener("mousemove", handleMouseMove);
    return () => {
      document.removeEventListener("mousemove", handleMouseMove);
    };
  }, [showAnimate, name, size, isSelected]);

  if (subscribedTeam === null || deck === null)
    return <Spinner size={48} inline={true} />;

  const cardUnlocks = new Set(Object.keys(subscribedTeam.cardUnlocks));
  const count = getDeckCardCount(deck, name);

  const increaseCountStep: DeckUpdate = {
    cards: {
      [name]: count + 1,
    },
  };
  const decreaseCountStep: DeckUpdate = {
    cards: {
      [name]: count - 1,
    },
  };
  const increaseCount = () => {
    serverInterfaceController.sendReq({
      type: WSReqType.DECKBUILDING_STEP,
      slot,
      deckUpd: increaseCountStep,
    });
  };
  const decreaseCount = () => {
    serverInterfaceController.sendReq({
      type: WSReqType.DECKBUILDING_STEP,
      slot,
      deckUpd: decreaseCountStep,
    });
  };
  const setPriority = () => {
    serverInterfaceController.sendReq({
      type: WSReqType.DECKBUILDING_STEP,
      slot,
      deckUpd: { priorityCard: name },
    });
    resetIsEditingPriority?.();
  };

  const renderQuantity = () => {
    if (isEditingPriority) {
      return <button onClick={setPriority}>Choose</button>;
    }

    return (
      <>
        <button
          disabled={
            !isDeckUpdateValid(decreaseCountStep, deck, cardUnlocks, cardsDB)
          }
          onClick={decreaseCount}
        >
          ‚óÄ
        </button>
        <div className="deck-card-quantity-value">{count}√ó</div>
        <button
          disabled={
            !isDeckUpdateValid(increaseCountStep, deck, cardUnlocks, cardsDB)
          }
          onClick={increaseCount}
        >
          ‚ñ∂
        </button>
      </>
    );
  };

  return (
    <div className="deck-card-display">
      <div className="deck-card-quantity">{renderQuantity()}</div>
      <div className="deck-card-motion" ref={cardRef}>
        <div className="deck-card" onClick={toggleSelected}>
          <CardDisplay
            highlighted={isSelected}
            size={size}
            cardName={name}
            disableScroll={!isSelected}
          />
        </div>
      </div>
    </div>
  );
}

export default DeckCardDisplay;
