import { useEffect, useMemo } from "react";
import { Spinner } from "components/Loading";
import Tooltip from "components/Tooltip";
import TeamDisplayNameDisplay from "components/TeamDisplayNameDisplay";
import FactionIcon from "components/FactionIcon";
import { speedrunTimeToString } from "game-server/global-updates";
import { TeamSummaryState } from "game-server/monitoring";
import { SYMBOL_INFO } from "engine/types/card-data";
import { Faction, factionToColor } from "engine/types/factions";
import {
  FACTIONS_MASTERY_ID,
  SPEEDRUN_MASTERY_ID,
} from "engine/puzzles/mastery-data";
import { Link, Navigate, useParams, useSearchParams } from "react-router-dom";
import Twemoji from "react-twemoji";
import settings from "settings";
import {
  useClientGlobalStateStore,
  useSubscribedTeam,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import {
  useIsAdmin,
  useServerInteractionStore,
} from "stores/ServerInteractionStore";
import "./TeamListDisplay.css";

function teamToSortKey(
  team: TeamSummaryState,
  isFaction: boolean,
  isSpeedrun: boolean
): number[] {
  const {
    completionTime = Infinity,
    factionScoreContribution,
    lastSolveTime = Infinity,
    numSolves,
    speedrunInfo: { totalTime, numTimes },
    teamId,
  } = team;
  return [
    -(isFaction ? factionScoreContribution : 0),
    -(isSpeedrun ? numTimes : 0),
    isSpeedrun ? totalTime : 0,
    completionTime,
    -numSolves,
    lastSolveTime,
    isNaN(+teamId) ? 0 : +teamId,
  ];
}

export function useSortedTeams(
  faction: Faction | null,
  isSpeedrun: boolean,
  includeHidden?: boolean,
  freezeAfterHuntEnd?: boolean
) {
  const liveTeams = useClientGlobalStateStore(
    (state) => state.globalState.teams
  );

  return useMemo(() => {
    const teams =
      ((freezeAfterHuntEnd ?? false) || settings.isPosthunt) &&
      Object.keys(settings.teamsSnapshot).length > 0
        ? settings.teamsSnapshot
        : liveTeams;
    return Object.values(teams)
      .filter((team) => {
        if (team.isHidden && !(includeHidden ?? false)) return false;
        if (faction !== null)
          return team.faction === faction && team.factionScoreContribution > 0;
        if (isSpeedrun) return team.speedrunInfo.numTimes > 0;
        return true;
      })
      .sort((a, b) => {
        const aKey = teamToSortKey(a, faction !== null, isSpeedrun);
        const bKey = teamToSortKey(b, faction !== null, isSpeedrun);
        for (let i = 0; i < aKey.length; i++) {
          if (aKey[i] !== bKey[i]) return aKey[i] - bKey[i];
        }
        return a.displayName.localeCompare(b.displayName);
      });
  }, [liveTeams, faction, isSpeedrun, includeHidden, freezeAfterHuntEnd]);
}

export function SpeedrunNote({ headerText }: { headerText: string }) {
  // TODO: improve this message?
  return (
    <Tooltip text="Not all subpuzzles count toward the total time.">
      <Twemoji>{headerText} üõà</Twemoji>
    </Tooltip>
  );
}

const TeamListDisplay = () => {
  const teamAdminData = useClientGlobalStateStore(
    (state) => state.globalState.teamAdminData
  );
  const serverInterfaceController = useServerInteractionStore(
    (state) => state.getServerInterfaceController
  )();
  const teamId =
    serverInterfaceController === null
      ? null
      : serverInterfaceController.getTeamId();
  const { setNeedAdmin, setNeedTeamList } = useGlobalSubscriptionStore();
  const isAdmin = useIsAdmin() ?? false;
  const subscribedTeam = useSubscribedTeam();
  const { mode } = useParams();
  const isFaction = mode && Object.values<string>(Faction).includes(mode);
  const isSpeedrun = mode === "speedrun";
  const faction: Faction | null = isFaction ? (mode as Faction) : null;
  const sortedTeams = useSortedTeams(faction, isSpeedrun, false, true);
  const [searchParams] = useSearchParams();

  useEffect(() => {
    if (!isAdmin) return;
    setNeedAdmin(true);
  }, [isAdmin, setNeedAdmin]);

  useEffect(() => {
    setNeedTeamList(true);
    // Don't unset on unmount to prevent extraneous OVERRIDE_STATEs.
    // We don't have unsubscribe anyway so unsetting is strictly worse.
  }, [setNeedTeamList]);

  if (subscribedTeam === null) return <Spinner size={48} />;
  const { masteries, factionScores: factionTotals } = subscribedTeam;
  const factionsEnabled =
    settings.isPublicAccess || masteries[FACTIONS_MASTERY_ID].isEnabled;
  const speedrunsEnabled =
    settings.isPublicAccess || masteries[SPEEDRUN_MASTERY_ID].isEnabled;
  if (
    (isFaction && !factionsEnabled) ||
    (isSpeedrun && !speedrunsEnabled) ||
    (mode && !isFaction && !isSpeedrun)
  ) {
    return <Navigate to="/teams" replace={true} />;
  }

  const timeOpts = {
    hourCycle: "h23",
    timeZoneName: "short",
    month: "short",
    day: "numeric",
    hour: "2-digit",
    minute: "2-digit",
  } as const;

  const factionColor = (faction: Faction, level: number) =>
    SYMBOL_INFO[factionToColor(faction)].color(level);

  const tabsDom = [];

  if (factionsEnabled || speedrunsEnabled)
    tabsDom.push(
      <Link
        key="solves"
        to={{ pathname: "/teams", search: searchParams.toString() }}
        replace={true}
      >
        <h2 className={isFaction || isSpeedrun ? "" : "highlight"}>Solves</h2>
      </Link>
    );

  if (speedrunsEnabled)
    tabsDom.push(
      <Link
        key="speed"
        to={{ pathname: "/teams/speedrun", search: searchParams.toString() }}
        replace={true}
      >
        <h2 className={isSpeedrun ? "highlight" : ""}>Speed</h2>
      </Link>
    );

  if (factionsEnabled)
    tabsDom.push(
      ...Object.values(Faction)
        .sort((fac1, fac2) => factionTotals[fac2] - factionTotals[fac1])
        .map((fac) => (
          <Link
            key={fac}
            to={{
              pathname: fac === faction ? "/teams" : `/teams/${fac}`,
              search: searchParams.toString(),
            }}
            className="teams-faction"
            replace={true}
          >
            <h2
              className={fac === faction ? "highlight" : ""}
              style={{ backgroundColor: factionColor(fac, 600) }}
            >
              <FactionIcon faction={fac} />
            </h2>
          </Link>
        ))
    );

  const teamsDom = sortedTeams.map((team, i) => {
    const {
      numSolves,
      completionTime,
      factionScoreContribution,
      speedrunInfo: { numTimes, totalTime },
    } = team;
    const score = (() => {
      if (isFaction) return factionScoreContribution;
      if (isSpeedrun) return speedrunTimeToString(totalTime);
      if (!completionTime) return null;
      return new Date(completionTime).toLocaleString([], timeOpts);
    })();
    return (
      <tr
        className={`${
          teamId !== null && team.teamId === teamId ? "current-team" : ""
        }`}
        key={team.teamId}
      >
        <td>
          {completionTime === undefined ? null : (
            <Twemoji tag="span">‚úàÔ∏è </Twemoji>
          )}
          {i + 1}
        </td>
        <td>
          <TeamDisplayNameDisplay teamData={team} />
          {(() => {
            // If impersonating, we need to stop impersonating
            // first, otherwise the impersonate button doesn't work.
            if (!isAdmin || settings.isImpersonate) return null;
            const adminData = teamAdminData[team.teamId];
            if (adminData === undefined) return null;
            return (
              <>
                {" "}
                (
                <a
                  href={`${settings.djangoBaseUrl}impersonate/${adminData.userId}`}
                >
                  impersonate
                </a>
                )
              </>
            );
          })()}
        </td>
        <td>{isSpeedrun ? numTimes : numSolves}</td>
        <td>{score}</td>
      </tr>
    );
  });

  const thStyle = {
    backgroundColor: faction ? factionColor(faction, 900) : undefined,
  };
  return (
    <>
      <div className="top-more-actions" />
      <h1>Team Scoreboard</h1>
      <main>
        {tabsDom.length === 0 ? null : (
          <div className="teams-faction-display">{tabsDom}</div>
        )}
        <table className="list-table">
          <colgroup>
            <col style={{ width: "10%" }} />
            <col style={{ width: "60%" }} />
            <col style={{ width: "10%" }} />
            <col style={{ width: "20%" }} />
          </colgroup>
          <tbody>
            <tr>
              <th style={thStyle}>Rank</th>
              <th style={thStyle}>Team</th>
              <th style={thStyle}>Solves</th>
              <th style={thStyle}>
                {faction ? (
                  <>
                    {factionTotals[faction]} <FactionIcon faction={faction} />
                  </>
                ) : isSpeedrun ? (
                  <SpeedrunNote headerText="Total time" />
                ) : (
                  "Finish time"
                )}
              </th>
            </tr>
            {teamsDom}
          </tbody>
        </table>
      </main>
    </>
  );
};

export default TeamListDisplay;
