import { useEffect, useRef, useState } from "react";
import Twemoji from "react-twemoji";

import { RoutedLink } from "components/Link";
import MapDisplay, { ParallaxBackground } from "components/MapDisplay";
import {
  NUM_LEGENDARIES,
  PUZ_NAME_FINAL_BATTLE,
  PUZ_NAME_PRE_FINAL_BATTLE,
} from "engine/puzzles/puzzle-data";
import {
  useSubscribedTeam,
  useShouldShowInitMap,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import "./MapPage.css";
import { Spinner } from "components/Loading";
import settings from "settings";

function MapPage() {
  const [scrollHeight, setScrollHeight] = useState<number | null>(null);
  const [footerHeight, setFooterHeight] = useState<number | null>(180);
  const contentElemRef = useRef<HTMLDivElement>(null);
  const footerElemRef = useRef<HTMLDivElement>(null);

  const subscribedTeam = useSubscribedTeam();
  const shouldShowInitMap = useShouldShowInitMap();
  const puzzles = subscribedTeam?.puzzles ?? null;
  const battleGroups = subscribedTeam?.battleGroups ?? null;

  const { djangoBaseUrl } = settings;

  useEffect(() => {
    const footerElem = footerElemRef.current;
    if (footerElem === null) return;

    const fixHeight = () => {
      setFooterHeight(footerElem.offsetHeight);
    };

    const resizeObserver = new ResizeObserver(fixHeight);
    resizeObserver.observe(footerElem);
    fixHeight();

    return () => {
      resizeObserver.disconnect();
    };
  });

  useEffect(() => {
    if (puzzles === null || battleGroups === null) return;

    const contentElem = contentElemRef.current;
    if (contentElem === null) return;

    const fixHeight = () => {
      setScrollHeight(
        ([...document.querySelectorAll(".map-puz")] as HTMLDivElement[])
          .map((elem) => elem.offsetTop + elem.offsetHeight)
          .reduce((a, b) => Math.max(a, b), 0) + (footerHeight ?? 0)
      );
    };

    const resizeObserver = new ResizeObserver(fixHeight);
    resizeObserver.observe(contentElem);
    fixHeight();

    return () => {
      resizeObserver.disconnect();
    };
  }, [
    footerHeight,
    // We need these dependencies on puzzles/battle groups, since
    // they cause the rendered .map-puz elements to change.
    puzzles,
    battleGroups,
  ]);

  // If a subscription is made invalid due to a puzzle getting set locked,
  // the solver will be redirected to the map page. Clear such problematic
  // subscriptions to prevent further errors.
  const {
    subscribedTeamBattle: subscribedTeamBattleSelection,
    setSubscribedTeamBattle,
    subscribedTeamFishPuzzle: subscribedTeamFishPuzzleSelection,
    setSubscribedTeamFishPuzzle,
  } = useGlobalSubscriptionStore();
  useEffect(() => {
    if (subscribedTeam === null) return;
    if (
      subscribedTeamBattleSelection !== null &&
      subscribedTeam.puzzles[subscribedTeamBattleSelection.puzName] ===
        undefined
    )
      setSubscribedTeamBattle(null);
    if (
      subscribedTeamFishPuzzleSelection !== null &&
      subscribedTeam.puzzles[subscribedTeamFishPuzzleSelection.puzName] ===
        undefined
    )
      setSubscribedTeamFishPuzzle(null);
  }, [
    subscribedTeam,
    subscribedTeamBattleSelection,
    subscribedTeamFishPuzzleSelection,
    setSubscribedTeamBattle,
    setSubscribedTeamFishPuzzle,
  ]);

  if (
    subscribedTeam === null ||
    puzzles === null ||
    battleGroups === null ||
    shouldShowInitMap === null
  )
    return <Spinner size={128} />;

  const numLegendariesBefriended = Object.values(puzzles).filter(
    ({ puzName, solveTime }) =>
      solveTime !== undefined && (battleGroups[puzName]?.isLegendary ?? false)
  ).length;
  const numLegendaryBattlesUnlocked = Object.values(puzzles).filter(
    ({ puzName }) => battleGroups[puzName]?.isLegendary ?? false
  ).length;
  const isFinalBattleUnlocked = puzzles[PUZ_NAME_FINAL_BATTLE] !== undefined;
  const isPreFinalBattleUnlocked =
    puzzles[PUZ_NAME_PRE_FINAL_BATTLE] !== undefined;

  const HUNT_CLOSE_DATE = new Date("2024-01-01T11:11:00+11:00");
  // format as F j, Y \a\t g:i A T to user locale
  const LOCAL_HUNT_CLOSE_DATE =
    "Local time: " +
    HUNT_CLOSE_DATE.toLocaleString(undefined, {
      year: "numeric",
      month: "long",
      day: "numeric",
      hour: "numeric",
      minute: "2-digit",
      timeZoneName: "short",
    });

  return (
    <>
      <ParallaxBackground
        isTitleRevealed={!shouldShowInitMap}
        scrollHeight={scrollHeight}
      />
      <div
        className="map-root"
        style={{
          height: scrollHeight === null ? undefined : `${scrollHeight}px`,
        }}
        ref={contentElemRef}
      >
        {settings.isPosthunt ? (
          <>
            <div className="map-notice">
              <div>
                <span
                  style={{
                    fontWeight: "bold",
                    color: "#f86",
                  }}
                >
                  <Twemoji tag="span">‚ö†Ô∏è</Twemoji> Note to new visitors and
                  postsolvers:
                </span>{" "}
                The <RoutedLink path="/game/unlock">unlock gadget</RoutedLink>{" "}
                is now available. Use it to reset the website state and simulate
                hunt progress.
              </div>
              <div>
                If you‚Äôre unfamiliar with the premise of this hunt, we highly
                recommend using the gadget to start the hunt from the beginning.
                Much of this hunt is designed to be experienced in tandem with
                hunt progress, rather than as independent puzzles.
              </div>
            </div>
          </>
        ) : null}
        <div className="map-notice">
          <div>
            {settings.isPosthunt ? (
              "Galactic Puzzle Hunt 2023 has come to a close. Thanks for puzzling with us!"
            ) : (
              <>
                The Galactic Puzzle Hunt is now over, but you can still register
                and progress through the hunt until{" "}
                <time
                  dateTime="2024-01-01T11:11:00+11:00"
                  data-format="F j, Y \a\t g:i A T"
                  title={LOCAL_HUNT_CLOSE_DATE}
                >
                  December 31, 2023 at 7:11 PM EST
                </time>
                .
              </>
            )}
          </div>
          <div>
            Please fill out our{" "}
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://forms.gle/rCDjii8iMwSRhv5KA"
            >
              feedback form
            </a>{" "}
            and check out our{" "}
            <a
              target="_blank"
              rel="noopener noreferrer"
              href={`${djangoBaseUrl}wrapup`}
            >
              wrap-up
            </a>
            !
          </div>
          <div>
            If you enjoyed our hunt, please consider{" "}
            <a
              target="_blank"
              rel="noopener noreferrer"
              href={`${djangoBaseUrl}donate`}
            >
              donating
            </a>
            .
            <br />
            <b>
              If you&rsquo;d like to hear from us about future events, please
              join our{" "}
              <a
                target="_blank"
                rel="noopener noreferrer"
                href="https://groups.google.com/g/galacticpuzzlehunt"
              >
                interest list
              </a>
              .
            </b>
          </div>
        </div>
        <MapDisplay
          windowScrollHeight={
            // Account for the height of the navbar.
            (scrollHeight ?? 0) + 50
          }
        />
      </div>
      <div
        className="map-footer"
        style={{
          // Offset lower by 1px to get around pixel rounding issues.
          top: `${(scrollHeight ?? 0) - (footerHeight ?? 0) + 1}px`,
        }}
        ref={footerElemRef}
      >
        {numLegendaryBattlesUnlocked === NUM_LEGENDARIES &&
        !isPreFinalBattleUnlocked ? (
          <div className="map-footer-text">
            <div className="map-footer-num-legendaries">
              {numLegendariesBefriended} / {NUM_LEGENDARIES} legendaries
              befriended.
            </div>
            <div>Befriend all the legendaries to enter the water!</div>
          </div>
        ) : null}
        <div className="map-shadow" />
      </div>
    </>
  );
}

export default MapPage;
