import BattlerImage from "components/BattlerImage";
import Image from "components/Image";
import Tooltip from "components/Tooltip";
import { PuzzleLink } from "components/Link";
import {
  MapPosition,
  PUZ_NAME_INTRO,
  BATTLE_GROUP_NAME_MOONICK,
  PUZ_NAME_FINAL_BATTLE,
  PUZ_NAME_MASTERY_TREE,
  PUZ_NAME_PVP,
  BATTLE_GROUP_NAME_INSTANCER,
  WEIRD_BATTLE_GROUPS,
} from "engine/puzzles/puzzle-data";
import { GlobalStatePuzzleStats } from "game-server/global-updates";
import { getBattlerArtPath } from "images";
import Twemoji from "react-twemoji";
import "./Clickable.css";

export interface ClickablesProps {
  clickableId: string;
  displayName: string;
  answer?: string;
  completionText?: string;
  isMeta: boolean;
  isBattle: boolean;
  isCutscene: boolean;
  isLegendary: boolean;
  isNew: boolean;
  isSolved: boolean;
  mapPos: MapPosition;
  isAnyRoomActive: boolean;
  hasAnswer: boolean;
  linkTargetOverride?: string;
  battleGroupName: string;
  stats?: GlobalStatePuzzleStats;
  icon?: string;
}

function Clickable(props: ClickablesProps) {
  const {
    clickableId,
    displayName,
    answer,
    completionText,
    isMeta,
    isBattle,
    isCutscene,
    isLegendary,
    isNew,
    isSolved,
    mapPos,
    isAnyRoomActive,
    battleGroupName,
    hasAnswer,
    linkTargetOverride,
    stats,
    icon,
  } = props;

  const [rightIcon, rightTooltip] = (() => {
    if (isAnyRoomActive) {
      return [
        isSolved ? "üîÅ" : "‚è≥",
        (isCutscene ? "Cutscene in progress!" : "Battle in progress!") +
          (isSolved ? " (replay)" : ""),
      ];
    }
    if (isSolved) return ["‚úÖÔ∏è", ""];
    if (isMeta) return ["‚ìÇÔ∏è", "Metapuzzle"];
    if (isNew && battleGroupName !== BATTLE_GROUP_NAME_INSTANCER)
      return ["üÜï", ""];
    if (["‚ùó", "‚ùì", "ü§º", "üï∏Ô∏è", "üå≤"].includes(icon ?? "")) return [icon, ""];
    return ["", ""];
  })();

  const innerDom = (
    <>
      <div
        id={`clickable-${clickableId}`}
        className={`map-enemy-battler${
          battleGroupName === PUZ_NAME_FINAL_BATTLE
            ? " map-kero"
            : battleGroupName === PUZ_NAME_MASTERY_TREE
            ? " map-mastery-tree"
            : battleGroupName === PUZ_NAME_PVP
            ? " map-pvp"
            : ""
        }`}
        style={{
          zIndex: battleGroupName === "infinitely_many_hogs_and_logs" ? 0 : 1,
        }}
      >
        {WEIRD_BATTLE_GROUPS.includes(battleGroupName) ? (
          <Image
            className="map-weird-img"
            src={getBattlerArtPath(battleGroupName)}
          />
        ) : isCutscene && battleGroupName !== PUZ_NAME_INTRO ? (
          <BattlerImage battleGroupName={"cutscene"} />
        ) : battleGroupName === BATTLE_GROUP_NAME_MOONICK ? (
          <BattlerImage battleGroupName={"moonick-clickable"} />
        ) : (
          <BattlerImage battleGroupName={battleGroupName} />
        )}
      </div>
      <div className="map-puz-label">
        <Twemoji>{icon}</Twemoji>
        <div className="map-puz-title">
          <div>{displayName}</div>
          {answer ? <b>{answer}</b> : null}
          {completionText ? <b>{completionText}</b> : null}
          {stats === undefined ||
          battleGroupName === BATTLE_GROUP_NAME_INSTANCER ? null : (
            <span>
              {stats.solveCount}{" "}
              <small>{stats.solveCount === 1 ? "solve" : "solves"}</small> |{" "}
              {stats.unlockCount}{" "}
              <small>{stats.unlockCount === 1 ? "unlock" : "unlocks"}</small>
            </span>
          )}
        </div>
        <Tooltip text={rightTooltip}>
          <Twemoji>{rightIcon}</Twemoji>
        </Tooltip>
      </div>
    </>
  );

  return (
    <div
      className="map-puz"
      style={{
        backgroundColor: "rgba(0, 255, 0, 0)",
        left: `${mapPos.x}vw`,
        top: `${mapPos.y}vw`,
      }}
    >
      <PuzzleLink
        battleGroupName={battleGroupName}
        hasAnswer={hasAnswer}
        linkTargetOverride={linkTargetOverride}
      >
        {innerDom}
      </PuzzleLink>
    </div>
  );
}

export default Clickable;
