import "./BattleControl.css";
import { useNavigate, useSearchParams } from "react-router-dom";

import { Player } from "engine/types/game-state";
import { getFailText } from "engine/types/action-validation";
import {
  PUZ_NAME_PVP,
  PUZ_NAME_POST_FINAL_BATTLE,
} from "engine/puzzles/puzzle-data";
import { GlobalStateBattleRoomStatus } from "game-server/global-updates";
import { WSReqType } from "game-server/ws";
import {
  useSubscribedTeam,
  useSubscribedBattleStatus,
  useSelectedDeckData,
  useSelectedPuzName,
  useSelectedBattleGroup,
} from "stores/ClientGlobalStateStore";
import { useIsAdminEnabledStore } from "stores/UserPreferencesStore";
import { usePuzName } from "stores/NavStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import Tooltip from "components/Tooltip";
import { useState } from "react";
import { ConfirmModal } from "components/Modal";
import settings from "settings";
import Twemoji from "react-twemoji";

interface BattleControlProps {
  player: Player;
}

function BattleControl(props: BattleControlProps) {
  const { player } = props;
  const serverInterfaceController = useServerInterfaceController();
  const navigate = useNavigate();

  const puzName = usePuzName();
  const selectedPuzName = useSelectedPuzName();
  const isPvP = puzName === PUZ_NAME_PVP;
  const isSpectating = useIsAdminEnabledStore((state) => state.isSpectating);

  const subscribedTeam = useSubscribedTeam();
  const battleGroup = useSelectedBattleGroup();
  const battleStatus = useSubscribedBattleStatus(puzName);
  const selectedDeckData = useSelectedDeckData(player);

  const [searchParams] = useSearchParams();
  const [showEndConfirm, setShowEndConfirm] = useState(false);

  if (battleGroup === null || subscribedTeam === null) return null;

  const { selectedDeckFailedChecks, isDeckLocked } = selectedDeckData;
  const { isCutscene = false } = battleGroup;
  const { puzzles } = subscribedTeam;
  const isSolved =
    selectedPuzName !== null &&
    puzzles[selectedPuzName].solveTime !== undefined;

  const isBattle = !isCutscene && !isPvP;

  const onJoin = () => {
    if (puzName === null) return;
    navigate({
      pathname: `../${isCutscene ? "cutscene" : "battle"}/${puzName}`,
      search: searchParams.toString(),
    });
  };

  const onStart = () => {
    if (puzName === null) return;
    navigate({
      pathname: `../${isCutscene ? "cutscene" : "battle"}/${puzName}/start`,
      search: searchParams.toString(),
    });
  };

  const onEnd = () => {
    if (puzName === null) return;
    serverInterfaceController.sendReq({
      type: WSReqType.RESIGN,
      player,
      puzName,
    });
  };

  const onPvpUnlock = () =>
    serverInterfaceController.sendReq({
      type: WSReqType.PVP_UNLOCK_DECK,
    });

  // text, onClick, label
  const buttons: [string, (() => void) | undefined, string][] = [];

  if (battleStatus === GlobalStateBattleRoomStatus.ACTIVE) {
    buttons.push([
      `Join your teammates in the ${isCutscene ? "cutscene" : "battle"}!`,
      onJoin,
      `Join ${isCutscene ? "cutscene" : "battle"}`,
    ]);
    if (!isPvP) {
      buttons.push([
        `End the ${isCutscene ? "cutscene" : "battle"}.`,
        () => setShowEndConfirm(true),
        "End",
      ]);
    }
  }
  if (battleStatus !== GlobalStateBattleRoomStatus.ACTIVE) {
    if (isPvP && isDeckLocked) {
      buttons.push([
        "Unlock your deck. This cancels any outgoing requests.",
        onPvpUnlock,
        "Unlock deck",
      ]);
    }
    if (!isPvP && !isSpectating) {
      const failText = getFailText(selectedDeckFailedChecks);
      const text =
        selectedPuzName === null
          ? `Select a battle/cutscene.`
          : failText
          ? `${failText} Pick a valid deck.`
          : isSolved
          ? `You‚Äôve already completed this ${
              isCutscene ? "cutscene" : "battle"
            }, but you may ${isCutscene ? "watch" : "play"} it again.`
          : `${isCutscene ? "Watch the cutscene!" : "Start the battle!"}`;
      const label = `${isSolved ? "Replay" : isCutscene ? "Watch" : "Start"} ${
        isCutscene ? "cutscene" : "battle"
      }!`;
      buttons.push([text, failText ? undefined : onStart, label]);
    }
    if (isCutscene) {
      buttons.push(["", undefined, "End"]);
    }
  }
  if (isBattle) {
    switch (battleStatus) {
      case GlobalStateBattleRoomStatus.INACTIVE: {
        buttons.push(["View the final field.", onJoin, "View"]);
        break;
      }
      case GlobalStateBattleRoomStatus.NONE: {
        buttons.push(["", undefined, "End"]);
        break;
      }
    }
  }

  return (
    <>
      <div className="prep-battle-control">
        {selectedPuzName === PUZ_NAME_POST_FINAL_BATTLE && isSolved ? (
          <Tooltip
            text="Skip to end."
            directRef={(props) => (
              <a href={`${settings.djangoBaseUrl}victory`} {...props}>
                <button>
                  <Twemoji style={{ fontSize: "2em" }}>ü•ßÔ∏è</Twemoji>
                </button>
              </a>
            )}
          />
        ) : null}
        {buttons.map(([text, onClick, label]) => (
          <Tooltip
            key={label}
            text={text}
            directRef={(props) => (
              <button onClick={onClick} disabled={!onClick} {...props}>
                {label}
              </button>
            )}
          ></Tooltip>
        ))}
      </div>
      <ConfirmModal
        title="Are you sure?"
        isOpen={showEndConfirm}
        onClose={() => setShowEndConfirm(false)}
        onConfirm={onEnd}
        confirmLabel="End"
      >
        Are you sure you want to end this {isCutscene ? "cutscene" : "battle"}?
      </ConfirmModal>
    </>
  );
}

export default BattleControl;
