import { useEffect } from "react";
import {
  Link,
  useNavigate,
  useSearchParams,
  useParams,
} from "react-router-dom";
import "./SolvedModal.css";

import { Player } from "engine/types/game-state";
import { FactionScoreConstants, calcFactionScore } from "engine/types/factions";
import { Speaker } from "engine/types/keyframes";
import {
  PUZ_NAME_PVP,
  BATTLE_GROUP_NAME_TUTORIAL,
  BATTLE_GROUP_NAME_MOONICK,
} from "engine/puzzles/puzzle-data";
import {
  SPEEDRUN_MASTERY_ID,
  FACTIONS_MASTERY_ID,
} from "engine/puzzles/mastery-data";
import { WSReqType } from "game-server/ws";
import {
  getCompletionVerb,
  speedrunTimeToString,
} from "game-server/global-updates";

import settings from "settings";
import CardDisplay, { CardSize } from "components/CardDisplay";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useCardsDBStore } from "stores/CardsDBStore";
import { makeStaticInspector } from "stores/ClientGameStore";
import {
  useIsInstancer,
  useSubscribedTeam,
  getPuzzlesInBattleGroup,
  useIsAdminOrHuntOver,
} from "stores/ClientGlobalStateStore";
import { useNavStore } from "stores/NavStore";
import SpeedrunLeaderboardSummary from "components/SpeedrunLeaderboardSummary";
import FactionIcon from "components/FactionIcon";
import FactionScoreTotalsDisplay from "components/FactionScoreTotalsDisplay";
import { StoryDialogBox } from "components/CutsceneDialogBox";
import { Spinner } from "./Loading";
import Modal from "./Modal";
import { LoseReason } from "game-server/backend-interface/BackendInterface";

function FactionScoreCalculationDisplay() {
  const solvedModalState = useNavStore((state) => state.solvedModalState);
  const subscribedTeam = useSubscribedTeam();

  if (subscribedTeam === null || solvedModalState === null) return null;
  const { factionScoreContributions } = subscribedTeam;
  const { puzName, teamData, stats, isWinner } = solvedModalState;
  const isPvP = puzName === PUZ_NAME_PVP;
  const { faction } = teamData;
  if (faction === null)
    throw new Error(
      "should only show score computation when player is part of faction"
    );

  // TODO: different DOM for PvP score, which has a multiplicative bonus
  return (
    <div>
      <div className="score-calc-row">
        <div>{isPvP ? "Win" : "Solve"} bonus</div>
        <div>+{FactionScoreConstants.SOLVE_BONUS}</div>
      </div>
      <div className="score-calc-row">
        <div>
          {stats.summons} {stats.summons === 1 ? "card" : "cards"} played
        </div>
        <div>+{stats.summons * FactionScoreConstants.SUMMONS_MULTIPLIER}</div>
      </div>
      {faction !== null ? (
        <div className="score-calc-row">
          <div>
            {stats.factionSummons} <FactionIcon faction={faction} />{" "}
            {stats.factionSummons === 1 ? "card" : "cards"} played
          </div>
          <div>
            +
            {stats.factionSummons *
              FactionScoreConstants.FACTION_SUMMONS_MULTIPLIER}
          </div>
        </div>
      ) : null}
      <div className="score-calc-row">
        <div>
          {stats.destroys} {stats.destroys === 1 ? "unit" : "units"} destroyed
        </div>
        <div>+{stats.destroys * FactionScoreConstants.DESTROYS_MULTIPLIER}</div>
      </div>
      <div className="score-calc-row">
        <div>{stats.damageDealt} total damage dealt</div>
        <div>
          +
          {Math.min(stats.damageDealt, FactionScoreConstants.DAMAGE_DEALT_CAP) *
            FactionScoreConstants.DAMAGE_DEALT_MULTIPLIER}
        </div>
      </div>
      {isPvP ? (
        <div className="score-calc-row">
          <div>PvP bonus</div>
          <div>√ó{FactionScoreConstants.PVP_MULTIPLIER}</div>
        </div>
      ) : null}
      <hr className="score-calc-sep" />
      <div className="score-calc-row">
        <div>
          <FactionIcon faction={faction} /> points from this battle
        </div>
        <div>+{calcFactionScore(stats, isPvP, isWinner)}</div>
      </div>
      <div className="score-calc-row">
        <div>
          Total team <FactionIcon faction={faction} /> points
        </div>
        <div>{factionScoreContributions[faction]}</div>
      </div>
    </div>
  );
}

function SolvedModal() {
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const serverInterfaceController = useServerInterfaceController();
  const isInstancer = useIsInstancer() ?? false;
  const solvedModalState = useNavStore((state) => state.solvedModalState);
  const setSolvedModalState = useNavStore((state) => state.setSolvedModalState);
  const subscribedTeam = useSubscribedTeam();
  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const isAdminOrHuntOver = useIsAdminOrHuntOver();
  const {
    roomId = null,
    roomPuzName = null,
    roomBattleGroupName = null,
  } = solvedModalState ?? {};
  const { puzzles = null } = subscribedTeam ?? {};
  const { spectateTeamId } = useParams();

  // Whether the server has completed registering the solve.
  const isSolveReady =
    spectateTeamId !== undefined ||
    roomPuzName === PUZ_NAME_PVP ||
    (roomPuzName !== null && puzzles?.[roomPuzName].solveTime !== undefined);

  const numPuzzles =
    roomBattleGroupName === null
      ? null
      : subscribedTeam?.battleGroups[roomBattleGroupName]?.numPuzzles ?? null;
  const puzzlesInRoomBattleGroup = getPuzzlesInBattleGroup(
    subscribedTeam,
    roomBattleGroupName
  );
  const roomPuzIndex =
    puzzlesInRoomBattleGroup === null || roomPuzName === null
      ? null
      : puzzlesInRoomBattleGroup.findIndex(
          (puzData) => puzData.puzName === roomPuzName
        );
  const hasNextPuzzle =
    // Omit the next battle button in Moonick since it isn't a
    // linear battle.
    roomBattleGroupName !== BATTLE_GROUP_NAME_MOONICK &&
    numPuzzles !== null &&
    roomPuzIndex !== null &&
    roomPuzIndex !== -1 &&
    roomPuzIndex + 1 < numPuzzles;
  const nextPuzzle = !hasNextPuzzle
    ? null
    : puzzlesInRoomBattleGroup?.[roomPuzIndex + 1]?.puzName ?? null;
  const nextPuzzleNeedsDeck =
    nextPuzzle !== null &&
    makeStaticInspector(nextPuzzle, cardsDB).getDeckOverride(Player.P1) ===
      null;
  const nextPuzzlePath =
    nextPuzzle === null || roomBattleGroupName === null
      ? null
      : nextPuzzleNeedsDeck &&
        roomBattleGroupName !== BATTLE_GROUP_NAME_TUTORIAL
      ? `/prep/${nextPuzzle}`
      : `/battle/${nextPuzzle}/start`;

  useEffect(() => {
    if (roomId === null) return;
    if (nextPuzzlePath === null) return;
    return serverInterfaceController.onNextBattleEvent.addListener(
      (targetRoomId) => {
        if (targetRoomId !== roomId) return;

        navigate({
          pathname: ".." + nextPuzzlePath,
          search: searchParams.toString(),
        });
      }
    );
  }, [
    serverInterfaceController,
    roomId,
    searchParams,
    navigate,
    nextPuzzlePath,
  ]);

  if (
    solvedModalState === null ||
    subscribedTeam === null ||
    puzzles === null ||
    roomPuzName === null ||
    roomBattleGroupName === null ||
    isAdminOrHuntOver === null
  )
    return null;

  // Battle card unlocks may be different from normal card unlocks that
  // arise directly from the solve. They are not currently used but
  // may be used for special card unlocks such as gacha-style unlocks.
  //
  // These systems are separate as it would take a bit of effort to use
  // battle card unlocks for normal card unlocking as we only commit
  // card unlocks until they have been synced to the backend, meaning
  // that we'll have to deal with race conditions.
  //
  // The effect of the current system is that after a battle group is
  // solved, solving any sub-battle in the battle group will show the
  // battle group's card unlocks. Similarly, if a solver is on a
  // sub-battle solved modal and another solver completes the battle
  // group's solve requirement, the card unlocks will appear in the
  // first solver's solved modal as well. This should be fine.
  const {
    puzName,
    cardUnlocks: battleCardUnlocks,
    speedrunTime,
    teamData,
    stats,
    isWinner,
    loseReason,
  } = solvedModalState;
  const { cardUnlocks } = subscribedTeam;
  const roomBattleGroup = subscribedTeam.battleGroups[roomBattleGroupName];
  const { displayName, postBattleDialogue } = roomBattleGroup;
  const { faction, prevBestSpeedrunTime } = teamData;
  const hasSpeedrunMastery =
    teamData.enabledMasteries[SPEEDRUN_MASTERY_ID] ?? false;
  const hasFactionsMastery =
    teamData.enabledMasteries[FACTIONS_MASTERY_ID] ?? false;
  const isPvP = puzName === PUZ_NAME_PVP;
  // TODO: I think we're just assuming we're the winners/solved if we see this, heh
  const factionScoreFromGame = calcFactionScore(stats, isPvP, isWinner);
  const hasFactionScore = hasFactionsMastery && faction !== null;
  const showFactionScore = hasFactionScore && factionScoreFromGame > 0;
  const allCardUnlocks = [
    ...Object.keys(cardUnlocks).filter(
      (cardName) => cardUnlocks[cardName].puzName === roomBattleGroupName
    ),
    // If there are sub-battle reward cards, display them after
    // any battle group reward cards.
    ...(roomBattleGroupName === roomPuzName
      ? []
      : Object.keys(cardUnlocks).filter(
          (cardName) => cardUnlocks[cardName].puzName === roomPuzName
        )),
    ...(battleCardUnlocks ?? []),
  ];
  const isBattleGroupSolved =
    puzzles[roomBattleGroupName].solveTime !== undefined;

  const cardUnlocksDom = allCardUnlocks.map((cardName) => {
    return (
      <CardDisplay
        key={cardName}
        cardName={cardName}
        size={allCardUnlocks.length > 3 ? CardSize.NORMAL : CardSize.LARGE}
        canZoom={false}
      />
    );
  });

  const isSpeedrunNewBest =
    prevBestSpeedrunTime === undefined || speedrunTime < prevBestSpeedrunTime;

  return (
    <Modal
      className="game-solved-modal"
      title={
        isPvP
          ? `You ${isWinner ? "won!" : "lost"}`
          : isBattleGroupSolved
          ? `${displayName} ${getCompletionVerb(roomBattleGroup)}!`
          : "Battle complete!"
      }
    >
      {!isSolveReady ? (
        <div className="game-solved-modal-main">
          <Spinner inline={true} />
        </div>
      ) : (
        <>
          {loseReason === LoseReason.NORMAL ? null : (
            <div className="game-solved-modal-reason">
              You {isWinner ? "won" : "lost"} because{" "}
              {isWinner ? "your opponent " : "you "}
              {loseReason === LoseReason.RESIGN
                ? "resigned"
                : loseReason === LoseReason.TIME
                ? "ran out of time"
                : ""}
              .
            </div>
          )}
          <div className="game-solved-modal-main">
            {allCardUnlocks.length === 0 ? null : (
              <div className="game-solved-modal-cards-display">
                <div className="game-solved-modal-cards">{cardUnlocksDom}</div>
              </div>
            )}
            {hasSpeedrunMastery && !isPvP ? (
              <div className="game-solved-modal-speedrun">
                <div>
                  <div>
                    <b>Time</b>: {speedrunTimeToString(speedrunTime)}{" "}
                    {isSpeedrunNewBest ? "(New team best!)" : null}
                  </div>
                  {prevBestSpeedrunTime === undefined ? null : (
                    <div>
                      <b>{isSpeedrunNewBest ? "Prev. best" : "Team best"}</b>:{" "}
                      {speedrunTimeToString(prevBestSpeedrunTime)}
                    </div>
                  )}
                </div>
                <SpeedrunLeaderboardSummary puzName={roomPuzName} />
              </div>
            ) : null}
            <div
              className={`game-solved-modal-score${
                !showFactionScore ? " centered" : ""
              }`}
            >
              {showFactionScore ? (
                <>
                  <FactionScoreCalculationDisplay />
                  <FactionScoreTotalsDisplay />
                </>
              ) : (
                <div>
                  <div>
                    {stats.summons} {stats.summons === 1 ? "card" : "cards"}{" "}
                    played
                  </div>
                  <div>
                    {stats.destroys} {stats.destroys === 1 ? "unit" : "units"}{" "}
                    destroyed
                  </div>
                  <div>{stats.damageDealt} damage dealt</div>
                </div>
              )}
            </div>
          </div>
          {postBattleDialogue !== undefined ? (
            <div className="game-solved-modal-dialogue">
              <div>
                <StoryDialogBox
                  speaker={Speaker.KERO_DOS}
                  dialogue={postBattleDialogue}
                />
              </div>
            </div>
          ) : null}
        </>
      )}
      <div className="game-solved-modal-buttons">
        <button
          onClick={() => {
            setSolvedModalState(null);
          }}
          style={{ marginRight: "auto" }}
        >
          View board
        </button>
        {!settings.isPosthunt &&
        isBattleGroupSolved &&
        spectateTeamId === undefined ? (
          <a href={`${settings.djangoBaseUrl}solve/${roomBattleGroupName}`}>
            <button>Leave feedback</button>
          </a>
        ) : null}
        {!isPvP && isAdminOrHuntOver ? (
          <>
            <a
              href={`${settings.djangoBaseUrl}solution/${roomBattleGroupName}`}
            >
              <button>Solution</button>
            </a>
            <a href={`${settings.djangoBaseUrl}stats/${roomBattleGroupName}`}>
              <button>Stats</button>
            </a>
          </>
        ) : null}
        {hasNextPuzzle && spectateTeamId === undefined ? (
          <a
            href={
              // Still show the button even if the unlock hasn't arrived.
              nextPuzzlePath === null
                ? "#"
                : `${settings.djangoBaseUrl}game${nextPuzzlePath}`
            }
            onClick={(e) => {
              // Don't send NEXT_BATTLE until we're sure the server has
              // completed unlocking the next puzzle.
              if (nextPuzzlePath === null) return;
              serverInterfaceController.sendReq({
                type: WSReqType.NEXT_BATTLE,
              });
              e.preventDefault();
            }}
          >
            <button
              className={nextPuzzlePath === null ? "loading" : ""}
              disabled={nextPuzzlePath === null}
            >
              Next battle
            </button>
          </a>
        ) : null}
        <Link
          to={{
            pathname: `../prep/${puzName}`,
            search: searchParams.toString(),
          }}
          style={{ marginLeft: "auto" }}
        >
          <button>Exit battle</button>
        </Link>
      </div>
    </Modal>
  );
}

export default SolvedModal;
