import { useEffect } from "react";
import Twemoji from "react-twemoji";
import "./MasteriesExplorer.css";

import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import { useNavStore } from "stores/NavStore";
import { MASTERY_GRID_WIDTH, MASTERY_GRID_HEIGHT } from "game-server/masteries";
import FormattedText from "components/FormattedText";

interface MasteriesExplorerProps {
  enabledMasteries?: { [masteryId: string]: boolean };
}

function MasteriesExplorer(props: MasteriesExplorerProps) {
  const { enabledMasteries } = props;
  const selectedMasteryId = useNavStore((state) => state.selectedMasteryId);
  const setSelectedMasteryId = useNavStore(
    (state) => state.setSelectedMasteryId
  );
  const hoveredMasteryId = useNavStore((state) => state.hoveredMasteryId);
  const setHoveredMasteryId = useNavStore((state) => state.setHoveredMasteryId);

  const subscribedTeam = useSubscribedTeam();

  useEffect(() => {
    return () => {
      setSelectedMasteryId(null);
      setHoveredMasteryId(null);
    };
  }, [setSelectedMasteryId, setHoveredMasteryId]);

  if (subscribedTeam === null) return null;

  const { masteries } = subscribedTeam;

  const selectMasteryDom = Object.values(masteries)
    .sort(
      ({ x: x1, y: y1 }, { x: x2, y: y2 }) =>
        y1 * MASTERY_GRID_WIDTH + x1 - (y2 * MASTERY_GRID_HEIGHT + x2)
    )
    .map(({ masteryId, isEnabled }) => {
      const isSelected =
        selectedMasteryId !== null && masteryId === selectedMasteryId;
      return (
        <div
          key={masteryId}
          className={`select-mastery-button-highlight-wrapper${
            isSelected ? " highlight" : ""
          }`}
        >
          <div
            className={`select-mastery-button-wrapper${
              (enabledMasteries ? enabledMasteries[masteryId] : isEnabled)
                ? " mastery-enabled"
                : ""
            }`}
          >
            <div
              className={`select-mastery-button ${
                isSelected ? "selected" : ""
              }`}
              onClick={() => {
                if (!isSelected) {
                  setSelectedMasteryId(masteryId);
                } else {
                  setSelectedMasteryId(null);
                }
              }}
              onMouseEnter={() => {
                setHoveredMasteryId(masteryId);
              }}
              onMouseLeave={() => {
                setHoveredMasteryId(null);
              }}
            >
              <Twemoji>üÉè</Twemoji>
            </div>
          </div>
        </div>
      );
    });

  const displayedMasteryId = hoveredMasteryId ?? selectedMasteryId;
  const isDisplayedMasteryEnabled =
    displayedMasteryId !== null && masteries[displayedMasteryId].isEnabled;
  const displayName =
    displayedMasteryId === null
      ? ""
      : masteries[displayedMasteryId].displayName ?? "???";
  const effectText =
    displayedMasteryId === null
      ? ""
      : masteries[displayedMasteryId].effectText ?? "???";

  return (
    <div className="mastery-explorer">
      <div
        className={`mastery-details${
          displayedMasteryId === null ? " blank" : ""
        }`}
      >
        <div className="mastery-title">{displayName}</div>
        <div className="mastery-text">
          <FormattedText
            text={effectText}
            opts={{
              ignoreKeywords: true,
            }}
          />
        </div>
      </div>
      <div className="select-mastery-buttons">{selectMasteryDom}</div>
    </div>
  );
}

export default MasteriesExplorer;
