import { PropsWithChildren, useState } from "react";

import "./MessagePopover.css";
import {
  autoUpdate,
  flip,
  FloatingFocusManager,
  offset,
  shift,
  limitShift,
  useFloating,
  Placement,
} from "@floating-ui/react";
import { useMutationObserver } from "stores/NavStore";

interface MessagePopoverProps {
  selector: string;
  placement?: string;
}

/**
 * A popover placed next to an element.
 *
 * Unlike Tooltip, we need to target arbitrary selectors.
 */
const MessagePopover = (props: PropsWithChildren<MessagePopoverProps>) => {
  const { selector, placement = "right", children } = props;
  const [elt, setElt] = useState<HTMLElement | null>(null);
  const [isPositioned, setIsPositioned] = useState(false);
  useMutationObserver(selector, setElt);

  const {
    context,
    refs,
    floatingStyles,
    update: updPos,
  } = useFloating({
    elements: { reference: elt ?? undefined },
    placement: placement as Placement,
    whileElementsMounted: (refEl, floatingEl, updPos) => {
      return autoUpdate(refEl, floatingEl, () => {
        updPos();
        setIsPositioned(true);
      });
    },
    middleware: [
      offset(5),
      flip(),
      shift({ mainAxis: false, crossAxis: true, limiter: limitShift() }),
    ],
  });

  return (
    <FloatingFocusManager context={context} modal={false}>
      <div
        className="message-popover"
        ref={refs.setFloating}
        style={floatingStyles}
      >
        {isPositioned ? children : null}
      </div>
    </FloatingFocusManager>
  );
};

export default MessagePopover;
