import { CardSize } from "components/CardDisplay";
import { Spinner } from "components/Loading";
import Tooltip from "components/Tooltip";
import { PRIORITY_CARD_MASTERY_ID } from "engine/puzzles/mastery-data";
import { WSReqType } from "game-server/ws";
import { useState } from "react";
import Twemoji from "react-twemoji";
import {
  useSubscribedDeck,
  useSubscribedTeam,
  useSubscribedTeamBattle,
} from "stores/ClientGlobalStateStore";
import { useSlot } from "stores/NavStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import DeckCardDisplay from "./DeckCardDisplay";
import "./DeckDisplay.css";
import DeckStats from "./DeckStats";
import PriorityCard from "./PriorityCard";

function DeckDisplay() {
  const [isEditingPriority, setIsEditingPriority] = useState(false);
  const serverInterfaceController = useServerInterfaceController();
  const slot = useSlot();
  const subscribedTeam = useSubscribedTeam();
  const deck = useSubscribedDeck(slot);
  const { lockedDeckSelection = null } = useSubscribedTeamBattle() ?? {};

  if (subscribedTeam === null || deck === null)
    return <Spinner size={48} inline={true} />;

  const { masteries } = subscribedTeam;
  const isPriorityCardEnabled = masteries[PRIORITY_CARD_MASTERY_ID].isEnabled;

  const resetPriorityCard = () => {
    serverInterfaceController.sendReq({
      type: WSReqType.DECKBUILDING_STEP,
      slot,
      deckUpd: { deletePriorityCard: true },
    });
    setIsEditingPriority(false);
  };

  return (
    <div className="deck-display">
      <div className="deck-display-header">
        <h2>
          Deck{" "}
          {lockedDeckSelection !== null && lockedDeckSelection.slot === slot ? (
            <Tooltip text="This deck is locked; changes below won't affect the ongoing battle.">
              <Twemoji>üîí</Twemoji>
            </Tooltip>
          ) : null}
        </h2>
        <button
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.DECKBUILDING_STEP,
              slot,
              deckUpd: {
                clear: true,
              },
            });
          }}
        >
          Clear deck
        </button>
      </div>
      {isPriorityCardEnabled ? (
        <PriorityCard
          priorityCard={deck.priorityCard}
          isEditingPriority={isEditingPriority}
          startEditingPriority={() => setIsEditingPriority(true)}
          resetPriorityCard={resetPriorityCard}
        />
      ) : null}
      <div className="deck-cards">
        {Object.keys(deck.cards)
          .sort()
          .map((name) => (
            <DeckCardDisplay
              key={name}
              name={name}
              size={CardSize.SMALL}
              isEditingPriority={isEditingPriority}
              resetIsEditingPriority={() => setIsEditingPriority(false)}
            />
          ))}
      </div>
      <DeckStats />
    </div>
  );
}

export default DeckDisplay;
