import { useCallback, useEffect, useState } from "react";

import { Player, Slot } from "engine/types/game-state";
import { StepType } from "engine/types/steps";
import { UIElement, selectorFor } from "engine/types/keyframes";

import { useClientGameStore, useInspector } from "stores/ClientGameStore";
import { useMutationObserver } from "stores/NavStore";
import CutsceneDialogBox from "components/CutsceneDialogBox";
import Mask from "./Mask";
import MessagePopover from "./MessagePopover";
import "./Keyframe.css";

const Keyframe = () => {
  const keyframe = useClientGameStore((state) => state.gameState.keyframe);
  const sendStep = useClientGameStore((state) => state.sendStep);
  const inspector = useInspector();
  const isGameActive = inspector.isGameActive();
  const [nextElt, setNextElt] = useState<HTMLElement | null>(null);

  const {
    id: keyframeId,
    dialogue,
    speaker,
    selector: selectorOpt,
    selectorPermanent,
    selectorSlot,
    placement,
    showNext = false,
    nextSelector: nextSelectorOpt,
    nextSelectorPermanent,
    showMask = false,
    unmaskSelectors,
    useCutsceneUI,
    hideBattler = false,
  } = keyframe ?? {};

  const resolveSelectorPermanent = (
    cardName: string,
    selectorElem?: UIElement
  ) => {
    const matches = inspector.getPermanents({
      name: cardName,
    });
    if (matches.length !== 1)
      throw new Error("expect requested selector permanent to exist uniquely");
    return selectorFor.permanent(matches[0], selectorElem);
  };
  const resolveSelector = (
    selectorElem?: UIElement,
    cardName?: string,
    slot?: Slot
  ) => {
    if (slot !== undefined) return selectorFor.slot(slot);
    if (cardName !== undefined)
      return resolveSelectorPermanent(cardName, selectorElem);
    if (selectorElem === undefined) return undefined;
    // Only support P1 gems for now
    if (selectorElem === UIElement.GEMS) return selectorFor.gems(Player.P1);
    return selectorFor.element(selectorElem);
  };

  const selector = resolveSelector(
    selectorOpt,
    selectorPermanent,
    selectorSlot
  );
  const nextSelector =
    resolveSelector(nextSelectorOpt, nextSelectorPermanent) ?? null;

  const onNext = useCallback(() => {
    if (keyframeId === undefined) return;
    sendStep?.({
      type: StepType.ADVANCE_KEYFRAME,
      keyframeId,
    });
  }, [sendStep, keyframeId]);

  useMutationObserver(nextSelector, setNextElt);

  useEffect(() => {
    if (!isGameActive) return;
    if (!nextElt) return;

    const prevPointerEvents = nextElt.style.pointerEvents;
    nextElt.addEventListener("click", onNext);
    nextElt.style.pointerEvents = "auto";
    return () => {
      nextElt.style.pointerEvents = prevPointerEvents;
      nextElt.removeEventListener("click", onNext);
    };
  }, [nextElt, onNext, isGameActive]);

  return (
    <>
      <div className="keyframe-container">
        {selector && dialogue && !hideBattler && keyframe !== undefined ? (
          <MessagePopover
            selector={selector}
            placement={placement}
            key={keyframeId}
          >
            <CutsceneDialogBox keyframe={keyframe} />
          </MessagePopover>
        ) : null}
      </div>
      {showMask && isGameActive ? (
        <Mask selector={selector} unmaskSelectors={unmaskSelectors} />
      ) : null}
    </>
  );
};

export default Keyframe;
