import { useEffect } from "react";
import { useNavigate, useParams, useSearchParams } from "react-router-dom";

import { opponentOf } from "engine/types/game-state";
import { DRAW_CARD_MASTERY_ID } from "engine/puzzles/mastery-data";
import { WSReqType } from "game-server/ws";

import {
  useClientGameStore,
  useInspector,
  useIsLockedBesidesKeyframe,
  useIsLocked,
  usePlayerPerspective,
} from "stores/ClientGameStore";
import {
  useSubscribedTeam,
  useIsValidBattle,
  useSelectedPuzName,
  useIsGameSettingsEnabled,
  useNeedRedirectToPrep,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import { useKeyboardCursorStore } from "stores/KeyboardCursorStore";
import { useNavStore, usePuzName } from "stores/NavStore";
import {
  useServerInterfaceController,
  useTeamId,
} from "stores/ServerInteractionStore";
import { useResetGameUIActionsState } from "stores/UserGameActionHandlers";
import { useUserLogStore } from "stores/UserLogStore";
import {
  useHotkeyPreferencesStore,
  useIsAdminEnabledStore,
} from "stores/UserPreferencesStore";
import { useAnimationSpeed } from "stores/AnimationStore";

import AIBattler from "components/AIBattlerDisplay";
import BattleBackground from "components/BattleBackground";
import CutsceneUI from "components/CutsceneUI";
import FieldDisplay from "components/FieldDisplay";
import { useGameHotkeys } from "components/GameHotkeys";
import HandDisplay from "components/HandDisplay";
import Keyframe from "components/Keyframe";
import { Spinner } from "components/Loading";
import Modal from "components/Modal";
import Presence from "components/Presence";
import {
  LeftOfFieldDisplay,
  RightOfFieldDisplay,
} from "components/SideFieldDisplay";
import SolvedModal from "components/SolvedModal";

import "./GamePage.css";
import { useGameEnd } from "./GamePageUtil/useGameEnd";
import { useGameUpdates } from "./GamePageUtil/useGameUpdates";
import { useInitSendStep } from "./GamePageUtil/useInitSendStep";
import { useTutorialKeyframes } from "./GamePageUtil/useTutorialKeyframes";

function GamePage() {
  const subscribedTeam = useSubscribedTeam();
  const setSubscribedTeamBattle = useGlobalSubscriptionStore(
    (state) => state.setSubscribedTeamBattle
  );
  const setStepLock = useClientGameStore((state) => state.setStepLock);
  const setUpdateLock = useClientGameStore((state) => state.setUpdateLock);
  const isLockedBesidesKeyframe = useIsLockedBesidesKeyframe();
  const isLocked = useIsLocked();
  const resetGameUIActionsState = useResetGameUIActionsState();
  const inspector = useInspector();
  const isGameActive = inspector.isGameActive();
  const serverInterfaceController = useServerInterfaceController();
  const teamId = useTeamId();
  const playerPerspective = usePlayerPerspective();
  const isPageReady = useNavStore((state) => state.isPageReady);
  const setIsPageReady = useNavStore((state) => state.setIsPageReady);
  const solvedModalState = useNavStore((state) => state.solvedModalState);
  const setSolvedModalState = useNavStore((state) => state.setSolvedModalState);
  const isHotkeyModalOpen = useNavStore((state) => state.isHotkeyModalOpen);
  const setIsHotkeyModalOpen = useNavStore(
    (state) => state.setIsHotkeyModalOpen
  );
  const puzName = usePuzName();
  const selectedPuzName = useSelectedPuzName();
  const { spectateTeamId } = useParams();
  const speed = useAnimationSpeed();
  const isGameSettingsEnabled = useIsGameSettingsEnabled();
  const clearLog = useUserLogStore((state) => state.clearLog);
  const resetKeyboardState = useKeyboardCursorStore(
    (state) => state.resetState
  );
  const setIsHotkeysEnabled = useKeyboardCursorStore(
    (state) => state.setEnabled
  );
  const isHotkeysEnabledPref = useHotkeyPreferencesStore(
    (state) => state.isEnabled
  );
  const setIsEndCutsceneRedirectHandled = useClientGameStore(
    (state) => state.setIsEndCutsceneRedirectHandled
  );
  const isSpectating = useIsAdminEnabledStore((state) => state.isSpectating);
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const isValidBattle = useIsValidBattle();
  const needRedirectToPrep = useNeedRedirectToPrep();

  const keyframe = useClientGameStore((state) => state.gameState.keyframe);
  const {
    hideGame = false,
    useCutsceneUI = false,
    showMask = false,
  } = keyframe ?? {};

  useEffect(() => {
    if (needRedirectToPrep !== null && needRedirectToPrep) {
      navigate({
        pathname: `../prep/${puzName}`,
        search: searchParams.toString(),
      });
    }
  }, [needRedirectToPrep, puzName, navigate, searchParams]);

  useEffect(() => {
    return () => {
      // Reset page state.
      setStepLock(false);
      setUpdateLock(false);
      resetGameUIActionsState();
      setIsPageReady(false);
      setSolvedModalState(null);
      setIsHotkeyModalOpen(false);
      clearLog();
      resetKeyboardState();
      setIsEndCutsceneRedirectHandled(false);
    };
  }, [
    setStepLock,
    setUpdateLock,
    resetGameUIActionsState,
    setIsPageReady,
    setSolvedModalState,
    setIsHotkeyModalOpen,
    clearLog,
    resetKeyboardState,
    setIsEndCutsceneRedirectHandled,
  ]);

  useEffect(() => {
    if (needRedirectToPrep === null || needRedirectToPrep) return;
    if (isSpectating) return;
    if (selectedPuzName === null) return;
    return serverInterfaceController.addAuthEffect(() => {
      serverInterfaceController.sendReq({
        type: WSReqType.VIEW_PUZZLE,
        puzName: selectedPuzName,
      });
    });
  }, [
    selectedPuzName,
    serverInterfaceController,
    isSpectating,
    needRedirectToPrep,
  ]);

  useEffect(() => {
    if (teamId === null) return;
    if (isValidBattle === null || !isValidBattle) return;
    setSubscribedTeamBattle({
      teamId: spectateTeamId ?? teamId,
      puzName,
    });
  }, [setSubscribedTeamBattle, puzName, teamId, spectateTeamId, isValidBattle]);

  useTutorialKeyframes();
  useInitSendStep();
  useGameUpdates();
  const hasGameEnded = useGameEnd();

  useGameHotkeys();
  useEffect(() => {
    if (hasGameEnded || !isGameSettingsEnabled) {
      setIsHotkeysEnabled(false);
      return;
    }
    setIsHotkeysEnabled(isHotkeysEnabledPref);
  }, [
    hasGameEnded,
    isGameSettingsEnabled,
    setIsHotkeysEnabled,
    isHotkeysEnabledPref,
  ]);

  useEffect(() => {
    serverInterfaceController.isFlushToggled = speed === 0;
  }, [serverInterfaceController, speed]);

  if (!isPageReady || needRedirectToPrep === null || needRedirectToPrep)
    return <Spinner size={128} />;

  return (
    <>
      <BattleBackground />
      <Presence cursorGroupName={puzName} enabled={true}>
        <div
          className={`game-and-keyframe${
            solvedModalState === null ? "" : " unfocused"
          }`}
        >
          {hideGame ? null : (
            <div className="below-nav" id="game-page-root-wrapper">
              <div
                id="game-page-root"
                className={`game-page-root ${
                  isLocked && showMask ? "game-lock" : ""
                }`}
              >
                <div
                  className="game-play-area"
                  onClick={() => {
                    resetGameUIActionsState();
                  }}
                >
                  {inspector.hasAI() ? (
                    <AIBattler />
                  ) : (
                    <HandDisplay player={opponentOf(playerPerspective)} />
                  )}
                  <div className="game-center-area">
                    <div className="game-left-area">
                      <LeftOfFieldDisplay />
                    </div>
                    <FieldDisplay />
                    <div className="game-right-area">
                      <RightOfFieldDisplay />
                    </div>
                  </div>
                  <HandDisplay player={playerPerspective} />
                </div>
              </div>
            </div>
          )}
          <div className={isLockedBesidesKeyframe ? "game-lock" : ""}>
            <Keyframe />
            {useCutsceneUI && !(!hideGame && !isGameActive) ? (
              <div className="below-nav game-cutscene-ui-wrapper">
                <CutsceneUI />
              </div>
            ) : null}
          </div>
        </div>
      </Presence>
      <Modal title="Hotkey Help" isOpen={isHotkeyModalOpen}>
        <div className="hotkey-help-modal-content">
          <kbd>W</kbd>
          <kbd>A</kbd>
          <kbd>S</kbd>
          <kbd>D</kbd> / <kbd>‚Üë</kbd>
          <kbd>‚Üê</kbd>
          <kbd>‚Üì</kbd>
          <kbd>‚Üí</kbd> Move cursor
          <br />
          <kbd>Space</kbd>/<kbd>Enter</kbd> Select
          <br />
          <kbd>Esc</kbd> Deselect
          <br />
          <kbd>E</kbd> End turn
          <br />
          {inspector.doesPlayerHaveMastery(
            playerPerspective,
            DRAW_CARD_MASTERY_ID
          ) ? (
            <>
              <kbd>P</kbd> Pay 2 Food to draw a card
              <br />
            </>
          ) : null}
          <kbd>Z</kbd> Special
          <br />
          <kbd>X</kbd> Flex
          <br />
          <kbd>C</kbd> Create
          <br />
          <kbd>V</kbd> Create2 (if there&apos;s a second create option)
          <br />
          <kbd>F</kbd> Focus field
          <br />
          <kbd>H</kbd> Focus hand
          <br />
          <kbd>1</kbd>-<kbd>0</kbd> Select 1st through 10th card in hand
          <br />
          <kbd>/</kbd> Show help
        </div>
        <div>
          <button
            onClick={() => {
              setIsHotkeyModalOpen(false);
            }}
          >
            Okay!
          </button>
        </div>
      </Modal>
      <SolvedModal />
    </>
  );
}

export default GamePage;
