import { GlobalUpdatesScopeType } from "game-server/global-updates";
import settings from "settings";
import TeamDisplayNameDisplay from "components/TeamDisplayNameDisplay";
import { PuzzleLink } from "components/Link";
import { Spinner, LargeSpinner } from "components/Loading";
import { SPEEDRUN_MASTERY_ID } from "engine/puzzles/mastery-data";
import {
  PUZ_NAME_MASTERY_TREE,
  isUsedForGlobalSpeedrunTime,
} from "engine/puzzles/puzzle-data";
import Globals from "Globals";
import { useEffect } from "react";
import { useNavigate, useParams, useSearchParams } from "react-router-dom";
import {
  useClientGlobalStateStore,
  useSubscribedTeam,
  useSubscribedTeamMembers,
} from "stores/ClientGlobalStateStore";
import { useServerInteractionStore } from "stores/ServerInteractionStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import { useUserPreferencesStore } from "stores/UserPreferencesStore";
import SelectFactionDisplay from "./SelectFactionDisplay";
import { SpeedrunNote } from "./TeamListDisplay";
import "./TeamPage.css";

function TeamPage() {
  const { djangoBaseUrl } = settings;
  const liveTeams = useClientGlobalStateStore(
    (state) => state.globalState.teams
  );
  const teams =
    settings.isPosthunt && Object.keys(settings.teamsSnapshot).length > 0
      ? settings.teamsSnapshot
      : liveTeams;
  const { setNeedTeamList } = useGlobalSubscriptionStore();
  const subscribedTeam = useSubscribedTeam();
  const { teamName } = useParams();
  const team =
    Object.values(teams).find((team) => team.displayName == teamName) ??
    Object.values(liveTeams).find((team) => team.displayName == teamName) ??
    null;
  const teamId = team?.teamId ?? null;
  const subscribedTeamMembers = useSubscribedTeamMembers(teamId);
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const serverInterfaceController = useServerInteractionStore(
    (state) => state.getServerInterfaceController
  )();
  const areTeamsLoaded = Object.keys(liveTeams).length > 0;
  const showSubpuzzles = useUserPreferencesStore(
    (state) => state.showSubpuzzles
  );
  const setShowSubpuzzles = useUserPreferencesStore(
    (state) => state.setShowSubpuzzles
  );

  useEffect(() => {
    setNeedTeamList(true);
    // Don't unset on unmount to prevent extraneous OVERRIDE_STATEs.
    // We don't have unsubscribe anyway so unsetting is strictly worse.
  }, [setNeedTeamList]);

  useEffect(() => {
    // In posthunt, we use members from a snapshot.
    if (settings.isPosthunt) return;
    if (serverInterfaceController === null) return;
    if (teamId === null) return;
    return serverInterfaceController.addSubscribeEffect({
      type: GlobalUpdatesScopeType.TEAM_MEMBERS,
      teamId,
    });
  }, [serverInterfaceController, teamId]);

  useEffect(() => {
    if (!areTeamsLoaded) return;
    if (team !== null) return;
    Globals.toastr.error(`Team ‚Äú${teamName}‚Äù not found.`);
    navigate({
      pathname: "../teams",
      search: searchParams.toString(),
    });
  }, [areTeamsLoaded, team, teamName, navigate, searchParams]);

  if (!areTeamsLoaded) return <LargeSpinner />;
  if (team === null) return null;

  const timeOpts = {
    hourCycle: "h23",
    timeZoneName: "short",
    month: "short",
    day: "numeric",
    hour: "2-digit",
    minute: "2-digit",
  } as const;

  const duration = (secs: number) => {
    secs = Math.max(Math.floor(secs / 1000), 0);
    const hours = Math.floor(secs / (60 * 60));
    secs -= hours * 60 * 60;
    const mins = Math.floor(secs / 60);
    secs -= mins * 60;
    if (hours > 0) return `${hours}h${mins}m`;
    if (mins > 0) return `${mins}m${secs}s`;
    return `${secs}s`;
  };

  const solves = (() => {
    // We don't have information about cross-team solves yet...
    if (!subscribedTeam || subscribedTeam.teamId !== team.teamId) return null;
    const { puzzles, battleGroups, masteries } = subscribedTeam;
    const speedrunsEnabled = masteries[SPEEDRUN_MASTERY_ID].isEnabled;
    const solveRow = (
      puzName: string,
      displayName: string,
      speedrunTime: number,
      subpuzzle?: boolean,
      unused?: boolean
    ) => {
      const unlockTime = puzzles[puzName].unlockTime ?? 0;
      const solveTime = puzzles[puzName].solveTime ?? 0;
      const hasAnswer = puzzles[puzName].hasAnswer ?? false;
      const className = [
        subpuzzle ? "team-solves-subpuzzle" : [],
        speedrunsEnabled && unused ? "team-solves-unused" : [],
      ];
      const speedrunText = (() => {
        if (speedrunTime === 0) return null;
        const text = duration(speedrunTime);
        if (unused) return `(${text})`;
        return text;
      })();
      return (
        <tr key={puzName} className={className.flat().join(" ")}>
          <td>
            <PuzzleLink battleGroupName={puzName} hasAnswer={hasAnswer}>
              {displayName}
            </PuzzleLink>
          </td>
          {speedrunsEnabled ? <td>{speedrunText}</td> : null}
          <td>{new Date(unlockTime).toLocaleString([], timeOpts)}</td>
          <td>{duration(solveTime - unlockTime)}</td>
          <td>{new Date(solveTime).toLocaleString([], timeOpts)}</td>
        </tr>
      );
    };
    const rows = Object.keys(battleGroups)
      .filter((puzName) => !battleGroups[puzName].isCutscene)
      .filter((puzName) => puzzles[puzName]?.solveTime)
      .sort(
        (p1, p2) => (puzzles[p1].solveTime ?? 0) - (puzzles[p2].solveTime ?? 0)
      )
      .flatMap((puzName) => {
        const subpuzzles = [PUZ_NAME_MASTERY_TREE].includes(puzName)
          ? []
          : Object.values(puzzles)
              .filter(
                (puzzle) =>
                  puzzle.battleGroupName === puzName &&
                  puzzle.puzName !== puzName
              )
              .sort(({ order: order1 }, { order: order2 }) => order1 - order2);
        let speedrunTotal = 0;
        if (subpuzzles.length === 0)
          speedrunTotal = puzzles[puzName].speedrunTime ?? 0;
        const rows = subpuzzles.map(
          ({ puzName, battleGroupName, speedrunTime, solveTime }, i) => {
            if (solveTime === undefined) return null;
            const used = isUsedForGlobalSpeedrunTime(
              puzName,
              battleGroupName,
              false
            );
            if (used) speedrunTotal += speedrunTime ?? 0;
            if (!showSubpuzzles) return null;
            return solveRow(
              puzName,
              `#${i + 1}`,
              speedrunTime ?? 0,
              true,
              !used
            );
          }
        );
        rows.unshift(
          solveRow(puzName, battleGroups[puzName].displayName, speedrunTotal)
        );
        return rows;
      });
    if (rows.length === 0) return null;
    return (
      <table className="list-table team-solves-table">
        <thead>
          <tr>
            <th>
              <h2>Solves</h2>
              <label>
                <input
                  type="checkbox"
                  onChange={(e) => setShowSubpuzzles(e.target.checked)}
                  checked={showSubpuzzles}
                />{" "}
                Show subpuzzles
              </label>
            </th>
            {speedrunsEnabled ? (
              <th>{<SpeedrunNote headerText="Best time" />}</th>
            ) : null}
            <th>Unlock time</th>
            <th>Time to solve</th>
            <th>Solve time</th>
          </tr>
        </thead>
        <tbody>{rows}</tbody>
      </table>
    );
  })();
  const actions =
    !settings.isPosthunt &&
    subscribedTeam &&
    subscribedTeam.teamId === team.teamId ? (
      <>
        <a href={`${djangoBaseUrl}edit-team`} className="btn">
          Edit team members
        </a>{" "}
        <a href={`${djangoBaseUrl}password-change`} className="btn">
          Change password
        </a>
      </>
    ) : null;

  return (
    <>
      <div className="top-more-actions">{actions}</div>
      <h1>
        <TeamDisplayNameDisplay teamData={team} noLink={true} />
      </h1>
      <main style={{ display: "flex", gap: "2.5em", flexDirection: "column" }}>
        {subscribedTeamMembers === null ? (
          <Spinner inline={true} />
        ) : (
          subscribedTeamMembers.members?.join(", ")
        )}
        <SelectFactionDisplay teamData={team} />
        {solves}
      </main>
    </>
  );
}

export default TeamPage;
