import { getDevKnobs } from "App.util";
import { ConfirmModal } from "components/Modal";
import Tooltip from "components/Tooltip";
import { PUZ_NAME_PVP } from "engine/puzzles/puzzle-data";
import { GamePhase, Player, MAX_MANUAL_SAVES } from "engine/types/game-state";
import { StepType } from "engine/types/steps";
import { Role } from "engine/types/updates";
import { WSReqType } from "game-server/ws";
import { useState } from "react";
import { Link, useParams, useSearchParams } from "react-router-dom";
import Twemoji from "react-twemoji";
import {
  useClientGameStore,
  useInspector,
  usePlayerPerspective,
  useRole,
  useIsLockedBesidesKeyframe,
} from "stores/ClientGameStore";
import {
  useSelectedDeckData,
  useSubscribedTeamBattle,
} from "stores/ClientGlobalStateStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useIsAdminEnabledStore } from "stores/UserPreferencesStore";
import "./GameMenu.css";

function GameMenu() {
  const serverInterfaceController = useServerInterfaceController();
  const sendStep = useClientGameStore((state) => state.sendStep);
  const inspector = useInspector();
  const role = useRole();
  const playerPerspective = usePlayerPerspective();
  const { selectedDeckSlot = null } =
    useSelectedDeckData(playerPerspective) ?? {};
  const subscribedTeamBattle = useSubscribedTeamBattle();
  const [searchParams] = useSearchParams();
  const disableSteps = useIsLockedBesidesKeyframe();
  const [showRestartModal, setShowRestartModal] = useState(false);
  const roomId = useClientGameStore((state) => state.roomId);
  const { spectateTeamId } = useParams();
  const isSpectating = useIsAdminEnabledStore((state) => state.isSpectating);

  const { puzName } = useParams<{
    puzName: string;
  }>();
  if (puzName === undefined)
    throw new Error("expect puzName parameter to be defined");

  const statusText = (() => {
    if (inspector.isGameActive()) {
      const turnNum = inspector.getTurnNumber();
      const isOwnTurn = inspector.isTurnPending(playerPerspective);
      const turnText = `Turn ${Math.floor(turnNum / 2) + 1}`;
      return `Battle in progress. ${turnText}. ${
        isOwnTurn ? "It's your turn!" : "Opponent's turn."
      }`;
    }

    const { winner } = inspector.gameState;
    const { isSolved } = inspector.gameState[playerPerspective];
    if (winner === undefined)
      throw new Error("expect winner to be defined when game ends");
    const loser = inspector.getOpponentOf(winner);

    if (isSolved ?? false) {
      if (winner === playerPerspective) return "You won!";
      else return "You lost successfully!";
    }

    if (inspector.hasAI()) {
      if (winner === playerPerspective)
        return "You won, but failed to make any friends.";
      else return "You lost.";
    }

    if (role === Role.GOD) {
      switch (winner) {
        case Player.P1:
          return "Player 1 won.";
        case Player.P2:
          return "Player 2 won.";
      }
    }

    if (winner === playerPerspective) return "You won!";
    else return "You lost.";
  })();

  const saveLoadDom = (() => {
    if (!inspector.areCheckpointsAllowed())
      return "Saving is not supported for this battle.";
    const checkpointSummaries = subscribedTeamBattle?.checkpointSummaries ?? {};
    const newSlot =
      Math.max(
        0,
        ...Object.keys(checkpointSummaries).map((slot) => Number(slot))
      ) + 1;
    return Array.from(new Array(MAX_MANUAL_SAVES + 1), (_, i) => {
      const checkpointSummary = checkpointSummaries[i];
      const checkpointSummaryDom = (() => {
        if (i === newSlot) return "New save";
        if (checkpointSummary === undefined) return null;
        const {
          turnNumber,
          roomId: checkpointRoomId,
          timestamp,
        } = checkpointSummary;
        const isOldRoom = roomId !== null && roomId !== checkpointRoomId;
        return (
          <Tooltip text={`Saved at ${new Date(timestamp).toLocaleString()}`}>
            <div className={`${isOldRoom ? "save-load-summary-old" : ""}`}>
              {i === 0
                ? "Autosave"
                : MAX_MANUAL_SAVES === 1
                ? "Save"
                : `Save ${i}`}{" "}
              {isOldRoom ? "(old)" : null}
              <div className="save-load-turn">
                Turn {Math.floor(turnNumber / 2) + 1}
              </div>
            </div>
          </Tooltip>
        );
      })();
      if (checkpointSummaryDom === null) return null;
      return (
        <div className="save-load-row" key={i}>
          <div className="save-load-status">
            <Twemoji>{checkpointSummaryDom}</Twemoji>
          </div>
          <Tooltip text="Save">
            <button
              disabled={
                i === 0 ||
                disableSteps ||
                inspector.gameState.phase !== GamePhase.ACTIVE
              }
              onClick={() => {
                if (sendStep) {
                  sendStep({
                    type: StepType.CREATE_CHECKPOINT,
                    slot: i,
                  });
                }
              }}
            >
              <Twemoji>üíæ</Twemoji>
            </button>
          </Tooltip>
          <Tooltip
            text="Load"
            key={
              // Force a re-render on checkpoint restore so that
              // the tooltip registers as unhovered if the field
              // size changes and the button moves out from
              // underneath the mouse.
              roomId
            }
          >
            <button
              disabled={i === newSlot || disableSteps}
              onClick={() => {
                serverInterfaceController.sendReq({
                  type: WSReqType.RESTORE_CHECKPOINT,
                  puzName,
                  slot: i,
                });
              }}
            >
              <Twemoji>üì§Ô∏è</Twemoji>
            </button>
          </Tooltip>
        </div>
      );
    });
  })();

  const restartConfirmText =
    inspector.getTurnNumber() > 1 && inspector.isGameActive()
      ? "Are you sure you want to restart?"
      : null;

  const onRestart = () => {
    serverInterfaceController.startAIBattleAsync(puzName, {
      restart: true,
      reuseDeck: true,
      devKnobs: getDevKnobs(),
    });
  };

  return (
    <>
      {spectateTeamId === undefined && !isSpectating ? (
        <div className="save-load-menu">{saveLoadDom}</div>
      ) : null}
      <div className="game-menu">
        <div className="game-menu-status">{statusText}</div>
        {puzName === PUZ_NAME_PVP &&
        spectateTeamId === undefined &&
        !isSpectating ? (
          <div
            onClick={() => {
              serverInterfaceController.sendReq({
                type: WSReqType.RESIGN,
                player: playerPerspective,
                puzName,
              });
            }}
          >
            <button disabled={!inspector.isGameActive()}>Forfeit</button>
          </div>
        ) : null}
        {puzName !== PUZ_NAME_PVP &&
        spectateTeamId === undefined &&
        !isSpectating ? (
          <>
            {selectedDeckSlot === null ? null : (
              <div>
                <Link
                  to={{
                    pathname: `../deck/${selectedDeckSlot}/${puzName}`,
                    search: searchParams.toString(),
                  }}
                >
                  <button>Edit deck</button>
                </Link>
              </div>
            )}
            <div>
              <button
                disabled={disableSteps}
                onClick={() => {
                  restartConfirmText ? setShowRestartModal(true) : onRestart();
                }}
              >
                Restart
              </button>
            </div>
          </>
        ) : null}
        <div>
          <Link
            to={{
              pathname: `../prep/${puzName}`,
              search: searchParams.toString(),
            }}
          >
            <button>Exit battle</button>
          </Link>
        </div>
      </div>
      <ConfirmModal
        title="Are you sure?"
        isOpen={showRestartModal}
        onClose={() => setShowRestartModal(false)}
        onConfirm={onRestart}
        confirmLabel="Restart"
      >
        {restartConfirmText}
      </ConfirmModal>
    </>
  );
}

export default GameMenu;
