import { useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import "./GamePage.css";

import { PUZ_NAME_POST_FINAL_BATTLE } from "engine/puzzles/puzzle-data";

import settings from "settings";
import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { Spinner } from "components/Loading";

/**
 * Landing page after cutscene to wait for the solve to get synced
 * and then jump into the victory page.
 */
function StartVictoryPage() {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const serverInterfaceController = useServerInterfaceController();
  const subscribedTeam = useSubscribedTeam();

  useEffect(() => {
    if (subscribedTeam === null) return;
    const puzData = subscribedTeam.puzzles[PUZ_NAME_POST_FINAL_BATTLE];
    // If the final cutscene is not marked solved, wait until it is.
    // This indicates that the solve has been synced to backend.
    if (puzData?.solveTime === undefined) return;

    window.location.href = `${settings.djangoBaseUrl}victory`;
  }, [subscribedTeam]);

  useEffect(() => {
    return serverInterfaceController.onErrorEvent.addListener((resp) => {
      navigate(
        {
          pathname: "../",
          search: searchParams.toString(),
        },
        { replace: true }
      );
    });
  }, [serverInterfaceController, navigate, searchParams]);

  return <Spinner size={128} />;
}

export default StartVictoryPage;
