import { useEffect } from "react";
import { useClientGameStore, useInspector } from "stores/ClientGameStore";
import { SelectedCardType, useTurnStore } from "stores/TurnStore";
import {
  useResetSelectedCard,
  useSelectFieldPermanent,
} from "stores/UserGameActionHandlers";

export const useTutorialKeyframes = () => {
  const selectedCard = useTurnStore((state) => state.selectedCard);
  const isKeyframeSelected = useTurnStore((state) => state.isKeyframeSelected);
  const setSelectedFieldPermanent = useSelectFieldPermanent();
  const resetSelectedCard = useResetSelectedCard();
  const inspector = useInspector();
  const keyframe = useClientGameStore((state) => state.gameState.keyframe);
  const { id: keyframeId } = keyframe ?? {};

  // Hacked-in logic for selecting stuff for tutorial.
  useEffect(() => {
    const query = (() => {
      switch (keyframeId) {
        case "tutorial1-attack":
          return { name: "yoshisaur-munchakoopas" };
        case "tutorial2-attack-2":
          return { name: "new-boarn" };
        case "tutorial3-after-summon-new-boarn":
          return { name: "new-boarn" };
        case "tutorial3-focus-hog":
          return { name: "hog" };
        case "tutorial4-focus-beeowulf-2":
          return { name: "beeowulf" };
        case "tutorial4-focus-calf-1":
        case "tutorial4-focus-calf-2":
          return { name: "chocolate-calf" };
        default:
          return undefined;
      }
    })();
    if (query === undefined) {
      if (
        isKeyframeSelected &&
        !(
          keyframeId !== undefined &&
          ["tutorial2-free-play"].includes(keyframeId)
        )
      )
        resetSelectedCard();
      return;
    }
    if (query === null) {
      resetSelectedCard();
      return;
    }
    const matches = inspector.getPermanents(query);
    if (matches.length !== 1)
      throw new Error("expect unique permanent when force selecting");
    const selectPermanentId = matches[0].id;
    if (
      selectedCard !== null &&
      selectedCard.type === SelectedCardType.PERMANENT &&
      selectedCard.permanentId === selectPermanentId
    ) {
      return;
    }
    setSelectedFieldPermanent(selectPermanentId, true);
  }, [
    keyframeId,
    setSelectedFieldPermanent,
    inspector,
    selectedCard,
    isKeyframeSelected,
    resetSelectedCard,
  ]);
};
