function PreBattleDialogueDisplay() {
  const subscribedTeam = useSubscribedTeam();
  const battleGroup = useSelectedBattleGroup();

  if (battleGroup === null || subscribedTeam === null) return null;

  const { cardUnlocks } = subscribedTeam;
  const { preBattleDialogue } = battleGroup;

  if (preBattleDialogue === undefined) return null;

  const lines = preBattleDialogue.trim().split("\n");
  const dialogBoxesDom = lines.map((line, i) => {
    const isSpeakerKero = line.startsWith("kero:");
    const isSpeakerDino = line.startsWith("dino:");
    const isSpeakerCaptainPi = line.startsWith("captain pi:");
    const dialogue = (() => {
      if (isSpeakerKero) return line.slice(line.indexOf(":") + 1).trim();
      if (isSpeakerDino) return line.slice(line.indexOf(":") + 1).trim();
      if (isSpeakerCaptainPi) return line.slice(line.indexOf(":") + 1).trim();
      return line;
    })();
    return (
      <div
        key={i}
        className="prep-dialogue-line"
        style={{
          alignSelf:
            isSpeakerKero || isSpeakerCaptainPi ? "flex-end" : "flex-start",
        }}
      >
        <StoryDialogBox
          dialogue={dialogue}
          speaker={
            isSpeakerKero
              ? Speaker.KERO_DOS
              : isSpeakerDino
              ? Speaker.DINO
              : isSpeakerCaptainPi
              ? Speaker.CAPTAIN_PI
              : Speaker.NARRATOR
          }
        />
      </div>
    );
  });

  return <div className="prep-dialogue">{dialogBoxesDom}</div>;
}

function PrepPuzzleSelectionDisplay() {
  const puzName = usePuzName();
  const subscribedTeam = useSubscribedTeam();
  const battleGroup = useBattleGroup();
  const puzzlesInBattleGroup = usePuzzlesInBattleGroup();
  const selectedBattleGroup = useSelectedBattleGroup();
  const puzzlesInSelectedBattleGroup = usePuzzlesInSelectedBattleGroup();
  const subscribedTeamBattle = useSubscribedTeamBattle();
  const serverInterfaceController = useServerInterfaceController();
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();

  const instanceablePuzzles = useMemo(() => {
    const instanceablePuzzles: {
      puzName: string;
      battleGroup: ClientBattleGroupGlobalState;
      icon?: string;
    }[] = [];

    if (subscribedTeam === null) return instanceablePuzzles;
    const { puzzles, battleGroups } = subscribedTeam;

    const numPuzzlesPerBattleGroup: { [battleGroupName: string]: number } = {};
    for (const puzData of Object.values(puzzles).sort(
      (puzData1, puzData2) => puzData1.order - puzData2.order
    )) {
      const { puzName, battleGroupName, hasAnswer = false } = puzData;
      const battleGroup = battleGroups[battleGroupName];
      const icon = getIcon(puzData, battleGroup);
      if (
        !isInstanceable(
          hasAnswer ?? false,
          puzName,
          battleGroupName,
          battleGroup.numPuzzles
        )
      )
        continue;
      const stageNum = numPuzzlesPerBattleGroup[battleGroupName] ?? 0;
      numPuzzlesPerBattleGroup[battleGroupName] = stageNum + 1;
      if (stageNum === 0) {
        // Only the first of each battle group.
        instanceablePuzzles.push({
          puzName,
          battleGroup,
          icon,
        });
      }
    }

    return instanceablePuzzles;
  }, [subscribedTeam]);

  if (subscribedTeam === null || battleGroup === null) return null;
  const selectedPuzName = subscribedTeamBattle?.selectedPuzName ?? null;
  const { roomStatus } = subscribedTeam.puzzles[puzName];
  const isRoomActive = roomStatus === GlobalStateBattleRoomStatus.ACTIVE;

  return (
    <>
      <div className="prep-instancer-room-selection">
        {(puzzlesInBattleGroup ?? []).map(
          ({ puzName: selectionPuzName }, i) => (
            <button
              className={`prep-puz-selection-row-btn${
                puzName === selectionPuzName ? " highlight" : ""
              }`}
              key={i}
              onClick={() =>
                navigate(
                  {
                    pathname: `../prep/${selectionPuzName}`,
                    search: searchParams.toString(),
                  },
                  { replace: true }
                )
              }
            >
              Room {i + 1}
            </button>
          )
        )}
      </div>

      <select
        id="prep-instancer-select"
        value={puzzlesInSelectedBattleGroup?.[0].puzName ?? ""}
        disabled={isRoomActive}
        onChange={(e) => {
          const newSelectedPuzName = e.target.value;
          if (newSelectedPuzName === "") return;
          // Put this check here to still render the selection even
          // when switching between instancer rooms.
          if (puzName === null) return;
          serverInterfaceController.sendReq({
            type: WSReqType.SELECT_PUZZLE,
            hostPuzName: puzName,
            selectedPuzName: newSelectedPuzName,
          });
        }}
      >
        <option disabled hidden key={""} value={""}></option>
        {instanceablePuzzles.map(({ puzName, battleGroup, icon }) => (
          <option key={puzName} value={puzName}>
            {`${icon} ${battleGroup.displayName}`}
          </option>
        ))}
      </select>

      {subscribedTeamBattle === null ? (
        <Spinner size={48} inline={true} />
      ) : (
        <>
          <BattlerImage
            battleGroupName={
              (selectedBattleGroup === null ? battleGroup : selectedBattleGroup)
                .battleGroupName
            }
          />
          <PreBattleDialogueDisplay />
        </>
      )}
    </>
  );
}

interface PrepDisplayProps {
  role: Role;
  player: Player;
}

function PrepDisplay(props: PrepDisplayProps) {
  const puzName = usePuzName();
  const isPvP = puzName === PUZ_NAME_PVP;

  const subscribedTeam = useSubscribedTeam();
  const battleGroup = useBattleGroup();
  const selectedBattleGroup = useSelectedBattleGroup();
  const setIsInstancerModalOpen = useNavStore(
    (state) => state.setIsInstancerModalOpen
  );
  const setIsPvPModalOpen = useNavStore((state) => state.setIsPvPModalOpen);
  const isInstancer = useIsInstancer() ?? false;
  const subscribedTeamBattle = useSubscribedTeamBattle();

  if (battleGroup === null || subscribedTeam === null)
    return <Spinner size={48} inline={true} />;

  const { battleGroupName, displayName } = battleGroup;
  const { puzzles } = subscribedTeam;
  const isBattleGroupSolved = puzzles[battleGroupName].solveTime !== undefined;

  const timeOpts = {
    hour12: true,
    timeZoneName: "short",
    year: "numeric",
    month: "long",
    day: "numeric",
    hour: "numeric",
    minute: "2-digit",
  } as const;
  const errata = Object.values(subscribedTeamBattle?.errata ?? {})
    .sort((e1, e2) => e1.timestamp - e2.timestamp)
    .map((erratum) => (
      <div key={erratum.erratumId}>
        {erratum.published ? null : (
          <strong>
            [
            <a
              href={`${settings.djangoBaseUrl}admin/puzzles/erratum/${erratum.erratumId}/change/`}
            >
              UNPUBLISHED
            </a>
            ]
          </strong>
        )}{" "}
        On {new Date(erratum.timestamp).toLocaleString([], timeOpts)},{" "}
        <span dangerouslySetInnerHTML={{ __html: erratum.text }}></span>
      </div>
    ));

  return isPvP ? (
    <>
      <h2 className="prep-header">
        {displayName}
        <Tooltip text="Click for more info">
          <div
            className="prep-help-btn"
            onClick={() => {
              setIsPvPModalOpen(true);
            }}
          >
            <Twemoji>üõà</Twemoji>
          </div>
        </Tooltip>
      </h2>
      <PvPLobbyDisplay />
    </>
  ) : (
    <div
      className={`prep-left${isInstancer ? " prep-left-instancer" : ""}`}
      style={{
        alignSelf: isPvP ? "start" : undefined,
        maxHeight: isPvP ? "100vh" : undefined,
      }}
    >
      <h2 className="prep-header">
        {displayName}
        {isInstancer ? (
          <Tooltip text="Click for more info">
            <div
              className="prep-help-btn"
              onClick={() => {
                setIsInstancerModalOpen(true);
              }}
            >
              <Twemoji>üõà</Twemoji>
            </div>
          </Tooltip>
        ) : null}
        {isBattleGroupSolved ? (
          <div className="solved-title-marker">
            {getCompletionVerb(battleGroup)}!
          </div>
        ) : null}
      </h2>
      {errata.length ? <div className="puzzle-erratum">{errata}</div> : null}
      <div className="prep-main">
        <>
          {isInstancer ? (
            <PrepPuzzleSelectionDisplay />
          ) : selectedBattleGroup !== null ? (
            <>
              <BattlerImage
                battleGroupName={selectedBattleGroup.battleGroupName}
              />
              <PreBattleDialogueDisplay />
            </>
          ) : null}
        </>
      </div>
    </div>
  );
}

