import { CursorEventType } from "engine/types/presence";
import { WSReqType } from "game-server/ws";
import { ServerInterfaceType } from "game-server/server-interface/ServerInterface";
import { AnimatePresence, clamp } from "framer-motion";
import { PropsWithChildren, useEffect, useState } from "react";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import Cursor from "./Cursor";
import CursorClick from "./CursorClick";
import "./Presence.css";
import { useMouseMove } from "./useMouseMove";
import { getRootElt, ratioToPos } from "./utils";

type CursorInfo = {
  connId: string;
  x: number;
  y: number;
};

let lastClickId = 0;
type ClickInfo = CursorInfo & { id: number };

function Presence({
  children,
  cursorGroupName,
  enabled,
  animated = true,
}: PropsWithChildren<{
  cursorGroupName: string;
  enabled: boolean;
  animated?: boolean;
}>) {
  const { onMouseLeave, onMouseMove, onClickCapture } = useMouseMove();

  const [isCursorGroupReady, setIsCursorGroupReady] = useState(false);
  const [cursors, setCursors] = useState<{
    [connId: string]: CursorInfo;
  }>({});
  const [clicks, setClicks] = useState<ClickInfo[]>([]);

  const serverInterfaceController = useServerInterfaceController();

  useEffect(() => {
    if (!isCursorGroupReady) {
      setCursors({});
      return;
    }
    return serverInterfaceController.onCursorEvent.addListener((e) => {
      for (const { connId, event } of e.events)
        switch (event.type) {
          case CursorEventType.CLICK:
          case CursorEventType.MOVE: {
            const { rX, rY } = event;
            const rawPos = ratioToPos({ rX, rY });
            if (rawPos.x === null && rawPos.y === null) {
              break;
            }
            const x = clamp(0, getRootElt().clientWidth - 20, rawPos.x);
            const y = clamp(0, getRootElt().clientHeight + 20, rawPos.y);

            const cursor = { connId, x, y };
            setCursors((cursors) => ({ ...cursors, [connId]: cursor }));

            if (event.type === CursorEventType.CLICK) {
              const click = { ...cursor, id: lastClickId++ };
              setClicks((clicks) => [...clicks, click]);
            }

            break;
          }
          case CursorEventType.REMOVE: {
            setCursors((value) => {
              const { [connId]: _, ...rest } = value;
              return rest;
            });
            break;
          }
        }
    });
  }, [serverInterfaceController, isCursorGroupReady]);

  useEffect(() => {
    if (clicks.length > 0) {
      setClicks([]);
    }
  }, [clicks]);

  useEffect(() => {
    if (!enabled) return;
    return serverInterfaceController.addAuthEffect(() => {
      const reqId = serverInterfaceController.nextEnterRoomReqId++;
      const cleanupOnAck =
        serverInterfaceController.onSetCursorGroupAckEvent.addListener(
          (ackReqId) => {
            if (ackReqId !== reqId) return;
            setIsCursorGroupReady(true);
          }
        );
      serverInterfaceController.sendReq({
        type: WSReqType.SET_CURSOR_GROUP,
        cursorGroupName,
        reqId,
      });
      return () => {
        cleanupOnAck();
        setIsCursorGroupReady(false);
        serverInterfaceController.sendReq({
          type: WSReqType.SET_CURSOR_GROUP,
          cursorGroupName: null,
          reqId,
        });
      };
    }, ServerInterfaceType.CURSOR);
  }, [enabled, serverInterfaceController, cursorGroupName]);

  if (!enabled) {
    return <div id="game-presence">{children}</div>;
  }

  return (
    <div
      id="game-presence"
      onClickCapture={onClickCapture}
      onMouseLeave={onMouseLeave}
      onMouseMove={onMouseMove}
    >
      {Object.values(cursors).map(({ connId, x, y }) => (
        <Cursor key={connId} connId={connId} x={x} y={y} animated={animated} />
      ))}
      <AnimatePresence>
        {clicks.map(({ id, connId, x, y }) => (
          <CursorClick key={id} connId={connId} x={x} y={y} />
        ))}
      </AnimatePresence>
      {children}
    </div>
  );
}

export default Presence;
