import CardDisplay, { CardSize } from "components/CardDisplay";
import Tooltip from "components/Tooltip";
import { useCardUnlocksForSelectedPuzzle } from "stores/ClientGlobalStateStore";
import "./Friends.css";

function Friends() {
  const cardUnlocksForPuzzle = useCardUnlocksForSelectedPuzzle();

  if (!cardUnlocksForPuzzle) return null;
  if (cardUnlocksForPuzzle.length === 0) return null;

  const cardUnlocksDom = cardUnlocksForPuzzle.map((cardName) => {
    return (
      <Tooltip
        key={cardName}
        placement="right"
        into={true}
        noWrap={true}
        text={
          <CardDisplay
            cardName={cardName}
            size={CardSize.LARGE}
            canZoom={false}
          />
        }
      >
        <CardDisplay
          cardName={cardName}
          size={CardSize.SMALL}
          canZoom={false}
        />
      </Tooltip>
    );
  });

  return (
    <div className="friends-found">
      <h4>Friends found</h4>
      <div className="friends-found-cards">{cardUnlocksDom}</div>
    </div>
  );
}

export default Friends;
