import Tooltip from "components/Tooltip";
import { useState } from "react";
import { ClickablesProps } from "./Clickable";
import "./ClickableSmall.css";

interface ClickableSmallProps extends ClickablesProps {
  windowScrollHeight: number;
  scrollTo: () => Promise<void>;
}

function ClickableSmall(props: ClickableSmallProps) {
  const {
    displayName,
    isSolved,
    mapPos,
    isAnyRoomActive,
    windowScrollHeight,
    scrollTo,
    icon,
  } = props;

  const [scrolling, setScrolling] = useState(false);

  return (
    <Tooltip
      text={scrolling ? undefined : displayName}
      placement="right"
      directRef={(props) => (
        <div
          className={`minimap-puz ${isSolved ? "solved" : "unsolved"} ${
            isAnyRoomActive ? "active" : ""
          }`}
          tabIndex={0}
          onClick={async () => {
            setScrolling(true);
            await scrollTo();
            setScrolling(false);
          }}
          style={{
            left: `${((mapPos.x - 20) / 60) * 100}%`,
            top: `${
              (((mapPos.y / 100) * window.innerWidth -
                window.innerWidth * 0.5) /
                (windowScrollHeight - window.innerWidth * 0.5)) *
              100
            }%`,
          }}
          {...props}
        >
          {icon}
          {/* we want to use text-shadow, so don't: <Twemoji>{icon}</Twemoji> */}
        </div>
      )}
    />
  );
}

export default ClickableSmall;
