import { useEffect, useState, useCallback } from "react";
import "./Mask.css";

import { useMutationObserver } from "stores/NavStore";

const useSize = (elt: Element | null) => {
  const [width, setWidth] = useState(0);
  const [height, setHeight] = useState(0);

  useEffect(() => {
    if (!elt) return;

    const observer = new ResizeObserver(() => {
      setWidth(elt.scrollWidth);
      setHeight(elt.scrollHeight);
    });

    observer.observe(elt);
    return () => observer.disconnect();
  }, [elt]);

  return { width, height };
};

interface UnmaskRectProps {
  unmaskSelector: string;
}

const UNMASK_PADDING = 10;

const UnmaskRect = (props: UnmaskRectProps) => {
  const { unmaskSelector } = props;
  const [maskElt, setMaskElt] = useState<HTMLElement | null>(null);
  const [elt, setElt] = useState<HTMLElement | null>(null);
  const [x, setX] = useState<number>(0);
  const [y, setY] = useState<number>(0);

  useMutationObserver(unmaskSelector, setElt);
  useMutationObserver("#keyframe-mask-img", setMaskElt);

  const updateOffset = useCallback(() => {
    if (maskElt === null) return;
    if (elt === null) return;
    const { top: maskTop, left: maskLeft } = maskElt.getBoundingClientRect();
    const { top: eltTop, left: eltLeft } = elt.getBoundingClientRect();
    setX(eltLeft - maskLeft);
    setY(eltTop - maskTop);
  }, [maskElt, elt, setX, setY]);

  useEffect(() => {
    updateOffset();
    window.addEventListener("resize", updateOffset, { passive: true });
    return () => {
      window.removeEventListener("resize", updateOffset);
    };
  }, [updateOffset]);

  const { width, height } = useSize(elt);

  return (
    <rect
      x={x - UNMASK_PADDING}
      y={y - UNMASK_PADDING}
      width={width + 2 * UNMASK_PADDING}
      height={height + 2 * UNMASK_PADDING}
      fill="black"
    />
  );
};

interface MaskProps {
  selector?: string;
  unmaskSelectors?: string[];
}

const Mask = (props: MaskProps) => {
  const { selector, unmaskSelectors } = props;
  const [elt, setElt] = useState<Element | null>(null);
  const { width, height } = useSize(elt);

  useMutationObserver("#game-page-root-wrapper", setElt);

  return (
    <div className="keyframe-mask">
      <svg
        id="keyframe-mask-img"
        className="keyframe-mask-img"
        width={width}
        height={height}
        xmlns="http://www.w3.org/2000/svg"
      >
        <mask id="keyframe-svg-mask-defn">
          {/* white is visible */}
          <rect x={0} y={0} width={width} height={height} fill="white" />
          {/* black is invisible */}
          {selector ? (
            <UnmaskRect key={selector} unmaskSelector={selector} />
          ) : null}
          {(unmaskSelectors ?? []).map((selector) => (
            <UnmaskRect key={selector} unmaskSelector={selector} />
          ))}
        </mask>

        <rect
          x={0}
          y={0}
          width={width}
          height={height}
          fill="currentColor"
          mask="url(#keyframe-svg-mask-defn)"
        />
      </svg>
    </div>
  );
};

export default Mask;
