import { useEffect, useState } from "react";
import { SYMBOL_INFO } from "engine/types/card-data";
import { Faction, factionToColor, capitalize } from "engine/types/factions";
import { FACTIONS_MASTERY_ID } from "engine/puzzles/mastery-data";
import {
  useSubscribedTeam,
  useClientGlobalStateStore,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";

import "./FactionScoreTotalsDisplay.css";
import FactionIcon from "components/FactionIcon";

const GRAPH_BUCKETS = 24; // Each bucket is one hour

function FactionScoreTotalsDisplay() {
  const subscribedTeam = useSubscribedTeam();
  const factionHistory = useClientGlobalStateStore(
    (state) => state.globalState.factionHistory
  );
  const { setNeedFactionHistory } = useGlobalSubscriptionStore();
  useEffect(() => {
    setNeedFactionHistory(true);
    // Don't unsubscribe from faction history on unmount to prevent
    // needing to resync the full state every time.
  }, [setNeedFactionHistory]);
  const [hoverFaction, setHoverFaction] = useState<string | null>(null);

  if (subscribedTeam === null) return null;
  const { faction: teamFaction, factionScores, masteries } = subscribedTeam;

  if (!masteries[FACTIONS_MASTERY_ID].isEnabled || teamFaction === null)
    return null;

  const orderedFactions = Object.entries(factionScores).sort(
    ([faction1, score1], [faction2, score2]) => score2 - score1
  );
  const epoch = Math.floor(Date.now() / 1000 / 60 / 60) - GRAPH_BUCKETS;
  const graphPoints = Object.fromEntries(
    Object.values(Faction).map((faction) => [
      faction,
      [] as { time: number; score: number }[],
    ])
  );
  for (const [key, value] of Object.entries(factionHistory).sort()) {
    const [faction, timeBucket] = key.split(".");
    graphPoints[faction].push({ time: +timeBucket - epoch, score: value });
  }
  let maxScore = 1;
  const graphLines = orderedFactions.map(([faction]) => {
    const line = [];
    let total = 0;
    for (const { time, score } of [
      ...graphPoints[faction],
      { time: Infinity, score: 0 },
    ]) {
      while (line.length < time && line.length <= GRAPH_BUCKETS)
        line.push(total);
      total += score;
    }
    maxScore = Math.max(maxScore, total);
    return { faction, line };
  });
  const scoreGraphDom = graphLines.map(({ faction, line }) => (
    <path
      key={faction}
      onMouseEnter={() => setHoverFaction(faction)}
      onMouseLeave={() => setHoverFaction(null)}
      fill="none"
      stroke={SYMBOL_INFO[factionToColor(faction as Faction)].color(400)}
      style={{ strokeDasharray: faction === hoverFaction ? "1 1" : undefined }}
      d={line
        .map(
          (score, i) =>
            `${i ? "L" : "M"} ${i} ${
              (1 - score / (maxScore * 1.2)) * GRAPH_BUCKETS * 0.75
            }`
        )
        .join(" ")}
    >
      <title>{capitalize(faction)}</title>
    </path>
  ));
  const scoreTotalsDom = orderedFactions.map(([faction, score]) => (
    <div
      className={`faction-score-totals-row${
        faction === teamFaction ? " highlight" : ""
      }`}
      onMouseEnter={() => setHoverFaction(faction)}
      onMouseLeave={() => setHoverFaction(null)}
      style={{
        backgroundColor:
          faction === hoverFaction ? "rgba(255,255,255,0.5)" : undefined,
      }}
      key={faction}
    >
      <FactionIcon faction={faction as Faction} />
      <div className="faction-score-totals-score">{score}</div>
    </div>
  ));

  return (
    <div className="faction-score-totals-display">
      <div>
        <h4>Global faction scores</h4>
      </div>
      <div className="faction-score-totals-main">
        <svg
          className="faction-score-totals-graph"
          viewBox={`0 0 ${GRAPH_BUCKETS} ${GRAPH_BUCKETS * 0.75}`}
          preserveAspectRatio="none"
        >
          {scoreGraphDom}
        </svg>
        <div className="faction-score-totals-list">{scoreTotalsDom}</div>
      </div>
    </div>
  );
}

export default FactionScoreTotalsDisplay;
