import Tooltip from "components/Tooltip";
import { PUZ_NAME_PVP, PVP_TURN_TIME_LIMIT } from "engine/puzzles/puzzle-data";
import { speedrunTimeToString } from "game-server/global-updates";
import { WSReqType } from "game-server/ws";
import { useEffect, useRef, useState } from "react";
import { usePuzName } from "stores/NavStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import "./SpeedrunTimer.css";

// 3 minutes / 6 = 30 seconds
const LOW_TIME_THRESHOLD = PVP_TURN_TIME_LIMIT / 6;

interface SpeedrunTimerProps {
  prevTurnsTime: number;
  startTurnTime: number | null;
  keepRunningDuringUpdates: boolean;
  isGameActive: boolean;
  isOurTimer: boolean;
}

function SpeedrunTimer(props: SpeedrunTimerProps) {
  const { keepRunningDuringUpdates, isGameActive, isOurTimer } = props;
  const timerRef = useRef<HTMLDivElement>(null);
  const isPvP = usePuzName() === PUZ_NAME_PVP;
  const [outOfTime, setOutOfTime] = useState(false);
  const serverInterface = useServerInterfaceController();
  const [timerConfigSnapshot, setTimerConfigSnapshot] = useState<{
    prevTurnsTime: number;
    startTurnTime: number;
  } | null>(null);

  useEffect(() => {
    if (!isGameActive) {
      setTimerConfigSnapshot(null);
      return;
    }
    if (!keepRunningDuringUpdates) return;
    if (props.startTurnTime === null) return;
    setTimerConfigSnapshot({
      prevTurnsTime: props.prevTurnsTime,
      startTurnTime: props.startTurnTime,
    });
  }, [
    keepRunningDuringUpdates,
    isGameActive,
    props.prevTurnsTime,
    props.startTurnTime,
    setTimerConfigSnapshot,
  ]);

  useEffect(() => {
    let animationFrame: number | null = null;
    const updateTimer = () => {
      if (timerRef.current !== null) {
        const {
          prevTurnsTime = props.prevTurnsTime,
          startTurnTime = props.startTurnTime,
        } = timerConfigSnapshot ?? {};
        const isTimerRunning = startTurnTime !== null;
        const currTurnTime = isTimerRunning
          ? new Date().getTime() - startTurnTime
          : 0;
        const timeToShow = (() => {
          if (isPvP) {
            const timeLeft = Math.max(0, PVP_TURN_TIME_LIMIT - currTurnTime);
            setOutOfTime(!isOurTimer && timeLeft <= 0);
            if (!isTimerRunning) return null;
            return timeLeft;
          }
          return prevTurnsTime + currTurnTime;
        })();
        timerRef.current.className = !isPvP
          ? ""
          : `${isTimerRunning ? "count-down" : ""}
             ${
               timeToShow !== null && timeToShow < LOW_TIME_THRESHOLD
                 ? "low-time"
                 : ""
             }`;
        timerRef.current.textContent =
          timeToShow !== null
            ? speedrunTimeToString(Math.max(0, timeToShow))
            : "";
      }
      animationFrame = requestAnimationFrame(updateTimer);
    };
    updateTimer();
    return () => {
      if (animationFrame !== null) cancelAnimationFrame(animationFrame);
    };
  }, [
    isOurTimer,
    isPvP,
    props.prevTurnsTime,
    props.startTurnTime,
    timerConfigSnapshot,
  ]);

  const claimTimeVictory = () => {
    serverInterface.sendReq({ type: WSReqType.PVP_CLAIM_TIME_VICTORY });
  };

  return (
    <div className="speedrun-timer">
      {isGameActive || !isPvP ? <div ref={timerRef} /> : null}
      {outOfTime ? (
        <Tooltip
          text="Claim a win because your opponent ran out of time."
          directRef={(props) => (
            <button {...props} onClick={claimTimeVictory}>
              Claim win on time
            </button>
          )}
        />
      ) : null}
    </div>
  );
}

export default SpeedrunTimer;
