import "./StepButton.css";
import { PropsWithChildren, useEffect, useState } from "react";

import { describeStep } from "components/BattleHelpText";
import HoverNotifier from "components/HoverNotifier";
import {
  stepToGain,
  stepToName,
  stepToPayment,
  Step,
  StepType,
} from "engine/types/steps";
import {
  Check,
  getFailText,
  isPassthrough,
} from "engine/types/action-validation";
import {
  useInspector,
  useStepMaker,
  useRole,
  useClientGameStore,
  usePlayerPerspective,
  useIsLockedBesidesKeyframe,
} from "stores/ClientGameStore";
import { AbilityType } from "engine/types/effects";
import { useEffectOpts } from "stores/TurnStore";
import { GemType, GemsDisplay, GemProp } from "components/GemDisplay";
import { useStartEffect } from "stores/UserGameActionHandlers";

interface StepButtonProps {
  id?: string;
  shortcut?: string;
  step: Step;
}

/**
 * Wraps children, so when clicked, a step happens. If there are no children,
 * it makes a generic button. There are three states:
 *
 * - .step-valid: We change the cursor, and, if true, show an overlayIcon. On
 * hover, we do the HoverNotifier thing. On click, the step will be sent.
 * - .step-preview: We change the cursor, and, if true, show an overlayIcon. On
 * hover, we do the HoverNotifier thing. On click, nothing will happen.
 * - passthrough: nothing! at all! we just pass it on
 */
function StepButton(props: PropsWithChildren<StepButtonProps>) {
  const { id, children, step, shortcut } = props;
  const startEffect = useStartEffect();
  const { pendingForm } = useEffectOpts();
  const sendStep = useClientGameStore((state) => state.sendStep);
  const role = useRole();
  const inspector = useInspector();
  const stepMaker = useStepMaker();
  const playerPerspective = usePlayerPerspective();
  const { keyframe } = inspector.gameState;
  const isLockedBesidesKeyframe = useIsLockedBesidesKeyframe();

  const { resolved, failedChecks } = inspector.resolveAndValidate(step, role);

  // Visually disable the UI if the UI is locked.
  if (isLockedBesidesKeyframe) failedChecks.add(Check.UI_LOCKED);
  // Ignore NUM_OPTS for effects:
  failedChecks.delete(Check.NUM_OPTS);
  const isValid = failedChecks.size === 0;

  const payment = stepToPayment(resolved);
  const gain = stepToGain(resolved);

  const helpText = stepToName(step);
  const gemsDisplay =
    payment.length === 0 && gain.length === 0 ? null : (
      <GemsDisplay
        gems={new Array<GemProp>()
          .concat(payment.map((c) => ({ color: c, type: GemType.CROSSED })))
          .concat(gain.map((c) => ({ color: c, type: GemType.SOLID })))}
        inline={true}
      />
    );

  const failText = getFailText(failedChecks);

  const passthrough = pendingForm || isPassthrough(failedChecks, step.type);

  const [delayed, setDelayed] = useState(step.type === StepType.END_TURN);

  useEffect(() => {
    setTimeout(() => setDelayed(false), 300);
  }, []);

  const isEndingTurn =
    step.type === StepType.END_TURN &&
    inspector.isTurnPending(playerPerspective);
  const hasActionsLeft = ((): boolean => {
    if (!isEndingTurn) return false;
    for (const permanent of inspector.getAllPermanents())
      if (inspector.hasActions(permanent)) return true;
    const draw = inspector.resolveAndValidate(
      stepMaker.draw(playerPerspective),
      role
    );
    if (draw.isValid) return true;
    for (const card of inspector.getHandForUI(playerPerspective))
      for (const slot of inspector.getAllSlots()) {
        const summon = inspector.resolveAndValidate(
          stepMaker.summonOrSpawn({
            player: playerPerspective,
            slot,
            handCardId: card.id,
            cardName: card.card.name,
          }),
          role
        );
        if (summon.isValid) return true;
      }
    return false;
  })();
  const helpTooltip =
    step.type === StepType.END_TURN && failedChecks.has(Check.UI_LOCKED)
      ? ""
      : failText ??
        (isEndingTurn && hasActionsLeft
          ? "There are still actions you can take!"
          : describeStep(inspector, step, resolved));

  // TODO: replace with better text maybe
  const defaultButton = passthrough ? null : (
    <button
      className={`default-step-button${
        isEndingTurn && failedChecks.size === 0 && !hasActionsLeft
          ? " important-step-button"
          : ""
      }`}
      disabled={!isValid || delayed}
      style={delayed ? { cursor: "wait" } : {}}
    >
      {helpText}
      {shortcut ? ` (${shortcut})` : null}
      {gemsDisplay === null ? null : <> {gemsDisplay}</>}
    </button>
  );

  return passthrough ? (
    <>{children}</>
  ) : (
    <div
      id={id}
      className={`step-button ${isValid ? "step-valid" : "step-preview"}`}
      onClick={(e) => {
        if (isValid) {
          e.stopPropagation();

          // If the server is down, don't accept creating new steps.
          // However, still mark inputs as clickable so that the click
          // handler doesn't propagate to the parent and cause a deselect.
          if (!sendStep) return;

          // ACTIVATE_ABILITY might need EffectOpts to be determined.
          if (step.type === StepType.ACTIVATE_ABILITY) {
            const permanent = inspector.getPermanent(step.permanentId);
            const effects = inspector.getSharedEffects(permanent);
            const effectForms =
              step.abilityType === AbilityType.FLEX
                ? effects.flexForms
                : effects.specialForms;

            if (effectForms && effectForms.length > 0) {
              startEffect(step.abilityType, effectForms);
              return;
            }
          }

          sendStep(step);
        }
      }}
    >
      <HoverNotifier payment={payment} gain={gain} help={helpTooltip}>
        {children ?? defaultButton}
      </HoverNotifier>
    </div>
  );
}

export default StepButton;
