import { useEffect } from "react";
import { SPEEDRUN_MASTERY_ID } from "engine/puzzles/mastery-data";
import {
  GlobalUpdatesScopeType,
  ClientSpeedrunTimeRecord,
  speedrunTimeToString,
  SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH,
} from "game-server/global-updates";

import {
  useServerInterfaceController,
  useTeamId,
} from "stores/ServerInteractionStore";
import {
  useSubscribedTeam,
  useSubscribedTeamSpeedrunLeaderboardSummary,
} from "stores/ClientGlobalStateStore";
import TeamDisplayNameDisplay, {
  clientTeamGlobalStateToTeamNameDisplayData,
} from "components/TeamDisplayNameDisplay";
import "./SpeedrunLeaderboardSummary.css";

// TODO: Take in the speedrun info as props so that we don't lose
// data when the selected puzzle is changed in instancer while the
// solved modal is open.
function SpeedrunLeaderboardSummary({ puzName }: { puzName: string | null }) {
  const serverInterfaceController = useServerInterfaceController();
  const teamId = useTeamId();
  const subscribedTeam = useSubscribedTeam();
  const subscribedTeamSpeedrunLeaderboardSummary =
    useSubscribedTeamSpeedrunLeaderboardSummary(puzName);

  useEffect(() => {
    if (teamId === null) return;
    if (puzName === null) return;
    return serverInterfaceController.addSubscribeEffect({
      type: GlobalUpdatesScopeType.TEAM_SPEEDRUN_LEADERBOARD_SUMMARY,
      teamId,
      puzName,
    });
  }, [serverInterfaceController, teamId, puzName]);

  if (
    subscribedTeam === null ||
    subscribedTeamSpeedrunLeaderboardSummary === null ||
    puzName === null
  )
    return null;

  const { masteries } = subscribedTeam;
  const { speedrunLeaderboardSummary, speedrunRank } =
    subscribedTeamSpeedrunLeaderboardSummary;
  const speedrunTime = subscribedTeam.puzzles[puzName]?.speedrunTime;

  if (
    !masteries[SPEEDRUN_MASTERY_ID].isEnabled ||
    speedrunLeaderboardSummary === undefined ||
    speedrunLeaderboardSummary.length <= 0 ||
    speedrunRank === undefined ||
    speedrunTime === undefined
  )
    return null;

  const makeSpeedrunRecordRow = (
    rank: number,
    record: ClientSpeedrunTimeRecord
  ) => (
    <div key={record.teamData.teamId} className="prep-speedrun-record">
      <div className="prep-speedrun-rank-cell">{rank}</div>
      <div className="prep-speedrun-name-cell">
        <TeamDisplayNameDisplay teamData={record.teamData} />
      </div>
      <div>{speedrunTimeToString(record.speedrunTime)}</div>
    </div>
  );

  const leaderboardDom = speedrunLeaderboardSummary
    .slice(0, SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH)
    .map((record, i) => makeSpeedrunRecordRow(i + 1, record));

  const selfRankDom =
    speedrunRank < SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH ? null : (
      <>
        {speedrunRank > SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH ? (
          <div>‚ãÆ</div>
        ) : null}
        {makeSpeedrunRecordRow(speedrunRank + 1, {
          teamData: clientTeamGlobalStateToTeamNameDisplayData(subscribedTeam),
          speedrunTime,
        })}
      </>
    );

  return (
    <div className="prep-speedrun">
      <div>
        <h4>Top runs</h4>
      </div>
      {leaderboardDom}
      {speedrunRank >= SPEEDRUN_LEADERBOARD_SUMMARY_LENGTH ? selfRankDom : null}
    </div>
  );
}

export default SpeedrunLeaderboardSummary;
