import FactionIcon from "components/FactionIcon";
import Tooltip from "components/Tooltip";
import { FACTIONS_MASTERY_ID } from "engine/puzzles/mastery-data";
import { capitalize } from "engine/types/factions";
import Twemoji from "react-twemoji";
import { Link, useSearchParams } from "react-router-dom";
import {
  useSubscribedTeam,
  useSubscribedTeamBattle,
} from "stores/ClientGlobalStateStore";
import "./PrepFactionDisplay.css";

function PrepFactionDisplay() {
  const subscribedTeam = useSubscribedTeam();
  const { activeGame } = useSubscribedTeamBattle() ?? {};
  const { faction: teamFaction, masteries } = subscribedTeam ?? {};
  const isFactionsEnabled =
    masteries?.[FACTIONS_MASTERY_ID]?.isEnabled ?? false;
  const [searchParams] = useSearchParams();

  if (!isFactionsEnabled) return null;

  const faction = activeGame?.faction ?? teamFaction;

  const lock = activeGame ? (
    <Tooltip
      className="faction-lock"
      text="Faction is locked; changes won't affect the current battle."
    >
      <Twemoji>üîí</Twemoji>
    </Tooltip>
  ) : null;

  return (
    <>
      {faction ? (
        <div className="faction-display">
          {lock}
          <b>Faction:</b> <FactionIcon faction={faction} />{" "}
          {capitalize(faction)} <FactionIcon faction={faction} />
        </div>
      ) : (
        <div className="no-faction-display">
          {lock} <Twemoji>‚ö†Ô∏è</Twemoji> You don‚Äôt have a faction yet!
        </div>
      )}
      <Link
        className="change-faction-btn"
        to={{
          pathname: `/team/${encodeURIComponent(
            encodeURIComponent(subscribedTeam?.displayName ?? "")
          )}`,
          search: searchParams.toString(),
        }}
      >
        <button className="secondary">
          {faction === null ? "Choose a faction" : "Change faction"}
        </button>
      </Link>
    </>
  );
}

export default PrepFactionDisplay;
