import { useEffect, useState } from "react";
import "./PrepRight.css";

import FactionScoreTotalsDisplay from "components/FactionScoreTotalsDisplay";
import SpeedrunLeaderboardSummary from "components/SpeedrunLeaderboardSummary";
import Tabs from "components/Tabs";
import {
  FACTIONS_MASTERY_ID,
  SPEEDRUN_MASTERY_ID,
} from "engine/puzzles/mastery-data";
import { Player } from "engine/types/game-state";
import { Role } from "engine/types/updates";
import { useStaticInspector } from "stores/ClientGameStore";
import {
  useSelectedBattleGroup,
  useSelectedPuzName,
  useSubscribedTeam,
  useSelectedDeckData,
  useCardUnlocksForSelectedPuzzle,
} from "stores/ClientGlobalStateStore";
import BattleControl from "./BattleControl";
import BattleStatus from "./BattleStatus";
import DeckSelection from "./DeckSelection";
import Friends from "./Friends";
import PrepFactionDisplay from "./PrepFactionDisplay";
import PrepMasteries from "./PrepMasteries";
import SubpuzzleSelection from "./SubpuzzleSelection";

const useWindowDimensions = () => {
  const [height, setHeight] = useState(window.innerHeight);
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    const onResize = () => {
      setHeight(window.innerHeight);
      setWidth(window.innerWidth);
    };
    onResize();
    window.addEventListener("resize", onResize, { passive: true });
    return () => window.removeEventListener("resize", onResize);
  });

  return { height, width };
};

interface PrepRightProps {
  role: Role;
  player: Player;
}

function PrepRight(props: PrepRightProps) {
  const { role, player } = props;
  const inspector = useStaticInspector();

  const selectedPuzName = useSelectedPuzName();
  const battleGroup = useSelectedBattleGroup();
  const { isCutscene = false } = battleGroup ?? {};

  const subscribedTeam = useSubscribedTeam();
  const { faction, masteries, puzzles } = subscribedTeam ?? {};
  const cardUnlocksForPuzzle = useCardUnlocksForSelectedPuzzle();

  const isFactionsEnabled =
    masteries?.[FACTIONS_MASTERY_ID]?.isEnabled ?? false;
  const isSpeedrunEnabled =
    masteries?.[SPEEDRUN_MASTERY_ID]?.isEnabled ?? false;
  const hasStats =
    (isFactionsEnabled && faction !== null) ||
    (isSpeedrunEnabled &&
      selectedPuzName !== null &&
      (puzzles ?? {})[selectedPuzName]?.solveTime !== undefined) ||
    (cardUnlocksForPuzzle !== null && cardUnlocksForPuzzle.length > 0);

  const { canSelectDeck = false } = useSelectedDeckData(player);

  const { height } = useWindowDimensions();
  const isTall = (height: number) => {
    return height > 880;
  };
  const [activeTab, setActiveTab] = useState(isTall(height) ? -1 : 0);

  useEffect(() => {
    if (isTall(height)) {
      if (activeTab !== -1) setActiveTab(-1);
    } else {
      if (activeTab === -1) setActiveTab(0);
    }
  }, [activeTab, height]);

  const tabItems = [
    {
      label: "Battle",
      content: (
        <>
          <PrepFactionDisplay />
          <DeckSelection role={role} player={player} />
          <PrepMasteries />
        </>
      ),
    },
    {
      label: "Stats",
      content: (
        <>
          <FactionScoreTotalsDisplay />
          <SpeedrunLeaderboardSummary puzName={selectedPuzName} />
          <Friends />
        </>
      ),
    },
  ];

  return (
    <div className="prep-right">
      <SubpuzzleSelection />
      {!isCutscene && activeTab !== -1 && hasStats ? (
        <Tabs.Tabbed
          className="prep"
          index={activeTab}
          setIndex={(index) => setActiveTab(index)}
          style={{
            minHeight: canSelectDeck
              ? `calc(12 * var(--deck-row-height) + 5.25em)`
              : `12em`,
          }}
          items={tabItems}
        />
      ) : (
        <div
          className="prep-tabbed prep-content"
          style={{
            minHeight: canSelectDeck
              ? `calc(12 * var(--deck-row-height) + 5.25em)`
              : `12em`,
          }}
        >
          {isCutscene ? null : <PrepFactionDisplay />}
          <DeckSelection role={role} player={player} />
          {isCutscene ? null : <PrepMasteries />}
          {isCutscene ? null : <FactionScoreTotalsDisplay />}
          {isCutscene ? null : (
            <SpeedrunLeaderboardSummary puzName={selectedPuzName} />
          )}
          <Friends />
        </div>
      )}
      <BattleStatus />
      <BattleControl player={player} />
    </div>
  );
}

export default PrepRight;
