import { useEffect, useRef, useState } from "react";
import "./AdminPage.css";

import { WSReqType } from "game-server/ws";
import { getPuzzleStageNumbers } from "game-server/global-updates";
import {
  useServerInterfaceController,
  useTeamId,
} from "stores/ServerInteractionStore";
import {
  useClientGlobalStateStore,
  useSubscribedTeam,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import { useCardsDBStore } from "stores/CardsDBStore";
import { LargeSpinner } from "components/Loading";

interface AdminPuzzleProps {
  teamId: string;
  puzName: string;
  displayName: string;
  isSolved: boolean;
  isUnlocked: boolean;
  answer?: string;
}

const AdminPuzzleDisplay = (props: AdminPuzzleProps) => {
  const { teamId, puzName, displayName, isSolved, isUnlocked, answer } = props;
  const solveInputRef = useRef<HTMLInputElement>(null);
  const serverInterfaceController = useServerInterfaceController();

  // TODO: Make links to the puzzles.
  const puzLabel = displayName;

  return (
    <div className="admin-puz" key={puzName}>
      <div className="admin-puz-label">
        {puzLabel}
        {answer === undefined ? null : ` (${answer})`}
      </div>
      <div>
        <button
          disabled={isSolved || !isUnlocked}
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideSolved: { [puzName]: true },
                },
              },
            });
          }}
        >
          Set solved
        </button>
      </div>
      <div>
        <button
          disabled={!isSolved}
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideSolved: { [puzName]: false },
                },
              },
            });
          }}
        >
          Set unsolved
        </button>
      </div>
      <div className="admin-puz-spacer"></div>
      <div>
        <button
          disabled={isUnlocked}
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideUnlocked: { [puzName]: true },
                },
              },
            });
          }}
        >
          Set unlocked
        </button>
      </div>
      <div>
        <button
          disabled={!isUnlocked}
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideUnlocked: { [puzName]: false },
                },
              },
            });
          }}
        >
          Set locked
        </button>
      </div>
      <div>(dev only)</div>
    </div>
  );
};

interface AdminCardProps {
  teamId: string;
  cardName: string;
  displayName: string;
  isUnlocked: boolean;
}

const AdminCardDisplay = (props: AdminCardProps) => {
  const { teamId, cardName, displayName, isUnlocked } = props;
  const serverInterfaceController = useServerInterfaceController();

  return (
    <div className="admin-puz" key={cardName}>
      <div className="admin-puz-label">{displayName}</div>
      <div>
        <button
          disabled={isUnlocked}
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideCardUnlocked: { [cardName]: true },
                },
              },
            });
          }}
        >
          Set unlocked
        </button>
      </div>
      <div>
        <button
          disabled={!isUnlocked}
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideCardUnlocked: { [cardName]: false },
                },
              },
            });
          }}
        >
          Set locked
        </button>
      </div>
      <div>(dev only)</div>
    </div>
  );
};

function TeamAdminDisplay() {
  const [displayNameInput, setDisplayNameInput] = useState("");
  const { subscribedTeamId } = useGlobalSubscriptionStore();
  const teamAdminData = useClientGlobalStateStore(
    (state) => state.globalState.teamAdminData
  );
  const puzzles = useClientGlobalStateStore(
    (state) => state.globalState.puzzles
  );
  const battleGroups = useClientGlobalStateStore(
    (state) => state.globalState.battleGroups
  );
  const subscribedTeam = useSubscribedTeam(subscribedTeamId);
  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const serverInterfaceController = useServerInterfaceController();
  const displayName =
    subscribedTeam === null ||
    teamAdminData[subscribedTeam.teamId] === undefined
      ? ""
      : teamAdminData[subscribedTeam.teamId].displayName;

  useEffect(() => {
    if (displayName === null) return;
    // Keep the display name input in sync if the display name
    // changes due to external sources.
    setDisplayNameInput(displayName);
  }, [displayName, setDisplayNameInput]);

  if (
    subscribedTeam === null ||
    Object.keys(puzzles).length === 0 ||
    teamAdminData[subscribedTeam.teamId] === undefined ||
    displayName === null
  )
    return <LargeSpinner />;

  const { teamId, puzzles: unlockedPuzzles, cardUnlocks } = subscribedTeam;
  const { isHidden, isInactive, isTempBlocked } = teamAdminData[teamId];
  const stageNumbers = getPuzzleStageNumbers(puzzles, battleGroups);
  const puzzlesDom = Object.values(puzzles)
    .sort((puz1, puz2) => puz1.order - puz2.order)
    .map(({ puzName, battleGroupName }) => {
      const unlockedPuzzle = unlockedPuzzles[puzName];
      const stageNum = stageNumbers[puzName];
      const battleGroupDisplayName = battleGroups[battleGroupName].displayName;
      const displayName =
        stageNum === 0
          ? battleGroupDisplayName
          : `${battleGroupDisplayName}, Stage ${stageNum}`;
      return (
        <AdminPuzzleDisplay
          key={puzName}
          teamId={teamId}
          puzName={puzName}
          displayName={displayName}
          isSolved={unlockedPuzzle?.solveTime !== undefined}
          isUnlocked={unlockedPuzzle !== undefined}
          answer={unlockedPuzzle?.answer}
        />
      );
    });
  const cardsDom = Object.keys(cardsDB)
    .filter((cardName) => cardsDB[cardName].cardUnlockGroupId !== null)
    .sort()
    .map((cardName) => {
      return (
        <AdminCardDisplay
          key={cardName}
          teamId={teamId}
          cardName={cardName}
          displayName={cardsDB[cardName].displayName}
          isUnlocked={cardUnlocks[cardName] !== undefined}
        />
      );
    });
  const setDisplayNameFromInput = () => {
    if (displayNameInput === displayName) return;
    serverInterfaceController.sendReq({
      type: WSReqType.ADMIN_MODIFY,
      teams: {
        [teamId]: {
          displayName: displayNameInput,
        },
      },
    });
  };
  return (
    <>
      <div className="admin-header">
        <div className="admin-selected-team-name">
          <div className="admin-selected-team-name-label">Team name:</div>
          <div>
            <input
              className="admin-team-name-input"
              type="text"
              value={displayNameInput}
              onChange={(e) => {
                setDisplayNameInput(e.target.value);
              }}
              onBlur={() => {
                setDisplayNameFromInput();
              }}
              onKeyPress={(e) => {
                if (e.key === "Enter") {
                  setDisplayNameFromInput();
                }
              }}
            />
          </div>
        </div>
        <div className="admin-header-checkboxes">
          <div className="admin-checkbox-control">
            <input
              type="checkbox"
              id="is-team-hidden-checkbox"
              defaultChecked={isHidden}
              onChange={(e) => {
                serverInterfaceController.sendReq({
                  type: WSReqType.ADMIN_MODIFY,
                  teams: {
                    [teamId]: {
                      isHidden: e.target.checked,
                    },
                  },
                });
              }}
            />{" "}
            <label htmlFor="is-team-hidden-checkbox">Is Hidden</label>
          </div>
          <div className="admin-checkbox-control">
            <input
              type="checkbox"
              id="is-team-inactive-checkbox"
              defaultChecked={isInactive}
              onChange={(e) => {
                serverInterfaceController.sendReq({
                  type: WSReqType.ADMIN_MODIFY,
                  teams: {
                    [teamId]: {
                      isInactive: e.target.checked,
                    },
                  },
                });
              }}
            />{" "}
            <label htmlFor="is-team-inactive-checkbox">Is Inactive</label>
          </div>
          <div className="admin-checkbox-control">
            <input
              type="checkbox"
              id="is-team-temporarily-blocked-checkbox"
              defaultChecked={isTempBlocked}
              onChange={(e) => {
                serverInterfaceController.sendReq({
                  type: WSReqType.ADMIN_MODIFY,
                  teams: {
                    [teamId]: {
                      isTempBlocked: e.target.checked,
                    },
                  },
                });
              }}
            />{" "}
            <label htmlFor="is-team-temporarily-blocked-checkbox">
              Is Temporarily Blocked
            </label>
          </div>
        </div>
      </div>
      <div className="admin-header-spacer" />
      <div className="admin-puzzles">
        {puzzlesDom}
        {cardsDom}
      </div>
    </>
  );
}

function AdminPage() {
  const serverInterfaceController = useServerInterfaceController();
  const selfTeamId = useTeamId();
  const teamAdminData = useClientGlobalStateStore(
    (state) => state.globalState.teamAdminData
  );
  const teams = useClientGlobalStateStore((state) => state.globalState.teams);
  const {
    subscribedTeamId,
    setSubscribedTeamId,
    setNeedAdmin,
    setNeedTeamList,
  } = useGlobalSubscriptionStore();
  const [searchText, setSearchText] = useState("");

  useEffect(() => {
    setNeedAdmin(true);
  }, [setNeedAdmin]);

  useEffect(() => {
    setNeedTeamList(true);
    // Don't unset on unmount to prevent extraneous OVERRIDE_STATEs.
    // We don't have unsubscribe anyway so unsetting is strictly worse.
  }, [setNeedTeamList]);

  useEffect(() => {
    // We use cross-team subscriptions to check out team state.
    // Reset the subscribed team ID to the real team when leaving the page.
    return () => {
      setSubscribedTeamId(selfTeamId);
    };
  }, [setSubscribedTeamId, selfTeamId]);

  if (Object.keys(teamAdminData).length === 0) return <LargeSpinner />;

  const teamsDom = Object.keys(teamAdminData)
    .filter((teamId) => {
      const teamData = teamAdminData[teamId];
      return teamData.displayName.toLowerCase().indexOf(searchText.trim()) >= 0;
    })
    .sort()
    .map((teamId) => {
      const teamData = teamAdminData[teamId];
      const isSelected =
        subscribedTeamId !== null && subscribedTeamId === teamId;
      return (
        <div key={teamId}>
          <button
            className={`admin-team-btn${isSelected ? " highlight" : ""}`}
            onClick={() => {
              setSubscribedTeamId(teamId);
            }}
          >
            {teamData.displayName}
          </button>
        </div>
      );
    });

  return (
    <div className="below-nav">
      <div className="admin-root">
        <div className="admin-sidebar">
          <div className="admin-teams">
            <input
              type="text"
              className="admin-team-search-input"
              onChange={(e) => {
                setSearchText(e.target.value.toLowerCase().trim());
              }}
            />
            {teamsDom}
          </div>
        </div>
        <div className="admin-main">
          <TeamAdminDisplay />
        </div>
      </div>
    </div>
  );
}

export default AdminPage;
