import { useEffect } from "react";
import Twemoji from "react-twemoji";
import "./UnlockPage.css";

import { getUnlockableCards } from "engine/types/card-data";
import { WSReqType } from "game-server/ws";
import { getPuzzleStageNumbers } from "game-server/global-updates";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useStaticInspector } from "stores/ClientGameStore";
import {
  useClientGlobalStateStore,
  useSubscribedTeam,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import { useCardsDBStore } from "stores/CardsDBStore";
import { CardEmbed } from "components/HuntLib/CardEmbed";
import { LargeSpinner } from "components/Loading";
import Tabs from "components/Tabs";
import Tooltip from "components/Tooltip";
import { PuzzleLink, RoutedLink } from "components/Link";
import settings from "settings";
import {
  BATTLE_GROUP_NAME_INSTANCER,
  PUZ_NAME_PVP,
  getPuzzleMapIcon,
} from "engine/puzzles/puzzle-data";

interface UnlockCardProps {
  teamId: string;
  cardName: string;
  displayName: string;
  isUnlocked: boolean;
}

const UnlockCardDisplay = (props: UnlockCardProps) => {
  const { teamId, cardName, displayName, isUnlocked } = props;
  const serverInterfaceController = useServerInterfaceController();
  const inspector = useStaticInspector();
  const isLegendary = inspector.isLegendary(cardName);

  return (
    <div className="unlock-puzzle-display" key={cardName}>
      <Tooltip text={isUnlocked ? "Set locked" : "Set unlocked"}>
        <button
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideCardUnlocked: { [cardName]: !isUnlocked },
                },
              },
            });
          }}
        >
          {isUnlocked ? "üîë" : "üîí"}
        </button>
      </Tooltip>
      <div className="unlock-puzzle-card">
        <CardEmbed content={`${isLegendary ? "üëë " : ""}${displayName}`} />
      </div>
    </div>
  );
};

interface UnlockPuzzleProps {
  teamId: string;
  puzName: string;
  displayName: string;
  isSolved: boolean;
  isUnlocked: boolean;
  hasAnswer: boolean;
  icon?: string;
}

const UnlockPuzzleDisplay = (props: UnlockPuzzleProps) => {
  const {
    teamId,
    puzName,
    displayName,
    isSolved,
    isUnlocked,
    hasAnswer,
    icon,
  } = props;
  const serverInterfaceController = useServerInterfaceController();

  return (
    <div className="unlock-puzzle-display" key={puzName}>
      <Tooltip text={isUnlocked ? "Set locked" : "Set unlocked"}>
        <button
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideUnlocked: { [puzName]: !isUnlocked },
                },
              },
            });
          }}
        >
          {isUnlocked ? "üîë" : "üîí"}
        </button>
      </Tooltip>
      <Tooltip text={isSolved ? "Set unsolved" : "Set solved"}>
        <button
          disabled={
            !isUnlocked ||
            puzName === PUZ_NAME_PVP ||
            puzName === BATTLE_GROUP_NAME_INSTANCER
          }
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.ADMIN_MODIFY,
              teams: {
                [teamId]: {
                  overrideSolved: { [puzName]: !isSolved },
                },
              },
            });
          }}
        >
          {isSolved ? "‚úÖÔ∏è" : "üî≤Ô∏è"}
        </button>
      </Tooltip>
      {isUnlocked ? (
        <PuzzleLink battleGroupName={puzName} hasAnswer={hasAnswer}>
          {icon} {displayName}
        </PuzzleLink>
      ) : (
        <div>
          {icon} {displayName}
        </div>
      )}
    </div>
  );
};

function UnlockCardsDisplay() {
  const { subscribedTeamId } = useGlobalSubscriptionStore();
  const teamAdminData = useClientGlobalStateStore(
    (state) => state.globalState.teamAdminData
  );
  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const subscribedTeam = useSubscribedTeam(subscribedTeamId);
  const inspector = useStaticInspector();

  if (
    subscribedTeam === null ||
    Object.keys(cardsDB).length === 0 ||
    teamAdminData[subscribedTeam.teamId] === undefined
  )
    return <LargeSpinner />;

  const { teamId, cardUnlocks } = subscribedTeam;

  const cardsDom = getUnlockableCards(cardsDB)
    .sort((cardName1, cardName2) => {
      const isLegendary1 = inspector.isLegendary(cardName1);
      const isLegendary2 = inspector.isLegendary(cardName2);
      if (isLegendary1 !== isLegendary2)
        return (isLegendary1 ? 1 : 0) - (isLegendary2 ? 1 : 0);
      return cardName1.localeCompare(cardName2);
    })
    .map((cardName) => {
      return (
        <UnlockCardDisplay
          key={cardName}
          teamId={teamId}
          cardName={cardName}
          displayName={cardsDB[cardName].displayName}
          isUnlocked={cardUnlocks[cardName] !== undefined}
        />
      );
    });

  return <div className="unlock-puzzles">{cardsDom}</div>;
}

function UnlockPuzzlesDisplay() {
  const { subscribedTeamId } = useGlobalSubscriptionStore();
  const teamAdminData = useClientGlobalStateStore(
    (state) => state.globalState.teamAdminData
  );
  const puzzles = useClientGlobalStateStore(
    (state) => state.globalState.puzzles
  );
  const battleGroups = useClientGlobalStateStore(
    (state) => state.globalState.battleGroups
  );
  const subscribedTeam = useSubscribedTeam(subscribedTeamId);

  if (
    subscribedTeam === null ||
    Object.keys(puzzles).length === 0 ||
    teamAdminData[subscribedTeam.teamId] === undefined
  )
    return <LargeSpinner />;

  const { teamId, puzzles: unlockedPuzzles } = subscribedTeam;
  const stageNumbers = getPuzzleStageNumbers(puzzles, battleGroups);

  const puzzlesDom = Object.values(puzzles)
    .filter(({ puzName }) => stageNumbers[puzName] === 0)
    .filter(
      ({ isCutscene, isFullPuzzle, puzName }) =>
        isCutscene ||
        isFullPuzzle ||
        puzName === PUZ_NAME_PVP ||
        puzName === BATTLE_GROUP_NAME_INSTANCER
    )
    .sort((puz1, puz2) => puz1.order - puz2.order)
    .map(({ puzName, battleGroupName }) => {
      const unlockedPuzzle = unlockedPuzzles[puzName];
      const stageNum = stageNumbers[puzName];
      const { isCutscene, answer } = puzzles[puzName];
      const { isLegendary, displayName: battleGroupDisplayName } =
        battleGroups[battleGroupName];
      const hasAnswer = answer !== null;
      const displayName =
        stageNum === 0
          ? battleGroupDisplayName
          : `${battleGroupDisplayName}, Stage ${stageNum}`;
      return (
        <UnlockPuzzleDisplay
          key={puzName}
          teamId={teamId}
          puzName={puzName}
          displayName={displayName}
          isSolved={unlockedPuzzle?.solveTime !== undefined}
          isUnlocked={unlockedPuzzle !== undefined}
          hasAnswer={hasAnswer}
          icon={getPuzzleMapIcon(
            hasAnswer,
            battleGroupName,
            isCutscene,
            isLegendary
          )}
        />
      );
    });

  return <div className="unlock-puzzles">{puzzlesDom}</div>;
}

function UnlockInfo() {
  const { subscribedTeamId } = useGlobalSubscriptionStore();
  const teamAdminData = useClientGlobalStateStore(
    (state) => state.globalState.teamAdminData
  );
  const subscribedTeam = useSubscribedTeam(subscribedTeamId);
  const serverInterfaceController = useServerInterfaceController();
  const allPuzzles = useClientGlobalStateStore(
    (state) => state.globalState.puzzles
  );
  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const inspector = useStaticInspector();

  if (
    subscribedTeam === null ||
    Object.keys(allPuzzles).length === 0 ||
    Object.keys(cardsDB).length === 0 ||
    teamAdminData[subscribedTeam.teamId] === undefined
  )
    return <LargeSpinner />;

  const { battleGroups, cardUnlocks, puzzles, teamId } = subscribedTeam;

  const resetAll = () => {
    serverInterfaceController.resetServer();
  };

  const unlockAllPuzzles = () =>
    serverInterfaceController.sendReq({
      type: WSReqType.ADMIN_MODIFY,
      teams: {
        [teamId]: {
          overrideUnlocked: Object.fromEntries(
            Object.keys(allPuzzles)
              .filter((puzName) => {
                const { isCutscene, isFullPuzzle } = allPuzzles[puzName];
                return (
                  isCutscene ||
                  isFullPuzzle ||
                  [PUZ_NAME_PVP, BATTLE_GROUP_NAME_INSTANCER].includes(puzName)
                );
              })
              .map((puzName) => [puzName, true])
          ),
        },
      },
    });

  const unlockCards = (cardNames: string[]) => {
    serverInterfaceController.sendReq({
      type: WSReqType.ADMIN_MODIFY,
      teams: {
        [teamId]: {
          overrideCardUnlocked: Object.fromEntries(
            cardNames.map((cardName) => [cardName, true])
          ),
        },
      },
    });
  };

  const unlockAllNonLegendaries = () =>
    unlockCards(
      getUnlockableCards(cardsDB).filter(
        (cardName) => !inspector.isLegendary(cardName)
      )
    );

  const unlockAllLegendaries = () =>
    unlockCards(
      getUnlockableCards(cardsDB).filter((cardName) =>
        inspector.isLegendary(cardName)
      )
    );

  const puzUnlockCount = Object.keys(battleGroups)
    .filter((puzName) => !battleGroups[puzName].isCutscene)
    .filter((puzName) => puzzles[puzName]?.unlockTime).length;
  const solveCount = Object.keys(battleGroups)
    .filter((puzName) => !battleGroups[puzName].isCutscene)
    .filter((puzName) => puzzles[puzName]?.solveTime).length;
  const cardUnlockCount = Object.keys(cardUnlocks).length;

  return (
    <>
      <div className="hunt-unlocks-buttons">
        <button onClick={resetAll}>Reset all progress</button>
        <button onClick={unlockAllPuzzles}>Unlock all puzzles</button>
        <button onClick={unlockAllNonLegendaries}>
          Unlock all non-legendaries
        </button>
        <button onClick={unlockAllLegendaries}>Unlock all legendaries</button>
      </div>
      <p>
        Puzzles unlocked: {puzUnlockCount} (of 41)
        <br />
        Puzzles solved: {solveCount} (of 39)
        <br />
        Cards unlocked: {cardUnlockCount} (of 63)
      </p>
    </>
  );
}

function UnlockPage() {
  const serverInterfaceController = useServerInterfaceController();
  const teamAdminData = useClientGlobalStateStore(
    (state) => state.globalState.teamAdminData
  );
  const { setNeedAdmin, setSubscribedTeamBattle, setSubscribedTeamFishPuzzle } =
    useGlobalSubscriptionStore();

  useEffect(() => {
    setNeedAdmin(true);
  }, [setNeedAdmin]);

  useEffect(() => {
    // Clear subscription state to prevent extraneous errors when
    // messing with unlocks.
    setSubscribedTeamBattle(null);
    setSubscribedTeamFishPuzzle(null);
  }, [setSubscribedTeamBattle, setSubscribedTeamFishPuzzle]);

  if (Object.keys(teamAdminData).length === 0) return <LargeSpinner />;

  return (
    <div className="unlock-body">
      <div className="top-more-actions" />
      <h1>Unlocking</h1>
      <main>
        <div className="note">
          <Twemoji tag="span">‚ö†Ô∏è</Twemoji> <b>Be careful!</b> There are no
          confirmations for anything you click on this page.
        </div>
        <p>
          <b>To experience the hunt like a team during the hunt would</b>, click
          ‚ÄúReset all progress‚Äù and start at the{" "}
          <RoutedLink
            path="/game/map"
            href={`${settings.djangoBaseUrl}puzzles`}
          >
            map
          </RoutedLink>
          . You won‚Äôt need to use this page again, unless you want to skip
          puzzles you‚Äôre stuck on. Progress is saved locally in your browser and
          synced across all your browser tabs. Note that there is currently no
          support for interaction between multiple postsolvers, and that all
          global leaderboards have been frozen.
        </p>
        <p>
          The default initial posthunt state has all puzzles and all
          non-legendaries unlocked to support postsolving individual puzzles
          independently. To restore this state, click ‚ÄúReset all progress‚Äù,
          ‚ÄúUnlock all puzzles‚Äù, then ‚ÄúUnlock all non-legendaries‚Äù.{" "}
          <b>Do not click ‚ÄúUnlock all legendaries‚Äù.</b>
        </p>

        <p>
          You can also manage unlocks within a battle by accessing the{" "}
          <b>Battle Unlocks</b> panel from the ‚Äúüîì‚Äù group in the navbar. This
          panel only becomes available once you have a battle open.
        </p>
        <h3>Hunt unlocks</h3>
        <UnlockInfo />
        <h3>Individual unlocks</h3>
        <Tabs.Container
          items={[
            {
              label: "Puzzles and cutscenes",
              content: <UnlockPuzzlesDisplay />,
            },
            {
              label: "Cards",
              content: <UnlockCardsDisplay />,
            },
          ]}
        >
          <Tabs.ButtonList className="unlock-button-list">
            <Tabs.Buttons />
          </Tabs.ButtonList>
          <p>
            <Twemoji tag="span">‚ö†Ô∏è</Twemoji> <b>Note:</b> Setting a card or
            puzzle to locked may appear to fail silently if your existing solves
            would cause the puzzle or card to be immediately unlocked.
          </p>
          <Tabs.Content />
        </Tabs.Container>
      </main>
    </div>
  );
}

export default UnlockPage;
