import "./PvPLobbyDisplay.css";
import {
  useClientGlobalStateStore,
  useSubscribedTeam,
  useSubscribedTeamPvP,
  useSubscribedBattleStatus,
  useSelectedDeckData,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import {
  useServerInterfaceController,
  useTeamId,
} from "stores/ServerInteractionStore";
import { usePuzName } from "stores/NavStore";
import {
  PvPLobbyTab,
  useUserPreferencesStore,
} from "stores/UserPreferencesStore";
import { WSReqType } from "game-server/ws";
import {
  GlobalUpdatesScopeType,
  GlobalStateBattleRoomStatus,
} from "game-server/global-updates";
import { PvPRequest } from "game-server/server-controllers";
import { TeamSummaryState } from "game-server/monitoring";
import { getDevKnobs } from "App.util";
import TeamDisplayNameDisplay from "components/TeamDisplayNameDisplay";
import { useSortedTeams } from "components/HuntLib/TeamListDisplay";
import Tooltip from "components/Tooltip";
import settings from "settings";

enum TargetTeamInvalidReason {
  SELF = "self",
  NO_PVP = "no_pvp",
  GAME_ACTIVE = "game_active",
  BLOCKED = "blocked",
  BLOCKED_BY = "blocked_by",
}

const validateTargetTeam = (
  teamId: string,
  { teamId: targetTeamId, hasPvP, isPvPGameActive }: TeamSummaryState,
  isBlocked: boolean,
  isBlockedBy: boolean
): TargetTeamInvalidReason | null => {
  if (teamId === targetTeamId) return TargetTeamInvalidReason.SELF;
  if (!(hasPvP ?? false)) return TargetTeamInvalidReason.NO_PVP;
  if (isPvPGameActive ?? false) return TargetTeamInvalidReason.GAME_ACTIVE;
  if (isBlocked) return TargetTeamInvalidReason.BLOCKED;
  if (isBlockedBy) return TargetTeamInvalidReason.BLOCKED_BY;
  return null;
};

interface PvPTeamDisplayProps {
  teamData: TeamSummaryState;
  incomingRequest?: PvPRequest;
  outgoingRequest?: PvPRequest;
  isBlocked: boolean;
  isBlockedBy: boolean;
  style?: CSSProperties;
}

function PvPTeamDisplay(props: PvPTeamDisplayProps) {
  const {
    teamData,
    incomingRequest: incomingRequestOpt,
    outgoingRequest: outgoingRequestOpt,
    isBlocked,
    isBlockedBy,
    style,
  } = props;
  const { teamId: targetTeamId } = teamData;
  const [timeRemaining, setTimeRemaining] = useState<number>(0);
  const [incomingRequest, setIncomingRequest] = useState<PvPRequest | null>(
    null
  );
  const [outgoingRequest, setOutgoingRequest] = useState<PvPRequest | null>(
    null
  );
  const serverInterfaceController = useServerInterfaceController();
  const teamId = useTeamId();
  const puzName = usePuzName();
  const battleStatus = useSubscribedBattleStatus(puzName);
  const isBattleActive =
    battleStatus === null
      ? null
      : battleStatus === GlobalStateBattleRoomStatus.ACTIVE;
  const invalidReason =
    teamId === null
      ? null
      : validateTargetTeam(teamId, teamData, isBlocked, isBlockedBy);
  const selectedDeckData = useSelectedDeckData(Player.P1);

  useEffect(() => {
    let activeTimeout: number | null = null;
    const getTimeToNextSecond = () => {
      const timeNowSeconds = new Date().getTime() / 1000;
      return (Math.floor(timeNowSeconds + 1) - timeNowSeconds) * 1000;
    };
    const perSecondHandler = () => {
      const timeNow = new Date().getTime();
      if (
        incomingRequestOpt !== undefined &&
        incomingRequestOpt.expiryTime > timeNow
      ) {
        setIncomingRequest(incomingRequestOpt);
        setOutgoingRequest(null);
        setTimeRemaining(incomingRequestOpt.expiryTime - timeNow);
      } else if (
        outgoingRequestOpt !== undefined &&
        outgoingRequestOpt.expiryTime > timeNow
      ) {
        setIncomingRequest(null);
        setOutgoingRequest(outgoingRequestOpt);
        setTimeRemaining(outgoingRequestOpt.expiryTime - timeNow);
      } else {
        setIncomingRequest(null);
        setOutgoingRequest(null);
        setTimeRemaining(0);
      }
      activeTimeout = window.setTimeout(
        perSecondHandler,
        getTimeToNextSecond()
      );
    };
    perSecondHandler();
    return () => {
      if (activeTimeout !== null) clearTimeout(activeTimeout);
    };
  }, [
    setOutgoingRequest,
    setIncomingRequest,
    setTimeRemaining,
    outgoingRequestOpt,
    incomingRequestOpt,
  ]);

  if (isBattleActive === null || selectedDeckData === null) return null;

  const { selectedDeckSlot, isSelectedDeckValid = false } = selectedDeckData;
  // TODO: Probably better to cover the entire lobby interface
  // if the deck is invalid, rather than hiding the reason
  // behind a tooltip.
  const deckInvalidReasonText = (() => {
    if (selectedDeckSlot === null) return "You have no deck selected.";
    if (!isSelectedDeckValid) return "Your deck is invalid.";
    return null;
  })();

  const buttonText = (() => {
    if (outgoingRequest !== null) return "Cancel";
    if (incomingRequest !== null) return "Accept";
    return "Challenge";
  })();

  const invalidReasonText = ((): string | null => {
    if (invalidReason === null) return null;
    switch (invalidReason) {
      case TargetTeamInvalidReason.SELF:
        return "Yourself";
      case TargetTeamInvalidReason.NO_PVP:
        return "PvP not enabled";
      case TargetTeamInvalidReason.GAME_ACTIVE:
        return "Battle in progress";
      case TargetTeamInvalidReason.BLOCKED:
        return "Blocked";
      case TargetTeamInvalidReason.BLOCKED_BY:
        return "Blocked by them";
    }
  })();

  const numMinutes = Math.floor(timeRemaining / 1000 / 60);
  const numSeconds = Math.floor(timeRemaining / 1000) % 60;
  return (
    <div
      className={`pvp-lobby-team${isBlocked || isBlockedBy ? " blocked" : ""}`}
      style={style}
    >
      <div className="pvp-lobby-team-label">
        <div className="pvp-lobby-team-name">
          <TeamDisplayNameDisplay
            teamData={teamData}
            invalid={invalidReason !== null}
          />
        </div>
        {invalidReasonText === null ? null : (
          <div className="pvp-lobby-invalid-reason">({invalidReasonText})</div>
        )}
        {timeRemaining === 0 ? null : (
          <div className="pvp-lobby-countdown">
            {numMinutes.toString().padStart(2, "0")}:
            {numSeconds.toString().padStart(2, "0")}
          </div>
        )}
      </div>
      <div>
        <Tooltip
          text={
            deckInvalidReasonText !== null ? deckInvalidReasonText : undefined
          }
        >
          <button
            className="pvp-lobby-btn-challenge"
            disabled={
              invalidReason !== null ||
              isBattleActive ||
              deckInvalidReasonText !== null
            }
            onClick={() => {
              if (outgoingRequest !== null) {
                serverInterfaceController.sendReq({
                  type: WSReqType.PVP_CANCEL_REQUEST,
                  targetTeamId,
                });
              } else if (incomingRequest !== null) {
                serverInterfaceController.sendReq({
                  type: WSReqType.PVP_ACCEPT_REQUEST,
                  targetTeamId,
                  devKnobs: getDevKnobs(),
                });
              } else {
                serverInterfaceController.sendReq({
                  type: WSReqType.PVP_MAKE_REQUEST,
                  targetTeamId,
                  autoAccept: settings.pvpAutoAccept ? true : undefined,
                });
              }
            }}
          >
            {buttonText}
          </button>
        </Tooltip>
      </div>
      <div>
        <button
          disabled={teamId === null || teamId === targetTeamId}
          className="pvp-lobby-btn-block"
          onClick={() => {
            serverInterfaceController.sendReq({
              type: WSReqType.PVP_SET_IS_BLOCKED,
              targetTeamId,
              isBlocked: !isBlocked,
            });
          }}
        >
          {isBlocked ? "Unblock" : "Block"}
        </button>
      </div>
    </div>
  );
}

interface PvPLobbyTabButtonProps {
  tab: PvPLobbyTab;
  refTimeNow?: number;
}

function PvPLobbyTabButton(props: PvPLobbyTabButtonProps) {
  const { tab, refTimeNow } = props;

  const pvpLobbyTab = useUserPreferencesStore((state) => state.pvpLobbyTab);
  const setPvPLobbyTab = useUserPreferencesStore(
    (state) => state.setPvPLobbyTab
  );
  const { incomingRequests = {} } = useSubscribedTeamPvP() ?? {};
  const hasIncoming =
    Object.values(incomingRequests).filter(
      ({ expiryTime }) => refTimeNow && expiryTime > refTimeNow
    ).length > 0;

  const buttonText = ((): string => {
    switch (tab) {
      case PvPLobbyTab.ALL:
        return "üëã";
      case PvPLobbyTab.OUTGOING:
        return "üì§";
      case PvPLobbyTab.INCOMING:
        return "üì•";
      case PvPLobbyTab.BLOCKED:
        return "üö´";
      case PvPLobbyTab.RING:
        return "ü§º";
    }
  })();

  const tooltipText = ((): string => {
    switch (tab) {
      case PvPLobbyTab.ALL:
        return "Show all teams.";
      case PvPLobbyTab.OUTGOING:
        return "Show teams you‚Äôve sent challenges to.";
      case PvPLobbyTab.INCOMING:
        return "Show teams who‚Äôve sent challenges to you.";
      case PvPLobbyTab.BLOCKED:
        return "Show teams you‚Äôve blocked. Blocked teams can‚Äôt send you challenges.";
      case PvPLobbyTab.RING:
        return "Show teams who are in the ring. Entering the ring sends a challenge to every team in it.";
    }
  })();

  return (
    <Tooltip text={tooltipText}>
      <button
        className={`pvp-lobby-menu-btn${
          pvpLobbyTab === tab ? " highlight" : ""
        }${
          tab === PvPLobbyTab.INCOMING && hasIncoming ? " extra-highlight" : ""
        }`}
        onClick={() => {
          setPvPLobbyTab(tab);
        }}
      >
        <Twemoji>{buttonText}</Twemoji>
      </button>
    </Tooltip>
  );
}

function PvPLobbyDisplay() {
  const serverInterfaceController = useServerInterfaceController();
  const teamId = useTeamId();

  const subscribedTeam = useSubscribedTeam();
  const subscribedTeamPvP = useSubscribedTeamPvP();
  const setNeedTeamList = useGlobalSubscriptionStore(
    (state) => state.setNeedTeamList
  );
  const teams = useClientGlobalStateStore((state) => state.globalState.teams);
  const pvp = useClientGlobalStateStore((state) => state.globalState.pvp);
  const pvpLobbyTab = useUserPreferencesStore((state) => state.pvpLobbyTab);
  const setPvPLobbyTab = useUserPreferencesStore(
    (state) => state.setPvPLobbyTab
  );
  const hideInvalid = useUserPreferencesStore((state) => state.hideInvalid);
  const setHideInvalid = useUserPreferencesStore(
    (state) => state.setHideInvalid
  );
  const [teamsList, setTeamsList] = useState<TeamSummaryState[]>([]);
  /** The reference timeNow to compute the visible teams. */
  const [refTimeNow, setRefTimeNow] = useState<number>(new Date().getTime());
  const [teamsFilter, setTeamsFilter] = useState<string>("");
  const fullTeamsList = useSortedTeams(null, false, true);

  useEffect(() => {
    setNeedTeamList(true);
  }, [setNeedTeamList]);

  useEffect(() => {
    if (teamId === null) return;
    return serverInterfaceController.addSubscribeEffect({
      type: GlobalUpdatesScopeType.TEAM_PVP,
      teamId,
    });
  }, [serverInterfaceController, teamId]);

  useEffect(() => {
    return serverInterfaceController.addSubscribeEffect({
      type: GlobalUpdatesScopeType.PVP,
    });
  }, [serverInterfaceController]);

  useEffect(() => {
    if (subscribedTeamPvP === null) return;
    if (pvp === null) return;
    if (teams === null) return;
    const { outgoingRequests, incomingRequests, blockedTeams, blockedByTeams } =
      subscribedTeamPvP;
    const { ring } = pvp;
    setTeamsList(
      ((): TeamSummaryState[] => {
        switch (pvpLobbyTab) {
          case PvPLobbyTab.ALL:
            return fullTeamsList.filter(({ isHidden }) => !isHidden);
          case PvPLobbyTab.OUTGOING:
            return fullTeamsList.filter(
              ({ teamId }) =>
                outgoingRequests[teamId] !== undefined &&
                outgoingRequests[teamId].expiryTime > refTimeNow
            );
          case PvPLobbyTab.INCOMING:
            return fullTeamsList.filter(
              ({ teamId }) =>
                incomingRequests[teamId] !== undefined &&
                incomingRequests[teamId].expiryTime > refTimeNow
            );
          case PvPLobbyTab.BLOCKED:
            return fullTeamsList.filter(
              ({ teamId }) => blockedTeams[teamId] ?? false
            );
          case PvPLobbyTab.RING:
            return ring.map((teamId) => teams[teamId]);
        }
      })().filter((teamData) => {
        if (!teamData.displayName.toLowerCase().includes(teamsFilter))
          return false;
        if (!hideInvalid) return true;
        return (
          teamId !== null &&
          validateTargetTeam(
            teamId,
            teamData,
            blockedTeams[teamData.teamId] ?? false,
            blockedByTeams[teamData.teamId] ?? false
          ) === null
        );
      })
    );
    const allRequests = [
      ...Object.values(incomingRequests),
      ...Object.values(outgoingRequests),
    ].filter(({ expiryTime }) => expiryTime > refTimeNow);
    if (allRequests.length > 0) {
      const nextUpdateTime = Math.min(
        ...allRequests.map(({ expiryTime }) => expiryTime)
      );
      const activeTimeout = window.setTimeout(() => {
        setRefTimeNow(new Date().getTime());
      }, nextUpdateTime - refTimeNow);
      return () => {
        window.clearTimeout(activeTimeout);
      };
    }
  }, [
    pvpLobbyTab,
    hideInvalid,
    teamId,
    refTimeNow,
    subscribedTeamPvP,
    pvp,
    teams,
    fullTeamsList,
    teamsFilter,
  ]);

  if (
    teamId === null ||
    pvp === null ||
    subscribedTeam === null ||
    subscribedTeamPvP === null ||
    teams === null
  )
    return <LargeSpinner inline={true} />;

  const { outgoingRequests, incomingRequests, blockedTeams, blockedByTeams } =
    subscribedTeamPvP;
  const { ring } = pvp;
  const isInRing = ring.includes(teamId);
  const pvpEnabled = subscribedTeam.masteries[PVP_MASTERY_ID].isEnabled;

  return (
    <>
      {pvpEnabled ? (
        <div className="pvp-lobby-root">
          <div className="pvp-lobby-menu">
            <div className="pvp-lobby-menu-top">
              <PvPLobbyTabButton tab={PvPLobbyTab.ALL} />
              <PvPLobbyTabButton tab={PvPLobbyTab.OUTGOING} />
              <PvPLobbyTabButton
                tab={PvPLobbyTab.INCOMING}
                refTimeNow={refTimeNow}
              />
              <PvPLobbyTabButton tab={PvPLobbyTab.BLOCKED} />
              <PvPLobbyTabButton tab={PvPLobbyTab.RING} />
              <Tooltip text="Hide teams you can't challenge.">
                <label className="pvp-hide-invalid">
                  <input
                    type="checkbox"
                    checked={hideInvalid}
                    onChange={() => {
                      setHideInvalid(!hideInvalid);
                    }}
                  />{" "}
                  Hide invalid
                </label>
              </Tooltip>
            </div>
            <div className="pvp-lobby-menu-bottom">
              <Tooltip
                text={
                  isInRing
                    ? "Resend a challenge to everyone in the ring."
                    : "Enter the ring, and send a challenge to everyone in it."
                }
              >
                <button
                  className="pvp-lobby-menu-btn"
                  onClick={() => {
                    serverInterfaceController.sendReq({
                      type: WSReqType.PVP_ENTER_RING,
                    });
                    setPvPLobbyTab(PvPLobbyTab.RING);
                  }}
                >
                  <Twemoji>{isInRing ? "‚è©ü§º" : "‚ñ∂Ô∏èü§º"}</Twemoji>
                </button>
              </Tooltip>
              <Tooltip text="Leave the ring. You'll stop getting challenged by teams entering the ring.">
                <button
                  className="pvp-lobby-menu-btn"
                  disabled={!isInRing}
                  onClick={() => {
                    serverInterfaceController.sendReq({
                      type: WSReqType.PVP_LEAVE_RING,
                    });
                  }}
                >
                  <Twemoji>‚èπÔ∏èü§º</Twemoji>
                </button>
              </Tooltip>
            </div>
          </div>
          <FixedSizeList
            height={500}
            itemCount={teamsList.length}
            itemSize={38}
            width="100%"
          >
            {({ index, style }) => {
              const teamData = teamsList[index];
              const { teamId } = teamData;
              const isBlocked = blockedTeams[teamId] ?? false;
              const isBlockedBy = blockedByTeams[teamId] ?? false;
              return (
                <PvPTeamDisplay
                  key={teamId}
                  teamData={teamData}
                  outgoingRequest={outgoingRequests[teamId]}
                  incomingRequest={incomingRequests[teamId]}
                  isBlocked={isBlocked}
                  isBlockedBy={isBlockedBy}
                  style={{
                    ...style,
                    background:
                      index % 2 === 0
                        ? isBlocked || isBlockedBy
                          ? "#644"
                          : "#444"
                        : isBlocked || isBlockedBy
                        ? "#422"
                        : "#222",
                  }}
                />
              );
            }}
          </FixedSizeList>
          <div className="pvp-team-search">
            <Twemoji>üîç</Twemoji>{" "}
            <input
              placeholder="Search for a team..."
              onChange={(e) =>
                setTeamsFilter(e.target.value.toLowerCase().trim())
              }
            />
          </div>
        </div>
      ) : (
        <div className="pvp-disabled">
          <h4>PvP is disabled!</h4>
          Your team will be unable to participate until you activate it (but you
          can continue a match if you already have one going).
        </div>
      )}
    </>
  );
}

export default PvPLobbyDisplay;
