import rockLobsterUrl from "audio/rock-lobster.mp3";
import sfxMetadata from "audio/sfx-metadata.json";
import sfxUrl from "audio/sfx.mp3";
import { PUZ_NAME_FINAL_BATTLE } from "engine/puzzles/puzzle-data";
import { UpdateType } from "engine/types/updates";
import { HuntNotificationType } from "game-server/notifications";
import { Howl, Howler } from "howler";
import { useEffect, useRef } from "react";
import { useAnimationStore } from "stores/AnimationStore";
import {
  getPlayerPerspectiveForRole,
  useClientGameStore,
} from "stores/ClientGameStore";
import {
  useClientGlobalStateStore,
  useRoomBattleGroupNameFromGameState,
} from "stores/ClientGlobalStateStore";
import {
  AudioLayer,
  useAudioPreferencesStore,
} from "stores/UserPreferencesStore";

// hacky way to make an enum
type SFX = Uppercase<keyof (typeof sfxMetadata)["sprite"]>;
const SFX = Object.fromEntries(
  Object.keys(sfxMetadata["sprite"]).map((k) => [k.toUpperCase(), k])
) as { [key in SFX]: string };

export const useAudioEffect = () => {
  const roomBattleGroupName = useRoomBattleGroupNameFromGameState();
  const volume = useAudioPreferencesStore((state) => state.volume);
  const isMuted = useAudioPreferencesStore((state) => state.isMuted);
  const layers = useAudioPreferencesStore((state) => state.layers);
  const register = useAnimationStore((state) => state.register);
  const setPlayAudioNotif = useClientGlobalStateStore(
    (state) => state.setPlayAudioNotif
  );

  const players = useRef<Record<AudioLayer, Howl | null>>({
    [AudioLayer.BGM]: null,
    [AudioLayer.SFX]: null,
    [AudioLayer.NOTIF]: null,
  });

  useEffect(() => {
    Howler.volume(volume / 100);
  }, [volume]);

  useEffect(() => {
    Howler.mute(isMuted);
  }, [isMuted]);

  useEffect(() => {
    Object.values(AudioLayer).forEach((key) => {
      players.current[key]?.volume(layers[key].volume / 100);
      players.current[key]?.mute(layers[key].isMuted);
    });
  }, [layers]);

  // bgm layer
  useEffect(() => {
    if (roomBattleGroupName === null) return;
    if (roomBattleGroupName !== PUZ_NAME_FINAL_BATTLE) return;

    players.current.bgm = new Howl({
      src: [rockLobsterUrl],
      html5: true,
      loop: true,
      volume: layers.bgm.volume / 100,
      mute: layers.bgm.isMuted,
    });
    const rockLobster = players.current.bgm;

    const playAudio = () => {
      rockLobster.play();
      document.body.removeEventListener("click", playAudio);
    };

    document.body.addEventListener("click", playAudio);
    return () => {
      document.body.removeEventListener("click", playAudio);
      rockLobster.unload();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [roomBattleGroupName]);

  // step sfx layer (won't overlap, will interrupt previous)
  useEffect(() => {
    players.current.sfx = new Howl({
      src: [sfxUrl],
      // @ts-expect-error: types aren't quite inferred right
      sprite: sfxMetadata.sprite,
      volume: layers.sfx.volume / 100,
      mute: layers.sfx.isMuted,
    });
    const sfx = players.current.sfx;
    const playerUs = getPlayerPerspectiveForRole(
      useClientGameStore.getState().role
    );

    const unregister = register(async (upd) => {
      switch (upd.type) {
        case UpdateType.END_TURN: {
          if (upd.player !== playerUs) {
            sfx.play(SFX["YOUR-TURN"]);
          }
          break;
        }
        case UpdateType.CREATE_PERMANENT: {
          sfx.play(SFX["STEP-SUMMON"]);
          break;
        }
        case UpdateType.ADD_GEMS: {
          // bad for starting turn
          sfx.play(SFX["STEP-ADD-GEMS"]);
          break;
        }
        case UpdateType.REMOVE_PERMANENT: {
          sfx.play(SFX["STEP-REMOVE-PERMANENT"]);
          break;
        }
        case UpdateType.MOVE:
        case UpdateType.SWAP: {
          sfx.play(SFX["STEP-MOVE"]);
          break;
        }
        case UpdateType.DAMAGE_PERMANENT: {
          if (upd.damage > 0) {
            sfx.play(SFX["STEP-DAMAGE-PERMANENT"]);
          } else if (upd.damage < 0) {
            sfx.play(SFX["STEP-HEAL"]);
          }
          break;
        }
      }
    });

    return () => {
      unregister();
      sfx.unload();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [register]);

  // notif layer
  useEffect(() => {
    players.current.notif = new Howl({
      src: [sfxUrl],
      // @ts-expect-error: types aren't quite inferred right
      sprite: sfxMetadata.sprite,
      volume: layers.notif.volume / 100,
      mute: layers.notif.isMuted,
    });
    const notif = players.current.notif;

    setPlayAudioNotif((upd) => {
      if (
        upd.type !== HuntNotificationType.VICTORY &&
        upd.type !== HuntNotificationType.UNLOCK // unlock overlaps solve
      ) {
        notif.stop(); // stop any previous sound
      }
      switch (upd.type) {
        case HuntNotificationType.PVP_REQUEST_ACCEPTED: {
          notif.play(SFX["NOTIF-TRUMPETS-2"]);
          break;
        }
        case HuntNotificationType.PVP_REQUEST_RECEIVED: {
          notif.play(SFX["NOTIF-TRUMPETS-1"]);
          break;
        }
        case HuntNotificationType.HINT_ANSWERED: {
          notif.play(SFX["NOTIF-BELLS"]);
          break;
        }
        case HuntNotificationType.ERRATUM: {
          notif.play(SFX["NOTIF-BELLS"]);
          break;
        }
        case HuntNotificationType.UNLOCK: {
          notif.play(SFX["NOTIF-CAVE"]);
          break;
        }
        case HuntNotificationType.SOLVE: {
          notif.play(SFX["NOTIF-CLAPS"]);
          break;
        }
        case HuntNotificationType.VICTORY: {
          notif.play(SFX.CHEER);
          break;
        }
      }
    });

    return () => {
      setPlayAudioNotif(null);
      notif.unload();
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
};
