import { useEffect } from "react";
import { useParams, useNavigate, useSearchParams } from "react-router-dom";
import "./GamePage.css";

import {
  useBattleGroup,
  useSubscribedTeam,
  useSubscribedBattleStatus,
} from "stores/ClientGlobalStateStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { GlobalStateBattleRoomStatus } from "game-server/global-updates";

import { getDevKnobs } from "App.util";
import { Spinner } from "components/Loading";

function StartBattlePage({ restart }: { restart?: boolean }) {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const serverInterfaceController = useServerInterfaceController();
  const { puzName, slot } = useParams<{
    puzName: string;
    slot: string;
  }>();
  if (puzName === undefined)
    throw new Error("expect puzName parameter to be defined");
  const battleStatus = useSubscribedBattleStatus(puzName);
  const isBattleActive =
    battleStatus === null
      ? null
      : battleStatus === GlobalStateBattleRoomStatus.ACTIVE;
  const subscribedTeam = useSubscribedTeam();
  const battleGroup = useBattleGroup();
  const isUnlocked =
    subscribedTeam !== null && subscribedTeam.puzzles[puzName] !== undefined;

  useEffect(() => {
    // If the puzzle is not unlocked, wait until it is.
    if (!isUnlocked) return;

    return serverInterfaceController.addAuthEffect(() => {
      serverInterfaceController.startAIBattleAsync(puzName, {
        slot: slot === undefined ? undefined : parseInt(slot),
        restart,
        devKnobs: getDevKnobs(),
      });
    });
  }, [
    serverInterfaceController,
    puzName,
    isUnlocked,
    navigate,
    searchParams,
    slot,
    restart,
  ]);

  useEffect(() => {
    if (isBattleActive === null) return;
    if (!isBattleActive) return;
    if (battleGroup === null) return;
    const { isCutscene = false } = battleGroup;
    navigate(
      {
        pathname: `../${isCutscene ? "cutscene" : "battle"}/${puzName}`,
        search: searchParams.toString(),
      },
      { replace: true }
    );
  }, [isBattleActive, navigate, puzName, searchParams, battleGroup]);

  useEffect(() => {
    return serverInterfaceController.onErrorEvent.addListener((resp) => {
      navigate(
        {
          pathname: `../prep/${puzName}`,
          search: searchParams.toString(),
        },
        { replace: true }
      );
    });
  }, [serverInterfaceController, navigate, puzName, searchParams]);

  return <Spinner size={128} />;
}

export default StartBattlePage;
