import { PuzzleLink } from "components/Link";
import { ConfirmModal } from "components/Modal";
import Tooltip from "components/Tooltip";
import ZoomedCardDisplay from "components/ZoomedCardDisplay";
import { DeckList } from "components/PrepDisplay/DeckSelection";
import { PUZ_NAME_PVP } from "engine/puzzles/puzzle-data";
import { Check, FailedChecks } from "engine/types/action-validation";
import { Player } from "engine/types/game-state";
import { GlobalStateBattleRoomStatus } from "game-server/global-updates";
import { WSReqType } from "game-server/ws";
import { useEffect, useState } from "react";
import { Link, useNavigate, useSearchParams } from "react-router-dom";
import Twemoji from "react-twemoji";
import { useStaticInspector } from "stores/ClientGameStore";
import {
  useSubscribedBattleStatus,
  useSubscribedDeck,
  useSubscribedTeam,
  useSelectedDeckData,
} from "stores/ClientGlobalStateStore";
import { useDeckbuildingStore } from "stores/DeckbuildingStore";
import { useIsAdminEnabledStore } from "stores/UserPreferencesStore";
import { usePuzName, useSlot } from "stores/NavStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useZoomedCard } from "stores/ZoomCardStore";
import "./DeckSidebar.css";

function CardSource() {
  const card = useZoomedCard();
  const subscribedTeam = useSubscribedTeam();
  const empty = <div>&nbsp;</div>;
  if (!card || !subscribedTeam) return empty;
  const { cardName } = card;
  const { battleGroups, cardUnlocks, puzzles } = subscribedTeam;
  const puzName = cardUnlocks[cardName]?.puzName;
  if (!puzName) return empty;
  // Special case to ignore card unlock attributions if the
  // puzzle is locked or unsolved.
  if (puzzles[puzName]?.solveTime === undefined) return empty;
  const battleGroupName = puzzles[puzName]?.battleGroupName;
  if (!battleGroupName) return empty;
  const displayName = battleGroups[battleGroupName]?.displayName;
  if (!displayName) return empty;
  return (
    <div className="card-source">
      from{" "}
      <PuzzleLink
        battleGroupName={battleGroupName}
        hasAnswer={puzzles[puzName].hasAnswer ?? false}
      >
        {displayName}
      </PuzzleLink>
    </div>
  );
}

function DeckSidebar() {
  const { displayNameInput, setDisplayNameInput } = useDeckbuildingStore();
  const navigate = useNavigate();
  const slot = useSlot();
  const deck = useSubscribedDeck(slot);
  const serverInterfaceController = useServerInterfaceController();
  const [searchParams] = useSearchParams();
  const inspector = useStaticInspector();
  const puzName = usePuzName();
  const isPvP = puzName === PUZ_NAME_PVP;
  const battleStatus = useSubscribedBattleStatus(puzName);
  const [showEndConfirm, setShowEndConfirm] = useState(false);
  const isBattleActive =
    battleStatus === null
      ? false
      : battleStatus === GlobalStateBattleRoomStatus.ACTIVE;
  const { selectedDeckSlot = null } = useSelectedDeckData(Player.P1);
  const isSpectating = useIsAdminEnabledStore((state) => state.isSpectating);

  const displayName = deck === null ? null : deck.displayName;

  const onStart = () => {
    navigate({
      pathname: `../battle/${puzName}/start/${slot}`,
      search: searchParams.toString(),
    });
  };

  useEffect(() => {
    if (displayName === null) return;
    // Keep the display name input in sync if the display name
    // changes due to external sources.
    setDisplayNameInput(displayName);
  }, [displayName, setDisplayNameInput]);

  if (deck === null) return null;

  const failedChecks: FailedChecks = new Set();
  inspector.validateDeck(failedChecks, Player.P1, deck);
  const failedChecksStr = [
    Check.DECK_TOO_SMALL,
    Check.DECK_TOO_LARGE,
    Check.DECK_TOO_MANY_LEGENDARIES,
  ]
    .filter((failedCheck) => failedChecks.has(failedCheck))
    .map((failedCheck) => {
      switch (failedCheck) {
        case Check.DECK_TOO_SMALL:
          return "Too few cards.";
        case Check.DECK_TOO_LARGE:
          return "Too many cards.";
        case Check.DECK_TOO_MANY_LEGENDARIES:
          return "Too many Legendaries.";
      }
      throw new Error("unrecognized deckbuilding failed check");
    })
    .join("\n");

  return (
    <div className="deck-sidebar">
      <ZoomedCardDisplay />
      <CardSource />
      <div className="deck-sidebar-buttons">
        {displayNameInput === null ? null : (
          <input
            className="deck-sidebar-input"
            type="text"
            value={displayNameInput}
            onChange={(e) => {
              setDisplayNameInput(e.target.value);
            }}
            onBlur={() => {
              if (displayNameInput !== deck.displayName) {
                serverInterfaceController.sendReq({
                  type: WSReqType.DECKBUILDING_STEP,
                  slot,
                  deckUpd: {
                    displayName: displayNameInput,
                  },
                });
              }
            }}
          />
        )}
        <Link
          to={{
            pathname: `../prep/${puzName}`,
            search: searchParams.toString(),
          }}
        >
          <button>Back</button>
        </Link>
        {isBattleActive ? (
          <>
            <div className="battle-status">
              <Twemoji>‚ö†Ô∏è Battle is ongoing!</Twemoji>
            </div>
            <Link
              to={{
                pathname: `../battle/${puzName}`,
                search: searchParams.toString(),
              }}
            >
              <button className="battle-button">Join battle</button>
            </Link>
            {isPvP || isSpectating ? null : (
              <Tooltip text={failedChecksStr}>
                <button
                  onClick={() => setShowEndConfirm(true)}
                  disabled={failedChecks.size > 0}
                >
                  Restart with this deck
                </button>
              </Tooltip>
            )}
          </>
        ) : isPvP || isSpectating ? null : (
          <Tooltip text={failedChecksStr}>
            <button
              className="battle-button"
              onClick={() => onStart()}
              disabled={failedChecks.size > 0}
            >
              Start battle!
            </button>
          </Tooltip>
        )}
      </div>
      <DeckList
        player={Player.P1}
        disabledExcept={null}
        highlightedDeckSlot={slot}
        secondaryHighlightedDeckSlot={selectedDeckSlot}
        lockedDeckSlot={null}
        context="collection"
      />
      <ConfirmModal
        title="Are you sure?"
        isOpen={showEndConfirm}
        onClose={() => setShowEndConfirm(false)}
        onConfirm={() => {
          navigate({
            pathname: `../battle/${puzName}/restart/${slot}`,
            search: searchParams.toString(),
          });
        }}
        confirmLabel="Restart"
      >
        This will end the current battle.
      </ConfirmModal>
    </div>
  );
}

export default DeckSidebar;
