import { useEffect } from "react";
import { useParams, useNavigate, useSearchParams } from "react-router-dom";
import "./GamePage.css";

import { PUZ_NAME_MASTERY_TREE } from "engine/puzzles/puzzle-data";

import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { Spinner } from "components/Loading";

/**
 * Landing page after cutscene to wait for the puzzle to get unlocked
 * and then jump into prep.
 */
function StartPrepPage() {
  const [searchParams, setSearchParams] = useSearchParams();
  const navigate = useNavigate();
  const serverInterfaceController = useServerInterfaceController();
  const { puzName } = useParams<{
    puzName: string;
  }>();
  if (puzName === undefined)
    throw new Error("expect puzName parameter to be defined");
  const subscribedTeam = useSubscribedTeam();
  const isMasteryTree = puzName === PUZ_NAME_MASTERY_TREE;

  useEffect(() => {
    if (subscribedTeam === null) return;
    const puzData = subscribedTeam.puzzles[puzName];
    // If the puzzle is not unlocked, wait until it is.
    if (puzData === undefined) return;

    navigate(
      {
        pathname: isMasteryTree ? "../mastery" : `../prep/${puzName}`,
        search: searchParams.toString(),
      },
      { replace: true }
    );
  }, [puzName, subscribedTeam, navigate, searchParams, isMasteryTree]);

  useEffect(() => {
    return serverInterfaceController.onErrorEvent.addListener((resp) => {
      navigate(
        {
          pathname: "../",
          search: searchParams.toString(),
        },
        { replace: true }
      );
    });
  }, [serverInterfaceController, navigate, searchParams]);

  return <Spinner size={128} />;
}

export default StartPrepPage;
