import { Player } from "engine/types/game-state";
import { Role } from "engine/types/updates";
import { WSReqType } from "game-server/ws";
import { useCallback, useEffect, useState } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import { useCardsDBStore } from "stores/CardsDBStore";
import { useStaticInspector } from "stores/ClientGameStore";
import {
  useClientGlobalStateStore,
  useSelectedPuzName,
  useBattleGroup,
  useIsInstancer,
  useSubscribedTeam,
  usePuzzlesInBattleGroup,
  useIsValidBattle,
} from "stores/ClientGlobalStateStore";
import { useGlobalSubscriptionStore } from "stores/GlobalSubscriptionStore";
import { useNavStore, usePuzName } from "stores/NavStore";
import {
  useServerInteractionStore,
  useTeamId,
} from "stores/ServerInteractionStore";
import {
  useUserPreferencesStore,
  useIsAdminEnabledStore,
} from "stores/UserPreferencesStore";
import "./PrepPage.css";

import { getInitAutoPrepOpts } from "App.util";
import BattleBackground from "components/BattleBackground";
import PrepDisplay from "components/PrepDisplay";
import { InstancerNotice, PvPNotice } from "components/PrepDisplay/PrepDisplay";
import PrepRight from "components/PrepDisplay/PrepRight";
import Modal from "components/Modal";
import { LargeSpinner } from "components/Loading";
import { getUnlockableCards } from "engine/types/card-data";
import {
  PUZ_NAME_PVP,
  BATTLE_GROUP_NAME_INSTANCER,
  PUZ_NAME_FINAL_BATTLE,
  NUM_LEGENDARIES,
} from "engine/puzzles/puzzle-data";
import { toastr } from "Globals";
import settings from "settings";

function PrepPage() {
  const [isUnmounted, setIsUnmounted] = useState(false);
  // Don't use useRole here as that only gets updated in game once
  // the role is received from the server.
  const role = settings.role ?? Role.P1;
  const player = role === Role.P2 ? Player.P2 : Player.P1;

  const setSubscribedTeamBattle = useGlobalSubscriptionStore(
    (state) => state.setSubscribedTeamBattle
  );
  const subscribedTeamBattle = useClientGlobalStateStore(
    (state) => state.globalState.subscribedTeamBattle
  );
  const serverInterfaceController = useServerInteractionStore(
    (state) => state.getServerInterfaceController
  )();
  const teamId = useTeamId();
  const puzName = usePuzName();
  const isPvP = puzName === PUZ_NAME_PVP;
  const selectedPuzName = useSelectedPuzName();
  const setHoveredMasteryId = useNavStore((state) => state.setHoveredMasteryId);
  const setSelectedMasteryId = useNavStore(
    (state) => state.setSelectedMasteryId
  );
  const cardsDB = useCardsDBStore((state) => state.cardsDB);

  const subscribedTeam = useSubscribedTeam();
  const setSubscribedTeamDecksTeamId = useGlobalSubscriptionStore(
    (state) => state.setSubscribedTeamDecksTeamId
  );
  const setNeedAdmin = useGlobalSubscriptionStore(
    (state) => state.setNeedAdmin
  );

  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const inspector = useStaticInspector();
  const battleGroup = useBattleGroup();
  const isInstancer = useIsInstancer() ?? false;
  const isInstancerModalOpen = useNavStore(
    (state) => state.isInstancerModalOpen
  );
  const setIsInstancerModalOpen = useNavStore(
    (state) => state.setIsInstancerModalOpen
  );
  const hasClosedInstancerModal = useUserPreferencesStore(
    (state) => state.hasClosedInstancerModal
  );
  const setHasClosedInstancerModal = useUserPreferencesStore(
    (state) => state.setHasClosedInstancerModal
  );
  const lastInstancerRoom = useUserPreferencesStore(
    (state) => state.lastInstancerRoom
  );
  const setLastInstancerRoom = useUserPreferencesStore(
    (state) => state.setLastInstancerRoom
  );
  const isPvPModalOpen = useNavStore((state) => state.isPvPModalOpen);
  const setIsPvPModalOpen = useNavStore((state) => state.setIsPvPModalOpen);
  const hasClosedPvPModal = useUserPreferencesStore(
    (state) => state.hasClosedPvPModal
  );
  const setHasClosedPvPModal = useUserPreferencesStore(
    (state) => state.setHasClosedPvPModal
  );
  const isSpectating = useIsAdminEnabledStore((state) => state.isSpectating);

  // Only used in posthunt, where we have access to all puzzles.
  // Detect if we are in an sub-battle that is not unlocked,
  // and if so, redirect to the origin battle group, if unlocked.
  const allPuzzles = useClientGlobalStateStore(
    (state) => state.globalState.puzzles
  );
  const battleGroupName = allPuzzles[puzName]?.battleGroupName ?? null;
  const puzzlesInBattleGroup = usePuzzlesInBattleGroup();
  const isDummy =
    battleGroup === null
      ? null
      : (battleGroup.numPuzzles ?? 1) > 1 && puzName === battleGroupName;
  const isStageLocked = !settings.isPosthunt
    ? false
    : subscribedTeam === null ||
      subscribedTeamBattle === null ||
      Object.keys(allPuzzles).length === 0
    ? null
    : selectedPuzName !== null &&
      allPuzzles[selectedPuzName] !== undefined &&
      subscribedTeam.puzzles[selectedPuzName] === undefined &&
      subscribedTeam.puzzles[allPuzzles[selectedPuzName].battleGroupName] !==
        undefined;

  const isUnknownBattle =
    Object.keys(allPuzzles).length !== 0 &&
    subscribedTeam !== null &&
    (battleGroupName === null ||
      subscribedTeam.puzzles[battleGroupName] === undefined ||
      subscribedTeam.puzzles[battleGroupName].hasAnswer);
  const isValidBattle = useIsValidBattle();

  useEffect(() => {
    if (!settings.isPosthunt) return;
    setNeedAdmin(true);
  }, [setNeedAdmin]);

  useEffect(() => {
    if (!hasClosedInstancerModal) setIsInstancerModalOpen(isInstancer);
    return () => {
      setIsInstancerModalOpen(false);
    };
  }, [isInstancer, hasClosedInstancerModal, setIsInstancerModalOpen]);

  useEffect(() => {
    if (!hasClosedPvPModal) setIsPvPModalOpen(isPvP);
    return () => {
      setIsPvPModalOpen(false);
    };
  }, [hasClosedPvPModal, setIsPvPModalOpen, isPvP]);

  useEffect(() => {
    if (!isInstancer) return;
    if (puzName === null) return;
    if (puzName === BATTLE_GROUP_NAME_INSTANCER) return;
    setLastInstancerRoom(puzName);
  }, [isInstancer, puzName, setLastInstancerRoom]);

  // For dummy puzzles, direct the user to the first unsolved battle in
  // the group, otherwise the last solved puzzle.
  const redirectPuzName = ((): string | null => {
    if (subscribedTeam === null || isUnknownBattle) return null;

    const { puzzles } = subscribedTeam;

    if (isStageLocked !== null && isStageLocked) {
      if (selectedPuzName === null)
        throw new Error(
          "selectedPuzName should be non-null if isStageLocked is set"
        );
      return allPuzzles[selectedPuzName].battleGroupName;
    }

    if (isDummy === null || !isDummy || puzzlesInBattleGroup === null)
      return null;

    if (puzzlesInBattleGroup.length <= 0)
      throw new Error("expect puzzles in battle group if battle is known");
    if (
      battleGroupName !== null &&
      battleGroupName === BATTLE_GROUP_NAME_INSTANCER &&
      lastInstancerRoom !== null
    )
      return lastInstancerRoom;

    const unsolvedPuzzles = puzzlesInBattleGroup.filter(
      ({ puzName: otherPuzName }) =>
        puzzles[otherPuzName].solveTime === undefined
    );
    return (
      unsolvedPuzzles.length > 0 ? unsolvedPuzzles[0] : puzzlesInBattleGroup[0]
    ).puzName;
  })();

  const navigateToBattlePage = useCallback(() => {
    navigate({
      pathname: `../battle/${puzName}`,
      search: searchParams.toString(),
    });
  }, [puzName, searchParams, navigate]);

  useEffect(() => {
    if (isUnknownBattle) {
      toastr.error(`Invalid battle "${puzName}".`);
      navigate(
        {
          pathname: `../map`,
          search: searchParams.toString(),
        },
        { replace: true }
      );
    }
    if (redirectPuzName !== null) {
      navigate(
        {
          pathname: `../prep/${redirectPuzName}`,
          search: searchParams.toString(),
        },
        { replace: true }
      );
      return;
    }
  }, [isUnknownBattle, navigate, puzName, redirectPuzName, searchParams]);

  useEffect(() => {
    if (serverInterfaceController === null) return;
    if (teamId === null) return;
    if (isValidBattle === null || !isValidBattle) return;
    return serverInterfaceController.addAuthEffect(() => {
      let isCanceled = false;
      (async () => {
        const autoPrepOpts = getInitAutoPrepOpts(teamId, {
          puzName,
          role,
        });
        const res = await serverInterfaceController.autoPrepAsync(autoPrepOpts);
        if (isUnmounted || !res || isCanceled) return;
        if (autoPrepOpts.readyForBattlePuzName !== undefined)
          navigateToBattlePage();
      })();
      return () => {
        isCanceled = true;
      };
    });
  }, [
    serverInterfaceController,
    teamId,
    puzName,
    role,
    isUnmounted,
    navigateToBattlePage,
    isValidBattle,
  ]);

  useEffect(() => {
    if (teamId === null) return;
    setSubscribedTeamDecksTeamId(teamId);
  }, [teamId, setSubscribedTeamDecksTeamId]);

  useEffect(() => {
    if (serverInterfaceController === null) return;
    if (isValidBattle === null || !isValidBattle) return;
    if (isSpectating) return;
    if (selectedPuzName === null) return;
    return serverInterfaceController.addAuthEffect(() => {
      serverInterfaceController.sendReq({
        type: WSReqType.VIEW_PUZZLE,
        puzName: selectedPuzName,
      });
    });
  }, [selectedPuzName, isValidBattle, serverInterfaceController, isSpectating]);

  useEffect(() => {
    if (teamId === null) return;
    if (puzName === null) return;
    if (isValidBattle === null || !isValidBattle) return;
    setSubscribedTeamBattle({
      teamId,
      puzName,
    });
  }, [setSubscribedTeamBattle, teamId, puzName, isValidBattle]);

  useEffect(() => {
    setIsUnmounted(false);
    return () => setIsUnmounted(true);
  }, []);

  if (settings.isPublicAccess) {
    const { djangoBaseUrl } = settings;
    return (
      <div className="below-nav public-access-not-supported">
        <h2>{battleGroup?.displayName}</h2>
        <div className="public-access-not-supported-message">
          Battles and cutscenes are not yet supported in public access. You may
          still access solutions and stats pages. Please create or use an
          existing account to gain access to puzzle content.
        </div>
        {!(battleGroup?.isCutscene ?? false) && !isPvP && !isInstancer ? (
          <>
            <div className="public-access-not-supported-buttons">
              <a href={`${djangoBaseUrl}solution/${battleGroupName}`}>
                <button>Solution</button>
              </a>
              <a href={`${djangoBaseUrl}stats/${battleGroupName}`}>
                <button>Stats</button>
              </a>
            </div>
          </>
        ) : (
          <></>
        )}
      </div>
    );
  }

  if (settings.isPosthunt && isPvP) {
    return (
      <div className="below-nav public-access-not-supported">
        <h2>{battleGroup?.displayName}</h2>
        <div className="public-access-not-supported-message">
          Unfortunately, the PvP arena is not supported at this time. The PvP
          arena does not have any impact on hunt progress, and is not required
          to solve any puzzles.
        </div>
      </div>
    );
  }

  if (
    serverInterfaceController === null ||
    subscribedTeam === null ||
    subscribedTeamBattle === null ||
    battleGroup === null ||
    !isValidBattle ||
    teamId === null
  )
    return <LargeSpinner />;
  const { puzzles, cardUnlocks } = subscribedTeam;
  // Show the interface in the transition period when we're switching
  // between battles in the same battle group, but not when we're
  // entering a new battle group.
  if (
    puzzles[subscribedTeamBattle.puzName]?.battleGroupName !==
    battleGroup.battleGroupName
  )
    return <LargeSpinner />;

  return (
    <>
      <BattleBackground />
      <div className="below-nav">
        <div
          className={`prep-root${isPvP ? " prep-root-pvp" : ""}${
            isInstancerModalOpen ? " unfocused" : ""
          }`}
          onClickCapture={(e) => {
            if (e.target instanceof HTMLElement) {
              const parent = e.target.parentElement;
              const grandparent = parent?.parentElement;
              if (
                grandparent &&
                grandparent.classList.contains("select-mastery-button") &&
                grandparent.classList.contains("selected")
              ) {
                setSelectedMasteryId(null);
                setHoveredMasteryId(null);
                e.stopPropagation();
                return;
              }
              if (parent && parent.classList.contains("mastery-details")) {
                return;
              }
              setSelectedMasteryId(null);
            }
          }}
        >
          <div className="prep-left-wrapper">
            <PrepDisplay role={role} player={player} />
          </div>
          <div className="prep-right-wrapper">
            {selectedPuzName === PUZ_NAME_FINAL_BATTLE &&
            Object.keys(cardUnlocks).filter((cardName) =>
              inspector.isLegendary(cardName)
            ).length < NUM_LEGENDARIES ? (
              <div className="prep-final-battle-notice">
                <div>
                  During the hunt, this battle would not unlock until all
                  legendaries have been unlocked. As a postsolver, you may use
                  the button below to unlock all legendaries and proceed with
                  this battle.
                </div>
                <div>
                  We recommend watching the ‚ÄúEntering the Water‚Äù cutscene before
                  attempting this battle.
                </div>
                <button
                  onClick={() => {
                    serverInterfaceController.sendReq({
                      type: WSReqType.ADMIN_MODIFY,
                      teams: {
                        [teamId]: {
                          overrideCardUnlocked: Object.fromEntries(
                            getUnlockableCards(cardsDB)
                              .filter((cardName) =>
                                inspector.isLegendary(cardName)
                              )
                              .map((cardName) => [cardName, true])
                          ),
                        },
                      },
                    });
                  }}
                >
                  Unlock all legendaries
                </button>
              </div>
            ) : (
              <PrepRight role={role} player={player} />
            )}
          </div>
        </div>
      </div>
      <Modal title="The Instancer" isOpen={isInstancerModalOpen}>
        <div className="prep-instancer-modal-notice">
          <InstancerNotice />
          <div>
            <button
              onClick={() => {
                setIsInstancerModalOpen(false);
                setHasClosedInstancerModal();
              }}
            >
              Okay!
            </button>
          </div>
        </div>
      </Modal>
      <Modal title="PvP Arena" isOpen={isPvPModalOpen}>
        <div className="prep-pvp-modal-notice">
          <PvPNotice />
          <div>
            <button
              onClick={() => {
                setIsPvPModalOpen(false);
                setHasClosedPvPModal();
              }}
            >
              Okay!
            </button>
          </div>
        </div>
      </Modal>
    </>
  );
}

export default PrepPage;
