import { useMemo } from "react";

import StepButton from "components/StepButton";
import { Player } from "engine/types/game-state";
import { cssIdFor, UIElement } from "engine/types/keyframes";
import { canRoleControlPlayer } from "engine/types/updates";
import { useCardsDBStore } from "stores/CardsDBStore";
import { useInspector, useRole, useStepMaker } from "stores/ClientGameStore";
import DeckInfo from "./DeckInfo";
import HandCardsDisplay from "./HandCardsDisplay";
import "./HandDisplay.css";
import { useKeyboardCursorStore } from "stores/KeyboardCursorStore";

const SHORTCUT_END_TURN = "E";
interface HandProps {
  player: Player;
}

function HandDisplay(props: HandProps) {
  const { player } = props;
  const inspector = useInspector();
  const stepMaker = useStepMaker();
  const playerState = inspector.gameState[player];
  const isAllCardsAvailable = inspector.isAllCardsAvailable();
  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const hotkeysEnabled = useKeyboardCursorStore((state) => state.enabled);
  const allCardsHand = useMemo(() => {
    const allCardNames = Object.keys(cardsDB);
    allCardNames.sort();
    return allCardNames.map((name, i) => ({
      id: `${i}`,
      card: { name },
    }));
  }, [cardsDB]);
  const role = useRole();
  const cards = isAllCardsAvailable ? allCardsHand : playerState.hand;

  const endTurnStep = stepMaker.endTurn(player);
  const isSmall = isAllCardsAvailable;
  const disable = !canRoleControlPlayer(role, player);

  return (
    <div
      className={`game-cards-hand ${isSmall ? "small-cards" : ""} ${
        disable ? "disable" : ""
      }`}
    >
      <DeckInfo player={player} />
      <HandCardsDisplay
        player={player}
        isAllCardsAvailable={isAllCardsAvailable}
        cards={cards}
        isSmall={isSmall}
        isTurnPending={inspector.isTurnPending(player)}
      />
      <div className="end-turn-button">
        <StepButton
          key={inspector.gameState.turnNumber}
          shortcut={hotkeysEnabled ? SHORTCUT_END_TURN : undefined}
          id={cssIdFor.element(UIElement.END_TURN)}
          step={endTurnStep}
        />
      </div>
    </div>
  );
}

export default HandDisplay;
