import { create } from "zustand";

export interface DeckbuildingStoreState {
  displayNameInput: string | null;
  setDisplayNameInput: (displayNameInput: string | null) => void;
}

export const useDeckbuildingStore = create<DeckbuildingStoreState>(
  (set, get) => ({
    displayNameInput: null,
    setDisplayNameInput: (displayNameInput) => {
      set({ displayNameInput });
    },
  })
);
