import { capitalize } from "engine/types/factions";
import { FACTIONS_MASTERY_ID } from "engine/puzzles/mastery-data";

import { Link, useSearchParams } from "react-router-dom";
import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import FactionScoreTotalsDisplay from "components/FactionScoreTotalsDisplay";
import FactionIcon from "components/FactionIcon";
import { Spinner } from "components/Loading";
import "./SidebarFactionDisplay.css";

function SidebarFactionDisplay() {
  const subscribedTeam = useSubscribedTeam();
  const [searchParams] = useSearchParams();

  if (subscribedTeam === null) return <Spinner inline={true} />;
  const { faction, factionScoreContributions, masteries } = subscribedTeam;
  const isFactionsEnabled = masteries[FACTIONS_MASTERY_ID].isEnabled ?? false;
  const factionScoreContribution =
    faction === null ? 0 : factionScoreContributions[faction];

  if (!isFactionsEnabled) return null;
  return (
    <div>
      {faction === null ? (
        <>
          <div className="no-faction-msg">
            You&apos;re not aligned with a faction yet.
          </div>
        </>
      ) : (
        <>
          <FactionScoreTotalsDisplay />
          <div>
            <b>Your faction:</b> <FactionIcon faction={faction} />{" "}
            {capitalize(faction)} <FactionIcon faction={faction} />
          </div>
          {factionScoreContribution === 0 ? null : (
            <div>
              <b>
                Total team <FactionIcon faction={faction} /> points:
              </b>{" "}
              {factionScoreContribution}
            </div>
          )}
        </>
      )}
      <Link
        to={{
          pathname: `/team/${encodeURIComponent(
            encodeURIComponent(subscribedTeam.displayName)
          )}`,
          search: searchParams.toString(),
        }}
      >
        <button className="change-faction-btn">
          {faction === null ? "Choose a faction!" : "Change faction alignment"}
        </button>
      </Link>
    </div>
  );
}

export default SidebarFactionDisplay;
