import Icon, { IconType } from "components/Icon";
import StepButton from "components/StepButton";
import Tooltip from "components/Tooltip";
import { DRAW_CARD_MASTERY_ID } from "engine/puzzles/mastery-data";
import { Player } from "engine/types/game-state";
import { cssIdFor, UIElement } from "engine/types/keyframes";
import { useInspector, useStepMaker } from "stores/ClientGameStore";
import { useZoomCardStore } from "stores/ZoomCardStore";
import "./DeckInfo.css";

interface DeckInfoProps {
  player: Player;
}

function DeckInfo(props: DeckInfoProps) {
  const { player } = props;

  const inspector = useInspector();
  const stepMaker = useStepMaker();
  const playerState = inspector.gameState[player];
  const mouseZoomedCard = useZoomCardStore((state) => state.mouseZoomedCard);
  const setZoomedCard = useZoomCardStore((state) => state.setMouseZoomedCard);
  const resetZoomedCard = useZoomCardStore(
    (state) => state.resetMouseZoomedCard
  );

  const hasDrawCardMastery = inspector.doesPlayerHaveMastery(
    player,
    DRAW_CARD_MASTERY_ID
  );
  const drawStep = stepMaker.draw(player);

  const { drawPile, topCard } = playerState;
  const topCardResolved = (() => {
    if (topCard !== undefined && topCard !== null) return topCard;
    if (drawPile !== undefined && drawPile.length > 0)
      return drawPile[drawPile.length - 1];
    return null;
  })();

  const isScrying =
    topCardResolved?.name !== undefined &&
    mouseZoomedCard?.cardName === topCardResolved?.name;

  const onScry = () => {
    if (topCardResolved !== null && topCardResolved.name !== undefined) {
      setZoomedCard(topCardResolved.name);
    }
  };

  const onUnscry = () => {
    resetZoomedCard();
  };

  const size = playerState.drawPileSize ?? playerState.drawPile?.length ?? 0;

  return (
    <div className="hand-deck-info">
      <Tooltip
        text={
          topCardResolved
            ? "Preview top card of draw pile"
            : "Number of cards in draw pile"
        }
      >
        <button
          className={`draw-pile-size ${topCardResolved ? "clickable" : ""} ${
            isScrying ? "selected" : ""
          }`}
          onClick={() => (isScrying ? onUnscry() : onScry())}
          onMouseEnter={onScry}
          onMouseLeave={onUnscry}
        >
          <Icon icon={IconType.DECK} />
          {size}
        </button>
      </Tooltip>
      {hasDrawCardMastery && size > 0 ? (
        <StepButton id={cssIdFor.element(UIElement.DRAW)} step={drawStep}>
          <button className={`deck-button ${hasDrawCardMastery ? "draw" : ""}`}>
            <Icon icon={IconType.DRAW} />
          </button>
        </StepButton>
      ) : null}
    </div>
  );
}

export default DeckInfo;
