import { useSearchParams, Link } from "react-router-dom";
import "./CutsceneUI.css";
import { getCutsceneArtPath } from "images";
import { getKeroLayers } from "engine/cards/card-effects-shared";
import { useClientGameStore } from "stores/ClientGameStore";
import {
  useSubscribedTeam,
  useRoomBattleGroupNameFromGameState,
} from "stores/ClientGlobalStateStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { usePuzName } from "stores/NavStore";

import CutsceneDialogBox from "components/CutsceneDialogBox";
import { getDevKnobs } from "App.util";
import CardDisplay, { CardSize } from "components/CardDisplay";
import Image, { KeroImage } from "components/Image";

function CutsceneUI() {
  const [searchParams] = useSearchParams();
  const puzName = usePuzName();
  const serverInterfaceController = useServerInterfaceController();
  const keyframe = useClientGameStore((state) => state.gameState.keyframe);
  const subscribedTeam = useSubscribedTeam();
  const roomBattleGroupName = useRoomBattleGroupNameFromGameState();

  if (subscribedTeam === null || roomBattleGroupName === null) return null;

  const { battleGroups, cardUnlocks } = subscribedTeam;
  const { isCutscene = false } = battleGroups[roomBattleGroupName];

  const { id: keyframeId, image } = keyframe ?? {};

  const imageChildren = (() => {
    switch (keyframeId) {
      case "tutorial1-card": {
        return <CardDisplay size={CardSize.LARGE} cardName={"new-boarn"} />;
      }
    }
    return null;
  })();

  const keroLayers = getKeroLayers(Object.keys(cardUnlocks), 0);

  return (
    <>
      <div className="game-cutscene-ui-img-wrapper">
        {image === "kero dos" ? (
          <KeroImage
            className="game-cutscene-ui-picture"
            keroLayers={keroLayers}
          />
        ) : null}
        {image !== undefined &&
        image !== "kero dos" &&
        imageChildren === null ? (
          <Image
            className="game-cutscene-ui-img"
            pictureClassName="game-cutscene-ui-picture"
            src={getCutsceneArtPath(image)}
          />
        ) : null}
        {imageChildren}
      </div>
      {keyframe !== undefined ? (
        <div
          id="game-cutscene-ui-dialog-box"
          className="game-cutscene-ui-dialog-box"
        >
          <CutsceneDialogBox keyframe={keyframe} />
        </div>
      ) : null}
      <div className="game-cutscene-ui-menu">
        {isCutscene ? (
          <button
            onClick={() => {
              serverInterfaceController.startAIBattleAsync(puzName, {
                restart: true,
                reuseDeck: true,
                devKnobs: getDevKnobs(),
              });
            }}
          >
            Restart
          </button>
        ) : null}
        <Link
          to={{
            pathname: `../prep/${puzName}`,
            search: searchParams.toString(),
          }}
        >
          <button>Exit {isCutscene ? "cutscene" : "battle"}</button>
        </Link>
      </div>
    </>
  );
}

export default CutsceneUI;
