import { useState, useEffect, useCallback } from "react";
import { useParams, useSearchParams, Link } from "react-router-dom";
import { useHotkeys } from "react-hotkeys-hook";
import Twemoji from "react-twemoji";
import "./MasteryTreePage.css";

import {
  PUZ_NAME_MASTERY_TREE,
  PUZ_NAME_MASTERY_TREE_PROMPT,
  PUZ_NAME_FIRST_ANSWER,
} from "engine/puzzles/puzzle-data";

import settings from "settings";
import { useMasteryTreeStore } from "stores/MasteryTreeStore";
import { useClientGameStore } from "stores/ClientGameStore";
import {
  useSubscribedTeam,
  useSubscribedMasteryTree,
  useIsAdminOrHuntOver,
} from "stores/ClientGlobalStateStore";
import {
  useServerInteractionStore,
  useTeamId,
} from "stores/ServerInteractionStore";
import { useIsAdminEnabledStore } from "stores/UserPreferencesStore";
import { useNavStore } from "stores/NavStore";
import { WSReqType } from "game-server/ws";
import { GlobalUpdatesScopeType } from "game-server/global-updates";
import {
  MasteryTreeStepType,
  MasteryTreeStep,
  AnswerOrientation,
  MASTERY_GRID_WIDTH,
  MASTERY_GRID_HEIGHT,
  START_X,
  START_Y,
  getPlacementCharIndexAt,
  normalizeAnswer,
  isPlacementCharValid,
  makeMasteryTreeGrid,
} from "game-server/masteries";
import MasteriesExplorer from "components/MasteriesExplorer";
import SidebarFactionDisplay from "components/SidebarFactionDisplay";
import BattleBackground from "components/BattleBackground";
import Presence from "components/Presence";
import { ConfirmModal } from "components/Modal";
import { Spinner } from "components/Loading";

function MasteryTreePage() {
  const { djangoBaseUrl } = settings;
  const [searchParams] = useSearchParams();
  const [showResetModal, setShowResetModal] = useState(false);
  const {
    answerSelection,
    setAnswerSelection,
    pendingAnswerSelection,
    setPendingAnswerSelection,
    hoverCell,
    setHoverCell,
  } = useMasteryTreeStore();
  const serverInterfaceController = useServerInteractionStore(
    (state) => state.getServerInterfaceController
  )();
  const teamId = useTeamId();
  const masteryTree = useSubscribedMasteryTree();
  const selectedMasteryId = useNavStore((state) => state.selectedMasteryId);
  const setSelectedMasteryId = useNavStore(
    (state) => state.setSelectedMasteryId
  );
  const setHoveredMasteryId = useNavStore((state) => state.setHoveredMasteryId);
  const setNavIsMasteryTreePage = useNavStore(
    (state) => state.setNavIsMasteryTreePage
  );
  const setIsMasteryUnlockModalOpen = useNavStore(
    (state) => state.setIsMasteryUnlockModalOpen
  );
  const setSendStep = useClientGameStore((state) => state.setSendStep);
  const { puzName } = useParams<{
    puzName?: string;
  }>();
  const isAdminOrHuntOver = useIsAdminOrHuntOver();
  const isSpectating = useIsAdminEnabledStore((state) => state.isSpectating);

  const subscribedTeam = useSubscribedTeam();
  const sendStep = useCallback(
    (step: MasteryTreeStep) => {
      if (serverInterfaceController === null) return;
      serverInterfaceController.sendReq({
        type: WSReqType.MASTERY_TREE_STEP,
        step,
      });
    },
    [serverInterfaceController]
  );

  useEffect(() => {
    setNavIsMasteryTreePage(true);
    return () => {
      setNavIsMasteryTreePage(false);
    };
  }, [setNavIsMasteryTreePage]);

  useEffect(() => {
    if (serverInterfaceController === null) return;
    if (isSpectating) return;
    return serverInterfaceController.addAuthEffect(() => {
      serverInterfaceController.sendReq({
        type: WSReqType.VIEW_PUZZLE,
        puzName: PUZ_NAME_MASTERY_TREE,
      });
    });
  }, [serverInterfaceController, isSpectating]);

  useEffect(() => {
    if (teamId === null) return;
    if (serverInterfaceController === null) return;
    return serverInterfaceController.addSubscribeEffect({
      type: GlobalUpdatesScopeType.TEAM_MASTERY_TREE,
      teamId,
    });
  }, [serverInterfaceController, teamId]);

  // Reset page state on dismount.
  useEffect(() => {
    return () => {
      setAnswerSelection(null);
      setHoveredMasteryId(null);
    };
  }, [setAnswerSelection, setHoveredMasteryId]);

  // Deselect the answer selection when it has been placed.
  useEffect(() => {
    if (answerSelection === null) return;
    if (masteryTree === null) return;
    if (masteryTree.placements[answerSelection.answer] === undefined) return;
    setAnswerSelection(null);
  }, [answerSelection, setAnswerSelection, masteryTree]);

  // Select the pending answer selection once it has been removed from
  // the grid.
  useEffect(() => {
    if (masteryTree === null) return;
    if (pendingAnswerSelection === null) return;
    // Ignore if the user selects something else in the meantime.
    if (answerSelection !== null) {
      setPendingAnswerSelection(null);
      return;
    }
    if (masteryTree.placements[pendingAnswerSelection.answer] !== undefined)
      return;
    setAnswerSelection(pendingAnswerSelection);
    setPendingAnswerSelection(null);
  }, [
    answerSelection,
    setAnswerSelection,
    pendingAnswerSelection,
    setPendingAnswerSelection,
    masteryTree,
  ]);

  useHotkeys("r", () => {
    if (answerSelection === null) return;
    setAnswerSelection({
      ...answerSelection,
      orientation: ((): AnswerOrientation => {
        switch (answerSelection.orientation) {
          case AnswerOrientation.HORIZONTAL:
            return AnswerOrientation.VERTICAL;
          case AnswerOrientation.VERTICAL:
            return AnswerOrientation.HORIZONTAL;
        }
      })(),
    });
  });

  useHotkeys("esc", () => {
    setAnswerSelection(null);
  });

  if (settings.isPublicAccess) {
    const { djangoBaseUrl } = settings;
    return (
      <div className="below-nav public-access-not-supported">
        <h2>Mastery Tree</h2>
        <div className="public-access-not-supported-message">
          The mastery tree is not yet supported in public access. You may still
          access the solution and stats pages. Please create or use an existing
          account to gain access to puzzle content.
        </div>
        <div className="public-access-not-supported-buttons">
          <a href={`${djangoBaseUrl}post-hunt-solve/mastery_tree`}>
            <button>Check your answer spoiler-free</button>
          </a>
          <a href={`${djangoBaseUrl}solution/mastery_tree`}>
            <button>View solution</button>
          </a>
          <a href={`${djangoBaseUrl}stats/mastery_tree`}>
            <button>Stats</button>
          </a>
        </div>
      </div>
    );
  }

  if (
    subscribedTeam === null ||
    masteryTree === null ||
    isAdminOrHuntOver === null
  )
    return <Spinner size={128} />;

  const { faction, puzzles, battleGroups, masteries, hintsInfo } =
    subscribedTeam;
  const isSolved = puzzles[PUZ_NAME_MASTERY_TREE]?.solveTime !== undefined;
  const { numHintsUsed, numHintsTotal } = hintsInfo;
  const numHintsRemaining = numHintsTotal - numHintsUsed;

  const availableAnswers = Object.values(puzzles).flatMap(
    ({ puzName, battleGroupName, answer }): ReadonlyArray<string> => {
      const battleGroup = battleGroups[battleGroupName];
      if (
        battleGroup === undefined ||
        puzName === PUZ_NAME_MASTERY_TREE ||
        answer === undefined ||
        answer === ""
      )
        return [];
      return [answer];
    }
  );
  availableAnswers.sort();
  const enabledMasteries = Object.values(masteries).filter(
    ({ isEnabled }) => isEnabled
  );
  const { puzPrompt } = puzzles[PUZ_NAME_MASTERY_TREE_PROMPT] ?? {};

  const urlParam = Object.keys(masteries)
    .sort()
    .map((masteryId) =>
      enabledMasteries.some(
        ({ masteryId: enabledMasteryId }) => masteryId === enabledMasteryId
      )
        ? "1"
        : "0"
    )
    .join("");

  const getMasteryIdAt = (x: number, y: number): string | null => {
    return (
      Object.values(masteries).find(
        (masteryData) => x === masteryData.x && y === masteryData.y
      )?.masteryId ?? null
    );
  };

  const hoveredMasteryId =
    hoverCell === null || answerSelection !== null
      ? null
      : getMasteryIdAt(hoverCell.x, hoverCell.y);
  const hoveredPlacement =
    hoverCell === null || answerSelection !== null || hoveredMasteryId !== null
      ? null
      : Object.values(masteryTree.placements)
          .reverse()
          .find(
            (placement) =>
              getPlacementCharIndexAt(placement, hoverCell.x, hoverCell.y) !==
              null
          ) ?? null;
  const newPlacement =
    answerSelection === null
      ? null
      : hoverCell === null
      ? null
      : {
          answer: answerSelection.answer,
          x: hoverCell.x,
          y: hoverCell.y,
          orientation: answerSelection.orientation,
        };

  const answersDom = availableAnswers.map((unnormalizedAnswer) => {
    const answer = normalizeAnswer(unnormalizedAnswer);
    const isUsed = !!masteryTree.placements[answer];
    const buttonsDom = isUsed
      ? (() => {
          const isSelected =
            hoveredPlacement !== null && hoveredPlacement.answer === answer;
          return (
            <button
              className={`mastery-tree-answer-button${
                isSelected ? " highlight" : ""
              }`}
              onClick={() => {
                sendStep?.({
                  type: MasteryTreeStepType.DELETE,
                  answer,
                });
              }}
            >
              Delete
            </button>
          );
        })()
      : [AnswerOrientation.HORIZONTAL, AnswerOrientation.VERTICAL].map(
          (orientation) => {
            const isSelected =
              answerSelection !== null &&
              answerSelection.answer === answer &&
              answerSelection.orientation === orientation;
            return (
              <button
                key={orientation}
                className={`mastery-tree-answer-button${
                  isSelected ? " highlight" : ""
                }`}
                disabled={isUsed}
                onClick={(e) => {
                  if (isSelected) {
                    setAnswerSelection(null);
                    return;
                  }
                  setAnswerSelection({
                    answer,
                    orientation,
                  });
                  e.stopPropagation();
                }}
              >
                {
                  {
                    [AnswerOrientation.HORIZONTAL]: "Horz",
                    [AnswerOrientation.VERTICAL]: "Vert",
                  }[orientation]
                }
              </button>
            );
          }
        );

    return (
      <div key={answer} className="mastery-tree-answer">
        <div className="mastery-tree-answer-label">{unnormalizedAnswer}</div>
        {buttonsDom}
      </div>
    );
  });

  const charGrid = makeMasteryTreeGrid(masteryTree);
  const gridDom = (() => {
    if (charGrid === null)
      return (
        <div className="mastery-tree-error">
          Oops! Looks like your mastery tree got into an invalid state. Use the
          buttons on the left to reset the mastery tree or delete answers from
          it to regain access to the mastery tree.
        </div>
      );

    const gridDom = [];
    for (let y = 0; y < MASTERY_GRID_HEIGHT; y++) {
      const rowDom = [];
      for (let x = 0; x < MASTERY_GRID_WIDTH; x++) {
        const existingChar = charGrid[y][x];
        const existingPlacement =
          Object.values(masteryTree.placements).find(
            (placement) => getPlacementCharIndexAt(placement, x, y) !== null
          ) ?? null;
        const isConnected =
          existingPlacement !== null &&
          masteryTree.connectedAnswers.includes(existingPlacement.answer);

        const newCharIndex =
          newPlacement === null
            ? null
            : getPlacementCharIndexAt(newPlacement, x, y);
        const newChar =
          newPlacement === null || newCharIndex === null
            ? null
            : newPlacement.answer[newCharIndex];
        const isCellInvalid = ((): boolean => {
          if (newPlacement === null) return false;
          if (newCharIndex === null) return false;
          return !isPlacementCharValid(charGrid, newPlacement, newCharIndex);
        })();
        const masteryId = getMasteryIdAt(x, y);
        const hasHoveredMastery =
          masteryId !== null &&
          hoveredMasteryId !== null &&
          masteryId === hoveredMasteryId;
        const hasHoveredPlacement =
          hoveredPlacement !== null &&
          getPlacementCharIndexAt(hoveredPlacement, x, y) !== null;
        const isStart = x === START_X && y === START_Y;
        const hasSelectedMastery =
          masteryId !== null &&
          selectedMasteryId !== null &&
          masteryId === selectedMasteryId;

        const highlightClass = isCellInvalid
          ? " invalid"
          : isStart
          ? " tree-root"
          : hasHoveredMastery
          ? " hovered-mastery"
          : masteryId !== null
          ? " mastery-target"
          : hasHoveredPlacement
          ? " hovered-answer"
          : isConnected
          ? " connected"
          : "";
        const isClickable = hasHoveredMastery || hasHoveredPlacement;
        const charToShow = newChar ?? existingChar;

        rowDom.push(
          <div
            key={x}
            className={`mastery-tree-cell${highlightClass}${
              hasSelectedMastery ? " selected-mastery" : ""
            }${isClickable ? " clickable" : ""}`}
            style={{
              width: `max(${100 / MASTERY_GRID_WIDTH}cqw, 1em)`,
              height: `max(${100 / MASTERY_GRID_HEIGHT}cqh, 1em)`,
            }}
            onMouseEnter={() => {
              setHoverCell({
                x,
                y,
              });
              if (masteryId !== null) setHoveredMasteryId(masteryId);
            }}
            onMouseLeave={() => {
              setHoveredMasteryId(null);
            }}
            onClick={(e) => {
              if (newPlacement !== null) {
                sendStep?.({
                  type: MasteryTreeStepType.PLACE,
                  placement: newPlacement,
                });
                e.stopPropagation();
                return;
              }
              if (masteryId !== null) {
                setSelectedMasteryId(masteryId);
                return;
              }
              if (hoveredPlacement !== null) {
                sendStep?.({
                  type: MasteryTreeStepType.DELETE,
                  answer: hoveredPlacement.answer,
                });
                setPendingAnswerSelection({
                  answer: hoveredPlacement.answer,
                  orientation: hoveredPlacement.orientation,
                });
                return;
              }
            }}
          >
            <div className="mastery-tree-cell-char">{charToShow}</div>
          </div>
        );
      }
      gridDom.push(
        <div
          key={y}
          className="mastery-tree-row"
          style={{
            width: `max(100cqw, ${MASTERY_GRID_WIDTH}em)`,
            height: `max(${100 / MASTERY_GRID_HEIGHT}cqh, 1em)`,
          }}
        >
          {rowDom}
        </div>
      );
    }
    return gridDom;
  })();

  const numEnabledMasteries = Object.values(masteries).filter(
    ({ isEnabled }) => isEnabled
  ).length;
  const totNumMasteries = Object.keys(masteries).length;
  const fishMetaDom = (
    <>
      {puzPrompt === undefined ? (
        <>
          <h5>
            Masteries activated: {numEnabledMasteries} / {totNumMasteries}
          </h5>
          <div>Activate all {totNumMasteries} masteries to proceed.</div>
          {subscribedTeam.puzzles[PUZ_NAME_FIRST_ANSWER]?.solveTime ? (
            <Link
              to={{
                pathname: `../prep/${PUZ_NAME_FIRST_ANSWER}`,
                search: searchParams.toString(),
              }}
            >
              <button className="mastery-tree-cutscene-btn">
                <div>Replay cutscene</div>
                <div>(Your first ANSWER)</div>
              </button>
            </Link>
          ) : null}
        </>
      ) : (
        <>
          <h5>
            {puzPrompt.header}
            {isSolved ? (
              <div
                className="solved-title-marker"
                style={{ fontSize: "0.8em" }}
              >
                Solved!{" "}
                <span className="solved-title-answer">
                  {puzzles[PUZ_NAME_MASTERY_TREE].answer}
                </span>
              </div>
            ) : null}
          </h5>
          <div>{puzPrompt.text}</div>
        </>
      )}
      {puzPrompt !== undefined || settings.isPosthunt || isAdminOrHuntOver ? (
        <div className="mastery-next-buttons">
          {puzPrompt !== undefined ? (
            <>
              {isSolved ? (
                settings.isPosthunt ? null : ( // hide feedback after posthunt
                  <a href={`${djangoBaseUrl}solve/${PUZ_NAME_MASTERY_TREE}`}>
                    <button>Leave feedback</button>
                  </a>
                )
              ) : (
                <a href={`${djangoBaseUrl}solve/${PUZ_NAME_MASTERY_TREE}`}>
                  <button>Submit answer</button>
                </a>
              )}
              {numHintsTotal && !isSolved ? (
                <a href={`${djangoBaseUrl}hints/${PUZ_NAME_MASTERY_TREE}`}>
                  <button>
                    {numHintsRemaining ? "Get a hint" : "View hints"} (
                    {numHintsRemaining}
                    <Twemoji tag="span">üí°</Twemoji>)
                  </button>
                </a>
              ) : null}
            </>
          ) : null}
          {settings.isPosthunt || isAdminOrHuntOver ? (
            <div className="mastery-solution-buttons">
              <a href={`${djangoBaseUrl}solution/${PUZ_NAME_MASTERY_TREE}`}>
                <button>Solution</button>
              </a>
              <a href={`${djangoBaseUrl}stats/${PUZ_NAME_MASTERY_TREE}`}>
                <button>Stats</button>
              </a>
            </div>
          ) : null}
        </div>
      ) : null}
    </>
  );

  return (
    <>
      <BattleBackground puzName={PUZ_NAME_MASTERY_TREE} />
      <Presence cursorGroupName={PUZ_NAME_MASTERY_TREE} enabled={true}>
        <div className="below-nav">
          <div
            className="mastery-tree-root"
            onClick={() => {
              setAnswerSelection(null);
            }}
          >
            <div className="mastery-tree-left-sidebar">
              <div className="mastery-tree-controls">
                <button onClick={() => setShowResetModal(true)}>Reset</button>
                {answerSelection === null ? null : (
                  <>
                    <div>
                      <kbd>r</kbd> Rotate
                    </div>
                    <div>
                      <kbd>Esc</kbd> Deselect
                    </div>
                  </>
                )}
              </div>
              <div className="mastery-tree-answers">{answersDom}</div>
            </div>
            <div className="mastery-tree-main">
              <div className={"mastery-tree-grid-panel"}>
                <div
                  id="mastery-tree-grid"
                  className={`mastery-tree-grid${
                    answerSelection !== null ? " pointer" : ""
                  }`}
                  style={{
                    height: `max(min(100cqh, ${
                      (100 / MASTERY_GRID_WIDTH) * MASTERY_GRID_HEIGHT
                    }cqw), ${MASTERY_GRID_HEIGHT}em)`,
                    width: `max(min(100cqw, ${
                      (100 / MASTERY_GRID_HEIGHT) * MASTERY_GRID_WIDTH
                    }cqh), ${MASTERY_GRID_WIDTH}em)`,
                  }}
                >
                  {gridDom}
                </div>
              </div>
            </div>
            <div className="mastery-tree-right-sidebar">
              <MasteriesExplorer />
              {faction === null ? <SidebarFactionDisplay /> : null}
              <div className="mastery-next">{fishMetaDom}</div>
              <div className="mastery-tree-right-sidebar-buttons">
                {
                  // URL overrides are not currently supported.
                  // <button
                  //   onClick={() => {
                  //     navigator.clipboard.writeText(urlParam).then(() => {
                  //       setCopyText("Copied!");
                  //       setTimeout(() => {
                  //         setCopyText("Copy URL code");
                  //       }, 1000);
                  //     });
                  //   }}
                  // >
                  //   {copyText}
                  // </button>
                }
                {puzName === undefined ? null : (
                  <Link
                    to={{
                      pathname: `../prep/${puzName}`,
                      search: searchParams.toString(),
                    }}
                  >
                    <button>Back</button>
                  </Link>
                )}
                <div>
                  <b>Note to postsolvers:</b> During the hunt, progress on the
                  mastery tree was gated behind hunt progress. If you‚Äôre just
                  postsolving individual battles, you may force-activate
                  masteries from the{" "}
                  <a
                    href="#"
                    onClick={() => {
                      setIsMasteryUnlockModalOpen(true);
                    }}
                  >
                    <b>
                      <Twemoji tag="span">üîì</Twemoji> Mastery Tree Unlocks
                    </b>{" "}
                    panel
                  </a>
                  .
                </div>
              </div>
            </div>
          </div>
        </div>
      </Presence>
      <ConfirmModal
        isOpen={showResetModal}
        onClose={() => setShowResetModal(false)}
        title="Are you sure?"
        onConfirm={() => {
          sendStep?.({
            type: MasteryTreeStepType.RESET,
          });
        }}
        confirmLabel="Reset"
      >
        This can‚Äôt be reversed.
      </ConfirmModal>
    </>
  );
}

export default MasteryTreePage;
