import { Update, UpdateType } from "engine/types/updates";
import { PUZ_NAME_POST_FINAL_BATTLE } from "engine/puzzles/puzzle-data";
import { LoseReason } from "game-server/backend-interface/BackendInterface";
import { EnterRoomParams } from "game-server/Room";
import { ServerInterfaceController } from "game-server/server-interface/ServerInterfaceController";
import { useMemo } from "react";
import { useNavigate, useSearchParams, useParams } from "react-router-dom";
import settings from "settings";
import { useAnimationStore } from "stores/AnimationStore";
import { useCardsDBStore } from "stores/CardsDBStore";
import {
  getPlayerPerspectiveForRole,
  makeInspector,
  useClientGameStore,
} from "stores/ClientGameStore";
import { useNeedRedirectToPrep } from "stores/ClientGlobalStateStore";
import { useHoverActionStore } from "stores/HoverActionStore";
import { useKeyboardCursorStore } from "stores/KeyboardCursorStore";
import { useNavStore, usePuzName } from "stores/NavStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useTurnStore } from "stores/TurnStore";
import { useUserLogStore } from "stores/UserLogStore";
import { useIsAdminEnabledStore } from "stores/UserPreferencesStore";

import { useUpdatesQueue } from "./useUpdatesQueue";

// How much to wait after making each update, in milliseconds.
const UPDATE_DELAYS = {
  [UpdateType.MOVE]: 200,
  [UpdateType.SWAP]: 200,
  [UpdateType.DAMAGE_PERMANENT]: 200,
  [UpdateType.REMOVE_PERMANENT]: 200,
};

/**
 * Performs all update effects besides modifications to the game
 * state. Game state modification effects should go in the
 * applyUpdate method of Reducer, to ensure that
 * the update takes effect on both the server and client.
 */
const triggerUpdateSideEffects = (
  upd: Update,
  navigate: ReturnType<typeof useNavigate>,
  searchParams: URLSearchParams
) => {
  const { addToUserLog } = useUserLogStore.getState();
  const { gameState, setRoomId, setIsEndCutsceneRedirectHandled } =
    useClientGameStore.getState();
  const { cardsDB } = useCardsDBStore.getState();
  const inspector = makeInspector(cardsDB, gameState);
  const playerPerspective = getPlayerPerspectiveForRole(
    useClientGameStore.getState().role
  );

  switch (upd.type) {
    case UpdateType.OVERRIDE_STATE: {
      const { roomId } = upd;
      setRoomId(roomId);

      // Reset the game UI actions state.
      const { resetSelectedCard: turnResetCard, resetEffectOpts } =
        useTurnStore.getState();
      const {
        resetSelectedCard: keyboardResetCard,
        resetEffectOpts: keyboardResetEffectOpts,
      } = useKeyboardCursorStore.getState();
      const { resetHoverState } = useHoverActionStore.getState();
      turnResetCard();
      resetEffectOpts();
      keyboardResetCard();
      keyboardResetEffectOpts();
      resetHoverState();
      break;
    }
    case UpdateType.ANNOUNCE: {
      addToUserLog(upd.message);
      break;
    }
    case UpdateType.ANNOUNCE_SUMMON_PERMANENT: {
      addToUserLog(
        `${upd.player === playerPerspective ? "You play" : "Opponent plays"} ${
          upd.cardName
        }`
      );
      break;
    }
    case UpdateType.ANNOUNCE_MOVE: {
      addToUserLog(
        `${upd.player === playerPerspective ? "You move" : "Opponent moves"} ${
          upd.cardName
        }`
      );
      break;
    }
    case UpdateType.ANNOUNCE_ATTACK_CARD: {
      addToUserLog(`${upd.cardName} attacks ${upd.target} for ${upd.damage}`);
      break;
    }
    case UpdateType.DRAW_CARD: {
      const cardName =
        upd.card.name === undefined
          ? "a card"
          : inspector.getCardDisplayName(upd.card);
      addToUserLog(
        `${
          upd.player === playerPerspective ? "You draw" : "Opponent draws"
        } ${cardName}`
      );
      break;
    }
    case UpdateType.DAMAGE_PERMANENT: {
      const permanent = inspector.getPermanent(upd.permanentId);
      const displayName = inspector.getCardDisplayName(permanent);
      addToUserLog(
        upd.damage < 0
          ? `${-upd.damage} damage removed from ${displayName}`
          : `${displayName} suffers ${upd.damage} damage`
      );
      break;
    }
    case UpdateType.REMOVE_PERMANENT: {
      const permanent = inspector.getPermanent(upd.permanentId);
      const cardName = inspector.getCardDisplayName(permanent);
      addToUserLog(`${cardName} expires`);
      break;
    }
    case UpdateType.START_TURN: {
      const turnText = upd.player === playerPerspective ? "Your" : "Opponent's";
      const turnNum = Math.floor(inspector.getTurnNumber() / 2) + 1;
      addToUserLog(`‚Äî ${turnText} turn ${turnNum}`);
      break;
    }
    case UpdateType.SET_GAME_RESULT: {
      const { gameState } = inspector;
      const playerState = gameState[playerPerspective];
      const { winner, loseReason } = upd;
      const { teamData } = playerState;
      if (teamData === undefined)
        throw new Error(
          "expect team data to be defined for player perspective"
        );
      const { isReplay } = teamData;
      const endCutsceneRedirect = inspector.getEndCutsceneRedirect();
      const endCutsceneRedirectDjango =
        inspector.getEndCutsceneRedirectDjango();
      const endMessage = (() => {
        const won = winner === playerPerspective;
        switch (loseReason) {
          case LoseReason.NORMAL: {
            return `You ${won ? "won" : "lost"}.`;
          }
          case LoseReason.RESIGN: {
            return `You ${won ? "won" : "lost"} because ${
              won ? "your opponent" : "you"
            } resigned.`;
          }
          case LoseReason.TIME: {
            return `You ${won ? "won" : "lost"} because ${
              won ? "your opponent" : "you"
            } ran out of time.`;
          }
        }
      })();
      endMessage && addToUserLog(endMessage);

      if (winner === playerPerspective) {
        if (
          !isReplay ||
          // Go to /victory after the final cutscene even on replay.
          gameState.puzName === PUZ_NAME_POST_FINAL_BATTLE
        ) {
          // Handle cutscene redirects without opening the solved modal.
          // Only redirect to the actual redirect page if this is the
          // first time the cutscene is played, and the client was around
          // to see the cutscene end.
          // If for example the client finishes a cutscene and hits "back",
          // we should redirect to the prep page instead. This is handled
          // later.
          if (endCutsceneRedirect !== null) {
            navigate({
              pathname: ".." + endCutsceneRedirect,
              search: searchParams.toString(),
            });
            setIsEndCutsceneRedirectHandled(true);
            return;
          }
          if (endCutsceneRedirectDjango !== null) {
            window.location.href = `${settings.djangoBaseUrl}${endCutsceneRedirectDjango}`;
            setIsEndCutsceneRedirectHandled(true);
            return;
          }
        }
      }
      break;
    }
  }
};

const handleUpdateAsync = async (
  serverInterfaceController: ServerInterfaceController,
  navigate: ReturnType<typeof useNavigate>,
  searchParams: URLSearchParams,
  upd: Update,
  isStale: () => boolean
) => {
  const { gameState, applyUpdate, setUpdateLock } =
    useClientGameStore.getState();
  const { setIsPageReady } = useNavStore.getState();
  const { animateUpdate } = useAnimationStore.getState();

  setUpdateLock(true);
  // apply changes outside the client game state
  triggerUpdateSideEffects(upd, navigate, searchParams);

  try {
    // trigger animations, wait for it all to finish
    await animateUpdate(upd, isStale);

    // HACK: This is just used by the final battle to inject
    // an animation delay.
    if (upd.type === UpdateType.WAIT_ANIMATE)
      await new Promise((resolve) => setTimeout(resolve, upd.waitTime));
  } catch (err) {
    // animiation failures are non-fatal
    console.error(err);
  }
  if (isStale()) return;

  const { role } = serverInterfaceController;
  if (role === undefined)
    throw new Error(
      "expect role to be defined once we've been joined to a room"
    );

  // apply changes to the client game state
  applyUpdate(upd, role);
  setUpdateLock(false);
  setIsPageReady(true);
};

const handleUpdatesSync = async (
  serverInterfaceController: ServerInterfaceController,
  navigate: ReturnType<typeof useNavigate>,
  searchParams: URLSearchParams,
  upds: Update[],
  isStale: () => boolean
) => {
  const { applyUpdate } = useClientGameStore.getState();
  const { setIsPageReady } = useNavStore.getState();

  for (const upd of upds) {
    triggerUpdateSideEffects(upd, navigate, searchParams);
    const { role } = serverInterfaceController;
    if (role === undefined)
      throw new Error(
        "expect role to be defined once we've been joined to a room"
      );

    // HACK: This is just used by the final battle to inject
    // an animation delay.
    if (upd.type === UpdateType.WAIT_ANIMATE) {
      await new Promise((resolve) => setTimeout(resolve, upd.waitTime));
      if (isStale()) return;
    }

    applyUpdate(upd, role);
  }

  setIsPageReady(true);
};

export const useGameUpdates = () => {
  const serverInterfaceController = useServerInterfaceController();
  const isAdminEnabled = useIsAdminEnabledStore(
    (state) => state.isAdminEnabled
  );
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const puzName = usePuzName();
  const { spectateTeamId } = useParams();
  const needRedirectToPrep = useNeedRedirectToPrep();

  const enterRoomParams = useMemo<EnterRoomParams | null>(
    () =>
      needRedirectToPrep !== null && !needRedirectToPrep
        ? {
            puzName,
            role: isAdminEnabled ? settings.role : undefined,
            spectateTeamId,
          }
        : null,
    [puzName, isAdminEnabled, spectateTeamId, needRedirectToPrep]
  );
  const processUpdateAsync = useMemo(() => {
    return async (upd: Update, isStale: () => boolean) => {
      return await handleUpdateAsync(
        serverInterfaceController,
        navigate,
        searchParams,
        upd,
        isStale
      );
    };
  }, [serverInterfaceController, navigate, searchParams]);
  const processUpdatesSync = useMemo(() => {
    return async (upds: Update[], isStale: () => boolean) => {
      return await handleUpdatesSync(
        serverInterfaceController,
        navigate,
        searchParams,
        upds,
        isStale
      );
    };
  }, [serverInterfaceController, navigate, searchParams]);
  useUpdatesQueue(
    enterRoomParams,
    UPDATE_DELAYS,
    processUpdateAsync,
    processUpdatesSync
  );
};
