import { CursorEvent, CursorEventType } from "engine/types/presence";
import { WSReqType } from "game-server/ws";
import { MouseEvent, useCallback, useMemo, useState } from "react";
import { useParams } from "react-router-dom";
import {
  useServerInterfaceController,
  useIsAdmin,
} from "stores/ServerInteractionStore";
import { useIsAdminEnabledStore } from "stores/UserPreferencesStore";
import { posToRatio, throttle } from "./utils";

export const useMouseMove = () => {
  const [pos, setPos] = useState({ x: 0, y: 0 });
  const serverInterfaceController = useServerInterfaceController();
  const isSpectating = useIsAdminEnabledStore((state) => state.isSpectating);
  const { spectateTeamId } = useParams();
  const enableSendEvents =
    useIsAdmin() === false && spectateTeamId === undefined && !isSpectating;

  // yes these are identical, so we have separate throttles
  const sendReq = useMemo(() => {
    const handler = (event: CursorEvent) => {
      if (!enableSendEvents) return;
      serverInterfaceController.sendReq({
        type: WSReqType.CURSOR_EVENT,
        event,
      });
    };
    return throttle(handler);
  }, [serverInterfaceController, enableSendEvents]);

  const sendReq2 = useMemo(() => {
    const handler = (event: CursorEvent) => {
      if (!enableSendEvents) return;
      serverInterfaceController.sendReq({
        type: WSReqType.CURSOR_EVENT,
        event,
      });
    };
    return throttle(handler);
  }, [serverInterfaceController, enableSendEvents]);

  const onMouseLeave = useCallback(() => {
    sendReq({
      type: CursorEventType.REMOVE,
    });
  }, [sendReq]);

  // HACK: Catch numbers that JSON into null. We're being super
  // careful here since this is a mid-hunt fix.
  const isJsonNull = (num: number) => JSON.parse(JSON.stringify(num)) === null;

  const onMouseMove = useCallback(
    (e: MouseEvent) => {
      const { rX, rY } = posToRatio({ x: e.pageX, y: e.pageY });
      if (rX !== null && rY !== null && !isJsonNull(rX) && !isJsonNull(rY)) {
        sendReq({
          type: CursorEventType.MOVE,
          rX,
          rY,
        });
      } else {
        sendReq({
          type: CursorEventType.REMOVE,
        });
      }
    },
    [sendReq]
  );

  const onClickCapture = useCallback(
    (e: MouseEvent) => {
      const { rX, rY } = posToRatio({ x: e.clientX, y: e.clientY });
      if (rX !== null && rY !== null && !isJsonNull(rX) && !isJsonNull(rY)) {
        sendReq2({
          type: CursorEventType.CLICK,
          rX,
          rY,
        });
      }
    },
    [sendReq2]
  );

  return { pos, setPos, onMouseLeave, onMouseMove, onClickCapture };
};
