import { useEffect, useLayoutEffect, useRef, useState } from "react";
import { useUserLogStore } from "stores/UserLogStore";
import "./GameLog.css";
import "overlayscrollbars/overlayscrollbars.css";
import {
  OverlayScrollbarsComponent,
  OverlayScrollbarsComponentRef,
} from "overlayscrollbars-react";
import { defaultOverlayScrollbarsOptions } from "HuntLib";

function GameLog() {
  const { userLog } = useUserLogStore();
  const [renderedUserLog, setRenderedUserLog] = useState(userLog);
  const [isFullyScrolled, setIsFullyScrolled] = useState(true);
  const ref = useRef<OverlayScrollbarsComponentRef>(null);

  useLayoutEffect(() => {
    const el = ref.current?.osInstance()?.elements()?.viewport;
    if (el) {
      if (el.scrollHeight - el.clientHeight - 50 <= el.scrollTop) {
        setIsFullyScrolled(true);
      } else {
        setIsFullyScrolled(false);
      }
    }
    setRenderedUserLog(userLog.trim());
  }, [userLog]);

  useEffect(() => {
    if (isFullyScrolled) {
      const el = ref.current?.osInstance()?.elements()?.viewport;
      if (el) {
        el.scrollTop = el.scrollHeight - el.clientHeight;
      }
    }
  }, [isFullyScrolled, renderedUserLog]);

  return (
    <div className="game-console-wrapper">
      <OverlayScrollbarsComponent
        options={defaultOverlayScrollbarsOptions}
        className="game-console"
        ref={ref}
      >
        {renderedUserLog}
      </OverlayScrollbarsComponent>
    </div>
  );
}

export default GameLog;
