import Image from "components/Image";
import { getMapBgArtPath, getMapBgSliceArtPaths } from "images";
import "./ParallaxBackground.css";

function ParallaxBackground({
  isTitleRevealed,
  scrollHeight,
}: {
  isTitleRevealed: boolean;
  scrollHeight: number | null;
}) {
  if (scrollHeight === null) return null;
  const sliceHeight = window.innerWidth / 2; // slices have a 2:1 ratio
  const sliceBg = (path: string) =>
    getMapBgSliceArtPaths(path)
      .slice(0, Math.ceil(scrollHeight / sliceHeight))
      .map((src, i) => (
        <Image
          className="layer"
          src={src}
          key={i}
          style={{ top: i * 50 + "vw" }}
        />
      ));
  return (
    <>
      <div className="fg" style={{ height: scrollHeight + "px" }}>
        {isTitleRevealed ? sliceBg("islands") : null}
      </div>
      <div className="bg" style={{ height: scrollHeight + "px" }}>
        {sliceBg("2-cave-full")}
        <h1 className="title">Galactic Puzzle Hunt 2023</h1>
        {isTitleRevealed ? (
          <Image
            className="gcc-new"
            src={getMapBgArtPath("galacticardcaptors.png")}
          />
        ) : (
          <Image
            className="gcc"
            src={getMapBgArtPath("galacticosmicave.png")}
          />
        )}
      </div>
    </>
  );
}

export default ParallaxBackground;
