import Tooltip from "components/Tooltip";
import {
  createContext,
  HTMLProps,
  ReactNode,
  useCallback,
  useContext,
  useState,
} from "react";
import Twemoji from "react-twemoji";
import "./Tabs.css";

type Tab = {
  label: string;
  tooltip?: ReactNode;
  content: ReactNode;
  isDisabled?: boolean;
};

interface TabsProps {
  className?: string;
  emptyContent?: ReactNode;
  index?: number;
  setIndex?: (index: number) => void;
  items: Tab[];
}

const TabContext = createContext<Required<TabsProps> & { allowEmpty: boolean }>(
  {
    allowEmpty: false,
    className: "tabs",
    emptyContent: null,
    index: -1,
    setIndex: () => {
      // nothing
    },
    items: [],
  }
);

function Container(
  props: TabsProps & Omit<HTMLProps<HTMLDivElement>, "onSelect">
) {
  const {
    emptyContent,
    className = "tabs",
    index: indexProp,
    setIndex: setIndexProp,
    items,
    children,
    ...rest
  } = props;
  const [index_, setIndex_] = useState(emptyContent ? -1 : 0);
  const isControlled = indexProp !== undefined && setIndexProp !== undefined;

  const index = isControlled ? indexProp : index_;
  const setIndex = useCallback(
    (index: number) => {
      (isControlled ? setIndexProp : setIndex_)(index);
    },
    [isControlled, setIndexProp]
  );

  return (
    <TabContext.Provider
      value={{
        allowEmpty: !!emptyContent,
        className,
        emptyContent: emptyContent ?? null,
        index,
        setIndex,
        items,
      }}
    >
      <div className={`${className}-tabbed`} {...rest}>
        {children}
      </div>
    </TabContext.Provider>
  );
}

function ButtonList(props: HTMLProps<HTMLDivElement>) {
  const { className } = useContext(TabContext);

  return (
    <div
      {...props}
      className={`${className}-tabs tabs-tabs ${props.className}`}
    />
  );
}

function Buttons({
  makeProps,
  overridden,
}: {
  makeProps?: (index: number) => Omit<HTMLProps<HTMLButtonElement>, "type">;
  overridden?: boolean;
}) {
  const { allowEmpty, className, index, setIndex, items } =
    useContext(TabContext);

  const renderButtons = useCallback(
    (selected: number) => {
      const makeButton = (item: Tab, index: number) => {
        const isActive = !overridden && index === selected;
        return (
          <Tooltip
            key={item.label}
            text={item.tooltip}
            directRef={(tooltipProps) => (
              <button
                {...makeProps?.(index)}
                key={item.label}
                className={`${className}-tab tabs-tab ${
                  isActive ? "active" : "inactive"
                }`}
                onClick={() => setIndex(isActive ? -1 : index)}
                disabled={item.isDisabled || (allowEmpty ? false : isActive)}
                {...tooltipProps}
              >
                <Twemoji>{item.label}</Twemoji>
              </button>
            )}
          />
        );
      };
      return <>{items.map(makeButton)}</>;
    },
    [allowEmpty, className, items, makeProps, setIndex, overridden]
  );

  return <>{renderButtons(index)}</>;
}

function Content() {
  const { emptyContent, index, items } = useContext(TabContext);
  const elt =
    0 <= index && index < items.length ? items[index].content : emptyContent;
  return <>{elt}</>;
}

function Tabbed(
  props: TabsProps & Omit<HTMLProps<HTMLDivElement>, "onSelect">
) {
  return (
    <Container {...props}>
      <ButtonList>
        <Buttons />
      </ButtonList>
      <div className={`${props.className}-content`}>
        <Content />
      </div>
    </Container>
  );
}

const Tabs = {
  Buttons,
  ButtonList,
  Container,
  Content,
  Tabbed,
};

export default Tabs;
