import { describeForm } from "components/BattleHelpText";
import FormattedText from "components/FormattedText";
import StepButton from "components/StepButton";
import { Permanent } from "engine/types/game-state";
import {
  AbilityType,
  EffectOptType,
  OneChoiceEffectOptForm,
} from "engine/types/effects";
import { cssIdFor, UIElement } from "engine/types/keyframes";
import { StepType, Step } from "engine/types/steps";
import { ReactNode, useState } from "react";
import {
  useClientGameStore,
  useInspector,
  useStepMaker,
  useRole,
  useIsLockedBesidesKeyframe,
} from "stores/ClientGameStore";
import { useSelectedPermanent, useTurnStore } from "stores/TurnStore";
import { useResetEffectOpts } from "stores/UserGameActionHandlers";
import "./CardActionsDisplay.css";
import { useKeyboardCursorStore } from "stores/KeyboardCursorStore";
import { Check, isPassthrough } from "engine/types/action-validation";

const SHORTCUT_SPECIAL = "Z";
const SHORTCUT_FLEX = "X";
const SHORTCUT_CREATE1 = "C";
const SHORTCUT_CREATE2 = "V";

type CardActionData = {
  step: Step;
  button: ReactNode;
};

export const useSelectedPermanentCardActionData = (): CardActionData[] => {
  const permanent = useSelectedPermanent();
  const inspector = useInspector();
  const stepMaker = useStepMaker();
  const role = useRole();
  const hotkeysEnabled = useKeyboardCursorStore((state) => state.enabled);
  const isLockedBesidesKeyframe = useIsLockedBesidesKeyframe();
  const cardActions: CardActionData[] = [];

  if (permanent === null) return [];
  const gemColorCombos = inspector.getCreateGemsColors(permanent);
  for (let i = 0; i < gemColorCombos.length; i++) {
    const gemColors = gemColorCombos[i];
    const createStep = stepMaker.create(permanent, gemColors);
    const shortcut = hotkeysEnabled
      ? i == 0
        ? SHORTCUT_CREATE1
        : i == 1
        ? SHORTCUT_CREATE2
        : undefined
      : undefined;
    cardActions.push({
      step: createStep,
      button: (
        <StepButton
          id={cssIdFor.element(UIElement.CREATE_GEMS)}
          key={`${StepType.CREATE_GEMS}-${gemColors.join("-")}`}
          shortcut={shortcut}
          step={createStep}
        ></StepButton>
      ),
    });
  }

  for (const abilityType of [AbilityType.FLEX, AbilityType.SPECIAL]) {
    const abilityStep = stepMaker.activateAbility(permanent, abilityType, []);
    const shortcut = hotkeysEnabled
      ? abilityType === AbilityType.FLEX
        ? SHORTCUT_FLEX
        : SHORTCUT_SPECIAL
      : undefined;
    cardActions.push({
      step: abilityStep,
      button: (
        <StepButton
          id={cssIdFor.element(
            abilityType === AbilityType.FLEX
              ? UIElement.FLEX
              : UIElement.SPECIAL
          )}
          key={abilityType}
          shortcut={shortcut}
          step={abilityStep}
        ></StepButton>
      ),
    });
  }

  return cardActions.filter(({ step }) => {
    const { failedChecks } = inspector.resolveAndValidate(step, role);
    // Visually disable the UI if the UI is locked.
    if (isLockedBesidesKeyframe) failedChecks.add(Check.UI_LOCKED);
    failedChecks.delete(Check.NUM_OPTS);
    return !isPassthrough(failedChecks, step.type);
  });
};

function CardActionsDisplay({ fallback = null }: { fallback?: ReactNode }) {
  const pendingEffect = useTurnStore((state) => state.pendingEffect);
  const pendingForms = useTurnStore((state) => state.pendingForms);
  const resetEffectOpts = useResetEffectOpts();
  const permanent = useSelectedPermanent();
  const inspector = useInspector();
  const stepMaker = useStepMaker();
  const hotkeysEnabled = useKeyboardCursorStore((state) => state.enabled);
  const selectedPermanentCardActionData = useSelectedPermanentCardActionData();

  if (permanent === null) return null;
  const permanentId = permanent.id;

  // TODO: userPrompt should be somewhere more central
  // TODO: cancel should not be here, but in the below zone
  if (pendingEffect !== null) {
    const pendingForm = pendingForms[0];
    if (pendingForm === undefined) {
      throw new Error(
        "effect opts selection should always have at least one pending form"
      );
    }
    const userPrompt = describeForm(
      inspector,
      pendingForm,
      pendingEffect,
      permanent.card.name
    );

    // Handle the case where the user needs to pick an option out of a dropdown in the side menu.
    const choices = (pendingForm as OneChoiceEffectOptForm).choices;
    return (
      <>
        <FormattedText text={userPrompt} />
        {choices ? (
          <DropdownSelectAndSubmit
            choices={choices}
            permanent={permanent}
            pendingEffect={pendingEffect}
          />
        ) : (
          ""
        )}
        <button
          key={"cancel"}
          onClick={() => {
            resetEffectOpts();
          }}
        >
          Cancel
        </button>
      </>
    );
  }

  return selectedPermanentCardActionData.length === 0 ? (
    <>{fallback}</>
  ) : (
    <div className="card-action-buttons">
      {selectedPermanentCardActionData.map(({ button }) => button)}
    </div>
  );
}

type DropdownSelectAndSubmitProps = {
  choices: string[];
  permanent: Permanent;
  pendingEffect: AbilityType;
};

function DropdownSelectAndSubmit(props: DropdownSelectAndSubmitProps) {
  const { choices, permanent, pendingEffect } = props;
  const sendStep = useClientGameStore((state) => state.sendStep);
  const stepMaker = useStepMaker();
  const [selectedOption, setSelectedOption] = useState(choices[0]); // This is the default.
  return (
    <>
      <select
        value={selectedOption}
        onChange={(e) => setSelectedOption(e.target.value)}
      >
        {choices.map((opt) => (
          <option key={opt} value={opt}>
            {opt}
          </option>
        ))}
      </select>
      <button
        key={"option-select"}
        onClick={() => {
          // If nothing has been selected yet, do nothing.
          if (!selectedOption) return;
          // If the server is down, don't accept creating new steps.
          // However, still mark inputs as clickable so that the click
          // handler doesn't propagate to the parent and cause a deselect.
          if (!sendStep) return;
          sendStep(
            stepMaker.activateAbility(permanent, pendingEffect, [
              {
                choice: selectedOption,
                type: EffectOptType.ONE_CHOICE,
              },
            ])
          );
        }}
      >
        Submit
      </button>
    </>
  );
}

export default CardActionsDisplay;
