import Twemoji from "react-twemoji";
import "./BattleStatus.css";

import Tooltip from "components/Tooltip";
import { FACTIONS_MASTERY_ID } from "engine/puzzles/mastery-data";
import {
  PUZ_NAME_MASTERY_TREE,
  PUZ_NAME_PVP,
} from "engine/puzzles/puzzle-data";
import { GlobalStateBattleRoomStatus } from "game-server/global-updates";
import settings from "settings";
import {
  useSelectedBattleGroup,
  useSubscribedBattleStatus,
  useSubscribedTeam,
  useIsAdminOrHuntOver,
} from "stores/ClientGlobalStateStore";
import { usePuzName } from "stores/NavStore";

function BattleStatus() {
  const { djangoBaseUrl } = settings;

  const puzName = usePuzName();
  const isPvP = puzName === PUZ_NAME_PVP;
  const selectedBattleGroup = useSelectedBattleGroup();
  const subscribedTeam = useSubscribedTeam();
  const battleStatus = useSubscribedBattleStatus(puzName);
  const isAdminOrHuntOver = useIsAdminOrHuntOver();

  if (
    subscribedTeam === null ||
    selectedBattleGroup === null ||
    isAdminOrHuntOver === null
  )
    return null;

  const { battleGroupName, isCutscene = false } = selectedBattleGroup;
  const { puzzles, masteries, hintsInfo } = subscribedTeam;
  const { numHintsUsed, numHintsTotal } = hintsInfo;
  const numHintsRemaining = numHintsTotal - numHintsUsed;
  const isBattleGroupSolved = puzzles[battleGroupName].solveTime !== undefined;

  const hasMasteries = puzzles[PUZ_NAME_MASTERY_TREE] !== undefined;
  const hasFactions = masteries[FACTIONS_MASTERY_ID].isEnabled !== undefined;
  const choicesText = (() => {
    const choices = [
      hasFactions ? "faction" : undefined,
      "deck",
      hasMasteries ? "masteries" : undefined,
    ].filter((s): s is string => !!s);

    if (choices.length > 1) {
      choices[choices.length - 1] = `and ${choices[choices.length - 1]}`;
    }
    return choices.join(choices.length > 2 ? ", " : " ");
  })();

  return (
    <div className="battle-status">
      <div className="battle-status-message">
        {!isCutscene && battleStatus === GlobalStateBattleRoomStatus.ACTIVE ? (
          <Tooltip
            text={`The ${choicesText} for the battle are locked. Changes to them won't affect the current battle.`}
          >
            <Twemoji>‚ö†Ô∏è Battle is ongoing!</Twemoji>
          </Tooltip>
        ) : null}
      </div>
      <div className="battle-status-buttons">
        {!isCutscene && !isPvP && (settings.isPosthunt || isAdminOrHuntOver) ? (
          <>
            <a href={`${djangoBaseUrl}solution/${battleGroupName}`}>
              <button>Solution</button>
            </a>
            <a href={`${djangoBaseUrl}stats/${battleGroupName}`}>
              <button>Stats</button>
            </a>
          </>
        ) : null}
        {!settings.isPosthunt && !isCutscene && isBattleGroupSolved ? (
          <a href={`${djangoBaseUrl}solve/${battleGroupName}`}>
            <button>Leave feedback</button>
          </a>
        ) : null}
        {numHintsTotal && !isCutscene && !isPvP && !isBattleGroupSolved ? (
          <a href={`${djangoBaseUrl}hints/${battleGroupName}`}>
            <button>
              {numHintsRemaining ? "Get a hint" : "View hints"} (
              {numHintsRemaining}
              <Twemoji tag="span">üí°</Twemoji>)
            </button>
          </a>
        ) : null}
      </div>
    </div>
  );
}

export default BattleStatus;
