import TeamDisplayNameDisplay from "components/TeamDisplayNameDisplay";
import {
  SPEEDRUN_MASTERY_ID,
  REPUTATION_BOOST_MASTERY_ID,
} from "engine/puzzles/mastery-data";
import { PUZ_NAME_PVP } from "engine/puzzles/puzzle-data";
import { Player } from "engine/types/game-state";
import {
  useInspector,
  usePlayerPerspective,
  useRoomPuzNameFromGameState,
} from "stores/ClientGameStore";
import {
  usePuzzlesInBattleGroup,
  useIsInstancer,
  useSubscribedTeam,
  useRoomSubpuzzleIndexFromGameState,
  useRoomBattleGroupFromGameState,
} from "stores/ClientGlobalStateStore";
import { usePrimaryPuzName } from "stores/NavStore";
import "./FieldTeamDataDisplay.css";
import SpeedrunTimer from "./SpeedrunTimer";

interface FieldTeamDataDisplayProps {
  player: Player;
}

function FieldTeamDataDisplay(props: FieldTeamDataDisplayProps) {
  const pagePuzName = usePrimaryPuzName();
  const subscribedTeam = useSubscribedTeam();
  const playerUs = usePlayerPerspective();
  const roomPuzName = useRoomPuzNameFromGameState();
  const isPvP = roomPuzName === PUZ_NAME_PVP;
  const roomBattleGroup = useRoomBattleGroupFromGameState();
  const puzzlesInBattleGroup = usePuzzlesInBattleGroup();
  const roomSubpuzzleIndex = useRoomSubpuzzleIndexFromGameState();
  const isInstancer = useIsInstancer() ?? false;
  const { player } = props;
  const inspector = useInspector();
  const showSpeedrunTimer = inspector.doesPlayerHaveMastery(
    player,
    SPEEDRUN_MASTERY_ID
  );
  const { gameState } = inspector;
  const playerState = gameState[player];
  const { teamData } = playerState;

  if (
    roomPuzName === null ||
    subscribedTeam === null ||
    roomBattleGroup === null ||
    puzzlesInBattleGroup === null ||
    roomSubpuzzleIndex === null
  )
    return null;
  const { puzzles } = subscribedTeam;
  const { solveTime } = puzzles[roomPuzName];
  const { displayName, numPuzzles = 1, isLegendary = false } = roomBattleGroup;
  const instancerIndex = isInstancer
    ? puzzlesInBattleGroup.findIndex(
        (puzData) => puzData.puzName === pagePuzName
      )
    : null;

  const teamNameDom =
    teamData === undefined ? (
      <div>
        <b>{`${displayName}${
          numPuzzles === 1 ? "" : `, Stage ${roomSubpuzzleIndex + 1}`
        }`}</b>
        {solveTime === undefined ? null : (
          <div className="game-puz-complete">
            {numPuzzles === 1
              ? isLegendary
                ? `(Befriended)`
                : `(Previously completed)`
              : "(Previously completed)"}
          </div>
        )}
        {instancerIndex === null ? null : (
          <div className="game-instancer-note">
            Instancer, Room {instancerIndex + 1}
          </div>
        )}
      </div>
    ) : (
      (() => {
        const {
          teamId,
          displayName,
          faction,
          factionScoreContribution,
          enabledMasteries,
        } = teamData;
        return (
          <TeamDisplayNameDisplay
            teamData={{
              teamId,
              displayName,
              faction,
              factionScoreContribution,
              hasReputationBoost:
                enabledMasteries[REPUTATION_BOOST_MASTERY_ID] ?? false
                  ? true
                  : undefined,
            }}
            noLink={!isPvP}
          />
        );
      })()
    );
  return (
    <div className="game-team-data">
      {teamNameDom}
      {showSpeedrunTimer || isPvP ? (
        // Speedrun times for single-player games include the time
        // from both players, so include the AI's time for extra
        // precision.
        <SpeedrunTimer
          prevTurnsTime={
            playerState.prevTurnsTime +
            (inspector.hasAI() && player === Player.P1
              ? gameState[Player.P2].prevTurnsTime
              : 0)
          }
          startTurnTime={playerState.startTurnTime}
          keepRunningDuringUpdates={inspector.hasAI()}
          isGameActive={inspector.isGameActive()}
          isOurTimer={player === playerUs}
        />
      ) : null}
    </div>
  );
}

export default FieldTeamDataDisplay;
