import { getPuzzleMapIcon } from "engine/puzzles/puzzle-data";
import {
  ClientBattleGroupGlobalState,
  ClientPuzzleGlobalState,
} from "game-server/global-updates";

export const scrollToPromise = (top: number) => {
  const promise = new Promise<void>((resolve) => {
    const handler = () => {
      const maxScrollIsh = document.body.scrollHeight - window.innerHeight - 10;
      if (
        Math.abs(window.scrollY - top) <= 10 ||
        (top > maxScrollIsh && window.scrollY > maxScrollIsh)
      ) {
        window.removeEventListener("scroll", handler);
        resolve();
      }
    };
    handler();
    window.addEventListener("scroll", handler, { passive: true });
  });

  window.scrollTo({ top, behavior: "smooth" });

  return promise;
};

export const getIcon = (
  puzData: ClientPuzzleGlobalState,
  battleGroup: ClientBattleGroupGlobalState
) => {
  const { hasAnswer = false } = puzData;
  const {
    battleGroupName,
    isCutscene = false,
    isLegendary = false,
  } = battleGroup;
  return getPuzzleMapIcon(hasAnswer, battleGroupName, isCutscene, isLegendary);
};
