import StepButton from "components/StepButton";
import ZoomedCardDisplay from "components/ZoomedCardDisplay";
import { opponentOf } from "engine/types/game-state";
import { StepType } from "engine/types/steps";
import { Role } from "engine/types/updates";
import { usePlayerPerspective, useRole } from "stores/ClientGameStore";
import { useZoomedCard } from "stores/ZoomCardStore";
import FieldTeamDataDisplay from "./FieldTeamDataDisplay";
import "./RightOfFieldDisplay.css";

function RightOfFieldDisplay() {
  const role = useRole();
  // Use the zoomed card here to show the buttons also on hover.
  const zoomedCard = useZoomedCard();
  const permanentId =
    zoomedCard === null ? null : zoomedCard.permanentId ?? null;
  const playerUs = usePlayerPerspective();
  const playerThem = opponentOf(playerUs);

  return (
    <>
      <div className="game-board-right">
        <FieldTeamDataDisplay player={playerThem} />
        <div
          className="game-zoomed-card-wrapper"
          onClick={(e) => e.stopPropagation()}
        >
          <ZoomedCardDisplay />
          {permanentId !== null && role === Role.GOD ? (
            <StepButton
              step={{
                type: StepType.REMOVE,
                permanentId,
              }}
            />
          ) : null}
        </div>
        <FieldTeamDataDisplay player={playerUs} />
      </div>
    </>
  );
}

export default RightOfFieldDisplay;
