import { useParams, useResolvedPath } from "react-router-dom";
import { useKeyboardCursorStore } from "stores/KeyboardCursorStore";
import {
  useServerInterfaceController,
  useTeamId,
  useIsAdmin,
} from "stores/ServerInteractionStore";
import {
  useHotkeyPreferencesStore,
  useUserPreferencesStore,
} from "stores/UserPreferencesStore";
import "./GameSettings.css";
import { useNavStore } from "stores/NavStore";
import { PUZ_NAME_FINAL_BATTLE } from "engine/puzzles/puzzle-data";
import Tooltip from "components/Tooltip";

const NESTED_TOOLTIP_TEXT = (location: string) =>
  `When enabled, you can mouse over keywords in tooltips from ${location} to show another tooltip.`;

function GameSettings() {
  const serverInterfaceController = useServerInterfaceController();

  const speed = useUserPreferencesStore((state) => state.speed);
  const setSpeed = useUserPreferencesStore((state) => state.setSpeed);
  const recurseFieldTooltips = useUserPreferencesStore(
    (state) => state.recurseFieldTooltips
  );
  const setRecurseFieldTooltips = useUserPreferencesStore(
    (state) => state.setRecurseFieldTooltips
  );
  const recurseActionTooltips = useUserPreferencesStore(
    (state) => state.recurseActionTooltips
  );
  const setRecurseActionTooltips = useUserPreferencesStore(
    (state) => state.setRecurseActionTooltips
  );
  const animKeroDos = useUserPreferencesStore((state) => state.animKeroDos);
  const setAnimKeroDos = useUserPreferencesStore(
    (state) => state.setAnimKeroDos
  );
  const setIsHotkeyModalOpen = useNavStore(
    (state) => state.setIsHotkeyModalOpen
  );

  const isHotkeysEnabled = useKeyboardCursorStore((state) => state.enabled);
  const isHotkeysEnabledPref = useHotkeyPreferencesStore(
    (state) => state.isEnabled
  );
  const setIsHotkeysEnabledPref = useHotkeyPreferencesStore(
    (state) => state.setIsEnabled
  );

  const { puzName } = useParams<{
    puzName: string;
  }>();
  if (puzName === undefined)
    throw new Error("expect puzName parameter to be defined");

  const teamId = useTeamId();
  const { spectateTeamId } = useParams();
  const path = useResolvedPath(
    `../spectate/${puzName}/${spectateTeamId ?? teamId}`
  );
  const isAdmin = useIsAdmin() ?? false;
  const spectateLink = isAdmin ? (
    <a href={path.pathname} target="_blank" rel="noreferrer">
      <button>Spectate link</button>
    </a>
  ) : null;

  return (
    <>
      <div className="game-settings">
        <Tooltip
          text="Instant speed might improve performance."
          directRef={(props) => (
            <div {...props}>
              <label>Animation speed</label>
              <div style={{ display: "flex", justifyContent: "space-between" }}>
                <button onClick={() => setSpeed(1)} disabled={speed === 1}>
                  1√ó
                </button>
                <button onClick={() => setSpeed(2)} disabled={speed === 2}>
                  2√ó
                </button>
                <button onClick={() => setSpeed(0)} disabled={speed === 0}>
                  Instant
                </button>
              </div>
            </div>
          )}
        />
        <div className="game-settings-checkbox">
          <input
            type="checkbox"
            id="hotkeys-checkbox"
            checked={isHotkeysEnabledPref}
            onChange={(e) => {
              const newVal = e.target.checked;
              setIsHotkeysEnabledPref(newVal);
            }}
          />{" "}
          <label htmlFor="hotkeys-checkbox">Enable hotkeys</label>
        </div>
        <div
          onClick={() => setIsHotkeyModalOpen(true)}
          style={{ marginTop: "-0.3em" }}
        >
          <button>Hotkeys Help{isHotkeysEnabled ? " (/)" : null}</button>
        </div>
        <Tooltip
          text={NESTED_TOOLTIP_TEXT("the field")}
          directRef={(props) => (
            <div className="game-settings-checkbox" {...props}>
              <input
                type="checkbox"
                id="recurse-field-tooltips-checkbox"
                checked={recurseFieldTooltips}
                onChange={(e) => setRecurseFieldTooltips(e.target.checked)}
              />{" "}
              <label htmlFor="recurse-field-tooltips-checkbox">
                Nest field tooltips
              </label>
            </div>
          )}
        />
        <Tooltip
          text={NESTED_TOOLTIP_TEXT("card action buttons")}
          directRef={(props) => (
            <div className="game-settings-checkbox" {...props}>
              <input
                type="checkbox"
                id="recurse-action-tooltips-checkbox"
                checked={recurseActionTooltips}
                onChange={(e) => setRecurseActionTooltips(e.target.checked)}
              />{" "}
              <label htmlFor="recurse-action-tooltips-checkbox">
                Nest action tooltips
              </label>
            </div>
          )}
        />
        {puzName === PUZ_NAME_FINAL_BATTLE ? (
          <Tooltip
            text="Turning off the Kero animation might improve performance."
            directRef={(props) => (
              <div className="game-settings-checkbox" {...props}>
                <input
                  type="checkbox"
                  id="anim-kero-dos-checkbox"
                  checked={animKeroDos}
                  onChange={(e) => setAnimKeroDos(e.target.checked)}
                />{" "}
                <label htmlFor="anim-kero-dos-checkbox">Animate Kero</label>
              </div>
            )}
          />
        ) : null}
        {spectateLink}
      </div>
    </>
  );
}

export default GameSettings;
