import settings from "settings";
import errorPng from "images/error.png";
import { useRouteError, isRouteErrorResponse } from "react-router-dom";

export function Error() {
  const error = useRouteError();
  const is404 = isRouteErrorResponse(error) && error.status === 404;
  return (
    <div
      style={{
        display: "flex",
        flexDirection: "column",
        gap: "24px",
        alignItems: "center",
        justifyContent: "center",
        position: "fixed",
        inset: "0",
      }}
    >
      <style
        dangerouslySetInnerHTML={{
          __html: `
.errorthing { animation: errorthing 5s ease-in-out infinite; }

@keyframes errorthing {
0% { filter: sepia(0.25) saturate(0); }
10% { filter: sepia(0.75) saturate(0.25); }
25% { filter: sepia(0.25) saturate(0.75); }
50% { filter: sepia(0) saturate(0.5); }
100% { filter: sepia(0.25) saturate(0); }
}
        `,
        }}
      />
      <h1>{is404 ? "Page not found" : "Something went wrong"}</h1>
      <a href={`${settings.djangoBaseUrl}puzzles`}>
        <img src={errorPng} className="errorthing" />
      </a>
      <h4 style={{ border: "none" }}>
        You are in a maze of twisty little passages, all alike.
      </h4>
      {is404 ? (
        <div>(This is not a puzzle. Click above to go back.)</div>
      ) : (
        <div>
          (This is not a puzzle. Try refreshing, or{" "}
          <a href="mailto:contact@galacticpuzzlehunt.com">contact us</a>.)
        </div>
      )}
    </div>
  );
}
