import MasteriesExplorer from "components/MasteriesExplorer";
import Tooltip from "components/Tooltip";
import { PUZ_NAME_MASTERY_TREE } from "engine/puzzles/puzzle-data";
import { useNavigate, useSearchParams } from "react-router-dom";
import Twemoji from "react-twemoji";
import {
  useSubscribedTeam,
  useSubscribedTeamBattle,
} from "stores/ClientGlobalStateStore";
import { usePuzName } from "stores/NavStore";
import "./PrepMasteries.css";

function PrepMasteries() {
  const puzName = usePuzName();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const subscribedTeam = useSubscribedTeam();
  const { activeGame } = useSubscribedTeamBattle() ?? {};

  const { puzzles } = subscribedTeam ?? {};
  const isMasteryTreeEnabled = puzzles?.[PUZ_NAME_MASTERY_TREE] !== undefined;

  const toMasteries = () =>
    navigate({
      pathname: `../mastery/${puzName}`,
      search: searchParams.toString(),
    });

  if (!isMasteryTreeEnabled) return null;

  const lock = activeGame ? (
    <Tooltip
      text="Masteries are locked; changes won't affect the current battle."
      directRef={(props) => (
        <div {...props} className="masteries-lock">
          <Twemoji>üîí</Twemoji>
        </div>
      )}
    />
  ) : null;

  return (
    <>
      <div className="prep-masteries">
        <MasteriesExplorer enabledMasteries={activeGame?.enabledMasteries} />
        {lock}
      </div>
      <button className="prep-masteries-btn secondary" onClick={toMasteries}>
        Select masteries
      </button>
    </>
  );
}

export default PrepMasteries;
