import { create } from "zustand";

import { AnswerOrientation } from "game-server/masteries";

export type AnswerSelection = {
  answer: string;
  orientation: AnswerOrientation;
};

export type HoverCell = {
  x: number;
  y: number;
};

export interface MasteryTreeStoreState {
  answerSelection: AnswerSelection | null;
  setAnswerSelection: (answerSelection: AnswerSelection | null) => void;

  pendingAnswerSelection: AnswerSelection | null;
  setPendingAnswerSelection: (answerSelection: AnswerSelection | null) => void;

  hoverCell: HoverCell | null;
  setHoverCell: (hoverCell: HoverCell | null) => void;
}

export const useMasteryTreeStore = create<MasteryTreeStoreState>(
  (set, get) => ({
    answerSelection: null,
    setAnswerSelection: (answerSelection) => {
      set({ answerSelection });
    },

    pendingAnswerSelection: null,
    setPendingAnswerSelection: (pendingAnswerSelection) => {
      set({ pendingAnswerSelection });
    },

    hoverCell: null,
    setHoverCell: (hoverCell) => {
      set({ hoverCell });
    },
  })
);
