import { useEffect, useRef, useState } from "react";
import "./BattleBackground.css";
import { getMapBgSliceArtPaths } from "images";

import Image from "components/Image";
import { usePuzNameIfExists } from "stores/NavStore";
import {
  useSubscribedTeam,
  useSelectedPuzName,
} from "stores/ClientGlobalStateStore";

interface BattleBackgroundProps {
  puzName?: string;
  mapYOffset?: number;
}

function BattleBackground(props: BattleBackgroundProps) {
  const [windowWidth, setWindowWidth] = useState<number>(window.innerWidth);
  const [windowHeight, setWindowHeight] = useState<number>(window.innerHeight);
  const bgElemRef = useRef<HTMLDivElement>(null);
  useEffect(() => {
    if (bgElemRef.current === null) return;
    const fixHeight = () => {
      setWindowWidth(window.innerWidth);
      setWindowHeight(window.innerHeight);
    };
    const resizeObserver = new ResizeObserver(fixHeight);
    resizeObserver.observe(bgElemRef.current);
    fixHeight();
    return () => resizeObserver.disconnect();
  });

  const { puzName: puzNameOpt, mapYOffset = 0 } = props;
  const selectedPuzName = useSelectedPuzName();
  const pagePuzName = usePuzNameIfExists();
  const puzName = puzNameOpt ?? selectedPuzName ?? pagePuzName;
  const subscribedTeam = useSubscribedTeam();

  if (subscribedTeam === null) return null;

  const { puzzles, battleGroups } = subscribedTeam;
  const sliceBg = (() => {
    if (puzName === null) return null;
    const { battleGroupName } = puzzles[puzName] ?? {};
    const { mapPos } = battleGroups[battleGroupName] ?? {};
    if (mapPos === undefined) return null;
    return (path: string) => {
      const screenTop = mapPos.y + mapYOffset - 20;
      const screenBottom = screenTop + (100 * windowHeight) / windowWidth;
      const sliceHeight = 50; // slices have a 2:1 ratio
      const firstSlice = Math.floor(screenTop / sliceHeight);
      return getMapBgSliceArtPaths(path)
        .slice(firstSlice, Math.ceil(screenBottom / sliceHeight))
        .map((src, i) => (
          <Image
            className="layer"
            src={src}
            key={i}
            style={{
              position: "absolute",
              top: (firstSlice + i) * sliceHeight - screenTop + "vw",
            }}
          />
        ));
    };
  })();

  return (
    <>
      <div className="battle-bg" style={{ background: "black" }} />
      {sliceBg === null ? (
        <div
          className="battle-bg"
          style={{ backgroundColor: "rgba(0, 0, 0, 0.5)" }}
        />
      ) : (
        <div className="battle-bg" ref={bgElemRef}>
          {sliceBg("2-cave-full")}
          {sliceBg("islands")}
        </div>
      )}
    </>
  );
}

export default BattleBackground;
