import { useState, useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import {
  PUZ_NAME_PVP,
  PUZ_NAME_FINAL_BATTLE,
} from "engine/puzzles/puzzle-data";
import {
  useClientGameStore,
  useInspector,
  usePlayerPerspective,
  useRoomPuzNameFromGameState,
} from "stores/ClientGameStore";
import {
  useRoomBattleGroupNameFromGameState,
  useRoomBattleGroupFromGameState,
  usePuzzlesInRoomBattleGroupFromGameState,
  useRoomSubpuzzleIndexFromGameState,
} from "stores/ClientGlobalStateStore";

import { usePuzName, useNavStore, LeftTab } from "stores/NavStore";
import { useCardsDBStore } from "stores/CardsDBStore";

export const useGameEnd = () => {
  /** Rising edge detector to open the menu when the game ends. */
  const [isGameEndUITriggered, setIsGameEndUITriggered] =
    useState<boolean>(false);
  const roomId = useClientGameStore((state) => state.roomId);
  const isEndCutsceneRedirectHandled = useClientGameStore(
    (state) => state.isEndCutsceneRedirectHandled
  );
  const inspector = useInspector();
  const cardsDB = useCardsDBStore((state) => state.cardsDB);
  const playerPerspective = usePlayerPerspective();
  const isPageReady = useNavStore((state) => state.isPageReady);
  const setLeftTabIndex = useNavStore((state) => state.setLeftTabIndex);
  const setSolvedModalState = useNavStore((state) => state.setSolvedModalState);
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();

  const puzName = usePuzName();
  const roomPuzName = useRoomPuzNameFromGameState();
  const roomBattleGroupName = useRoomBattleGroupNameFromGameState();
  const { numPuzzles = null } = useRoomBattleGroupFromGameState() ?? {};
  const roomPuzIndex = useRoomSubpuzzleIndexFromGameState();
  const puzzlesInRoomBattleGroup = usePuzzlesInRoomBattleGroupFromGameState();

  const hasGameEnded = inspector.hasGameEnded();
  const { gameState } = inspector;
  const playerState = gameState[playerPerspective];
  const { loseReason } = gameState;
  const { teamData, isSolved = false, stats } = playerState;
  const endCutsceneRedirect = inspector.getEndCutsceneRedirect();
  const endCutsceneRedirectDjango = inspector.getEndCutsceneRedirectDjango();
  const speedrunTime = hasGameEnded ? inspector.getSpeedrunTime() : null;

  useEffect(() => {
    if (!isPageReady) return;
    if (roomBattleGroupName === null) return;
    if (hasGameEnded) {
      if (teamData === undefined)
        throw new Error(
          "expect team data to be defined for player perspective"
        );

      // Handle cutscene redirects without opening the solved modal.
      if (
        isSolved &&
        (endCutsceneRedirect !== null || endCutsceneRedirectDjango !== null)
      ) {
        // The case where the team runs through the cutscene for the
        // first time, and the client is present for the cutscene end,
        // is handled when receiving SET_GAME_RESULT.
        if (isEndCutsceneRedirectHandled) return;

        // Special-case out the final battle, since we want to show
        // the solved modal on replay instead of redirecting to the
        // prep page.
        if (roomPuzName !== PUZ_NAME_FINAL_BATTLE) {
          navigate({
            pathname: `../prep/${puzName}`,
            search: searchParams.toString(),
          });
          return;
        }
      }

      if (!isGameEndUITriggered) {
        setIsGameEndUITriggered(true);
        setLeftTabIndex(LeftTab.MENU);
        if (isSolved) {
          if (speedrunTime === null)
            throw new Error("expect speedrun time to be computed");
          if (roomId === null) throw new Error("expect roomId to be set");
          setSolvedModalState({
            roomId,
            puzName,
            roomPuzName,
            roomBattleGroupName,
            cardUnlocks: playerState.cardUnlocks ?? [],
            speedrunTime,
            teamData,
            stats,
            isWinner: true,
            loseReason,
          });
        }
        if (puzName === PUZ_NAME_PVP) {
          if (roomId === null) throw new Error("expect roomId to be set");
          setSolvedModalState({
            roomId,
            puzName,
            roomPuzName,
            roomBattleGroupName,
            cardUnlocks: playerState.cardUnlocks ?? [],
            speedrunTime: 0,
            teamData,
            stats,
            isWinner: gameState.winner === playerPerspective,
            loseReason,
          });
        }
      }
    } else {
      setIsGameEndUITriggered(false);
    }
  }, [
    isPageReady,
    hasGameEnded,
    isGameEndUITriggered,
    setIsGameEndUITriggered,
    setSolvedModalState,
    isSolved,
    playerState.cardUnlocks,
    endCutsceneRedirect,
    endCutsceneRedirectDjango,
    navigate,
    searchParams,
    teamData,
    puzName,
    speedrunTime,
    stats,
    roomId,
    setLeftTabIndex,
    roomPuzName,
    roomBattleGroupName,
    gameState.winner,
    playerPerspective,
    loseReason,
    isEndCutsceneRedirectHandled,
  ]);

  return hasGameEnded;
};
