import { WSErrorCode } from "game-server/ws";
import { useEffect } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import settings from "settings";
import { useClientGameStore } from "stores/ClientGameStore";
import { useKeyboardCursorStore } from "stores/KeyboardCursorStore";
import { usePuzName } from "stores/NavStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useResetGameUIActionsState } from "stores/UserGameActionHandlers";

export const useInitSendStep = () => {
  const setStepLock = useClientGameStore((state) => state.setStepLock);
  const setSendStep = useClientGameStore((state) => state.setSendStep);
  const resetGameUIActionsState = useResetGameUIActionsState();
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const puzName = usePuzName();
  const serverInterfaceController = useServerInterfaceController();
  const resetForSuccessfulStep = useKeyboardCursorStore(
    (state) => state.resetForSuccessfulStep
  );

  useEffect(() => {
    return serverInterfaceController.addAuthEffect(() => {
      let isCanceled = false;
      setSendStep((step) => {
        (async () => {
          resetGameUIActionsState();
          // TODO: reset hotkey state?

          // unlocked when we get STEP_ACK
          setStepLock(true);
          const err = await serverInterfaceController.sendStepAsync(step);
          if (isCanceled) return;
          setStepLock(false);

          if (err !== null) {
            const { errCode } = err;
            switch (errCode) {
              case WSErrorCode.CANCELED_BY_CLIENT: {
                break;
              }
              case WSErrorCode.INVALID_STEP: {
                // Note that all steps should now be validated on the client
                // side, so we should not be receiving invalid step errors
                // except due to race conditions during online mode (which
                // are okay).
                if (settings.localMode) {
                  throw new Error(`unexpected invalid step error`);
                }
                break;
              }
              default: {
                throw new Error(`unhandled error "${errCode}"`);
              }
            }
          } else {
            resetForSuccessfulStep();
          }
        })();
      });
      return () => {
        isCanceled = true;
        setSendStep(null);
      };
    });
  }, [
    serverInterfaceController,
    setSendStep,
    resetGameUIActionsState,
    resetForSuccessfulStep,
    setStepLock,
  ]);

  useEffect(() => {
    return serverInterfaceController.onErrorEvent.addListener((resp) => {
      // If we received an error, we're most probably not going
      // to get our step ack.
      setStepLock(false);

      if (resp.errCode === WSErrorCode.NO_ROOM) {
        navigate(
          {
            pathname: `../prep/${puzName}`,
            search: searchParams.toString(),
          },
          { replace: true }
        );
      }
    });
  }, [serverInterfaceController, setStepLock, puzName, navigate, searchParams]);
};
