import Tooltip from "components/Tooltip";
import { Check, FailedChecks } from "engine/types/action-validation";
import { getDeckSize } from "engine/types/decks";
import { Player } from "engine/types/game-state";
import { getDeckUniqueLegendaries } from "game-server/deckbuilding";
import Twemoji from "react-twemoji";
import { useStaticInspector } from "stores/ClientGameStore";
import {
  useSubscribedDeck,
  useSubscribedTeam,
} from "stores/ClientGlobalStateStore";
import { useSlot } from "stores/NavStore";
import "./DeckStats.css";

function DeckStats() {
  const slot = useSlot();
  const deck = useSubscribedDeck(slot);
  const inspector = useStaticInspector();
  const subscribedTeam = useSubscribedTeam();

  if (!deck || !subscribedTeam) return null;

  const numCards = getDeckSize(deck);
  const maxNumCards = inspector.getMaxNumCards();

  const { cardUnlocks } = subscribedTeam;
  const numLegendariesBefriended = Object.values(cardUnlocks).filter(
    ({ cardName }) => inspector.isLegendary(cardName)
  ).length;
  const hasLegendariesUnlocked = numLegendariesBefriended > 0;
  const numLegendaries = getDeckUniqueLegendaries(deck);
  const maxNumLegendaries = inspector.getMaxNumLegendaries();

  const failedChecks: FailedChecks = new Set();
  // No harm always using P1, since this is symmetric the only place
  // where this matters (PvP)
  inspector.validateDeck(failedChecks, Player.P1, deck);
  // Show failed checks in order.
  const failedChecksStr = [
    Check.DECK_TOO_SMALL,
    Check.DECK_TOO_LARGE,
    Check.DECK_TOO_MANY_LEGENDARIES,
  ]
    .filter((failedCheck) => failedChecks.has(failedCheck))
    .map((failedCheck) => {
      switch (failedCheck) {
        case Check.DECK_TOO_SMALL:
          return "Too few cards.";
        case Check.DECK_TOO_LARGE:
          return "Too many cards.";
        case Check.DECK_TOO_MANY_LEGENDARIES:
          return "Too many Legendaries.";
      }
      throw new Error("unrecognized deckbuilding failed check");
    })
    .join("\n");

  return (
    <div className="deck-stats">
      <div>
        <span
          className={
            numCards === 0 || numCards > maxNumCards
              ? "deck-sidebar-text-invalid"
              : numCards === maxNumCards
              ? "deck-sidebar-text-full"
              : undefined
          }
        >
          <b>{numCards}</b> / {maxNumCards}
        </span>{" "}
        cards
      </div>
      {hasLegendariesUnlocked ? (
        <div>
          <span
            className={
              numLegendaries > maxNumLegendaries
                ? "deck-sidebar-text-invalid"
                : numLegendaries === maxNumLegendaries
                ? "deck-sidebar-text-full"
                : undefined
            }
          >
            <b>{numLegendaries}</b> / {maxNumLegendaries}
          </span>{" "}
          unique Legendary cards
        </div>
      ) : null}
      {failedChecksStr ? (
        <Tooltip className="failed-checks" text={failedChecksStr}>
          <Twemoji>‚ùå</Twemoji>
        </Tooltip>
      ) : null}
    </div>
  );
}

export default DeckStats;
