export const getRootElt = () => {
  return (
    document.getElementById("game-page-root") ??
    document.getElementById("mastery-tree-root") ??
    document.body
  );
};

const getRefElt = () => {
  return (
    document.getElementById("game-board") ??
    document.getElementById("mastery-tree-grid") ??
    document.getElementById("game-cutscene-ui-dialog-box")
  );
};

export const posToRatio = ({ x, y }: { x: number; y: number }) => {
  const rect = getRefElt()?.getBoundingClientRect();

  if (!rect) {
    return { rX: null, rY: null };
  }

  const rX = (x - (rect.x + window.scrollX)) / rect.width;
  const rY = (y - (rect.y + window.scrollY)) / rect.height;
  return { rX, rY };
};

export const ratioToPos = ({ rX, rY }: { rX: number; rY: number }) => {
  const rect = getRefElt()?.getBoundingClientRect();

  if (!rect) {
    return { x: null, y: null };
  }

  const x = rect.width * rX + rect.x + window.scrollX;
  const y = rect.height * rY + rect.y + window.scrollY;
  return { x, y };
};

/* eslint-disable */
export function throttle<T extends (...args: any) => any>(
  func: T,
  // TODO: change throttle ms based on load testing
  wait = 200
): typeof func {
  let inThrottle: boolean;
  let lastResult: ReturnType<T>;

  // @ts-expect-error
  return function (this: any) {
    const args = arguments as any;
    const context = this;
    if (!inThrottle) {
      inThrottle = true;
      setTimeout(() => (inThrottle = false), wait);
      lastResult = func.apply(context, args);
    }
    return lastResult;
  };
}
/* eslint-enable */

const murmurhash = (str: string, seed: number) => {
  let l = str.length,
    h = seed ^ l,
    i = 0,
    k;

  while (l >= 4) {
    k =
      (str.charCodeAt(i) & 0xff) |
      ((str.charCodeAt(++i) & 0xff) << 8) |
      ((str.charCodeAt(++i) & 0xff) << 16) |
      ((str.charCodeAt(++i) & 0xff) << 24);
    k =
      (k & 0xffff) * 0x5bd1e995 + ((((k >>> 16) * 0x5bd1e995) & 0xffff) << 16);
    k ^= k >>> 24;
    k =
      (k & 0xffff) * 0x5bd1e995 + ((((k >>> 16) * 0x5bd1e995) & 0xffff) << 16);

    h =
      ((h & 0xffff) * 0x5bd1e995 +
        ((((h >>> 16) * 0x5bd1e995) & 0xffff) << 16)) ^
      k;
    l -= 4;
    ++i;
  }

  switch (l) {
    case 3: {
      h ^= (str.charCodeAt(i + 2) & 0xff) << 16;
      h ^= (str.charCodeAt(i + 1) & 0xff) << 8;
      h ^= str.charCodeAt(i) & 0xff;
      h =
        (h & 0xffff) * 0x5bd1e995 +
        ((((h >>> 16) * 0x5bd1e995) & 0xffff) << 16);
      break;
    }
    case 2: {
      h ^= (str.charCodeAt(i + 1) & 0xff) << 8;
      h ^= str.charCodeAt(i) & 0xff;
      h =
        (h & 0xffff) * 0x5bd1e995 +
        ((((h >>> 16) * 0x5bd1e995) & 0xffff) << 16);
      break;
    }
    case 1: {
      h ^= str.charCodeAt(i) & 0xff;
      h =
        (h & 0xffff) * 0x5bd1e995 +
        ((((h >>> 16) * 0x5bd1e995) & 0xffff) << 16);
      break;
    }
  }

  h ^= h >>> 13;
  h = (h & 0xffff) * 0x5bd1e995 + ((((h >>> 16) * 0x5bd1e995) & 0xffff) << 16);
  h ^= h >>> 15;

  return h >>> 0;
};

export const colorHash = (str: string, l: number, c: number) => {
  const h = murmurhash(str, 0x1234abcd) % 359;
  return `oklch(${l}% ${c} ${h})`;
};
