import { useState, useEffect, useMemo } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";
import Twemoji from "react-twemoji";

import {
  BATTLE_GROUP_NAME_MOONICK,
  MOONICK_UNLOCK_GROUPING,
  MOONICK_UNLOCK_THRESHOLDS,
} from "engine/puzzles/puzzle-data";
import {
  ClientPuzzleGlobalState,
  GlobalStateBattleRoomStatus,
} from "game-server/global-updates";

import Tooltip from "components/Tooltip";
import {
  useSubscribedTeam,
  useSelectedPuzName,
  useSelectedBattleGroup,
  useIsInstancer,
  usePuzzlesInSelectedBattleGroup,
} from "stores/ClientGlobalStateStore";
import { usePuzName } from "stores/NavStore";

import "./SubpuzzleSelection.css";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { WSReqType } from "game-server/ws";

interface SubpuzzleButtonProps {
  puzData: ClientPuzzleGlobalState;
  puzIndex: number;
}

function SubpuzzleButton(props: SubpuzzleButtonProps) {
  const { puzData, puzIndex } = props;

  const pagePuzName = usePuzName();
  const selectedPuzName = useSelectedPuzName();
  const isInstancer = useIsInstancer() ?? false;
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const serverInterfaceController = useServerInterfaceController();
  const subscribedTeam = useSubscribedTeam();

  const { puzName, solveTime, roomStatus, isNew } = puzData ?? {};

  const isSelected = selectedPuzName === puzData?.puzName;
  const isSolved = solveTime !== undefined;
  const className = `prep-puz-selection-btn ${isSelected ? "highlight" : ""} ${
    isSolved ? "solve-highlight" : ""
  }`;
  const isInstancerRoomActive =
    isInstancer &&
    subscribedTeam !== null &&
    subscribedTeam.puzzles[pagePuzName].roomStatus ===
      GlobalStateBattleRoomStatus.ACTIVE;

  const goToPuzzle = isInstancer
    ? () => {
        if (isInstancerRoomActive) return;
        serverInterfaceController.sendReq({
          type: WSReqType.SELECT_PUZZLE,
          hostPuzName: pagePuzName,
          selectedPuzName: puzData.puzName,
        });
      }
    : () =>
        navigate(
          {
            pathname: `../prep/${puzName}`,
            search: searchParams.toString(),
          },
          { replace: true }
        );

  const {
    text,
    disabled = false,
    label,
  } = (() => {
    if (puzName === undefined) {
      return {
        text: "Locked.",
        disabled: true,
        label: `${puzIndex}üîí`,
      };
    }

    if (
      isInstancer
        ? isInstancerRoomActive && puzName === selectedPuzName
        : roomStatus === GlobalStateBattleRoomStatus.ACTIVE
    ) {
      return {
        text: "Battle in progress.",
        label: `${puzIndex}‚öîÔ∏è`,
        disabled: isInstancerRoomActive,
      };
    }

    if (isNew) {
      return {
        text: "New battle!",
        label: `${puzIndex}‚ú®`,
        disabled: isInstancerRoomActive,
      };
    }

    if (isSolved) {
      return {
        text: "Done!",
        label: `${puzIndex}`,
        disabled: isInstancerRoomActive,
      };
    }

    return {
      text: undefined,
      label: `${puzIndex}`,
      disabled: isInstancerRoomActive,
    };
  })();

  return (
    <div className="prep-puz-selection-btn-wrapper">
      <Tooltip text={text}>
        <button className={className} disabled={disabled} onClick={goToPuzzle}>
          <Twemoji className="prep-puz-selection-btn-content">{label}</Twemoji>
        </button>
      </Tooltip>
    </div>
  );
}

function SubpuzzleSelection() {
  const subscribedTeam = useSubscribedTeam();
  const battleGroup = useSelectedBattleGroup();
  const puzzlesInBattleGroup = usePuzzlesInSelectedBattleGroup();
  const selectedPuzName = useSelectedPuzName();

  const { battleGroupName = null, numPuzzles = 1 } = battleGroup ?? {};
  const isMoonick =
    battleGroupName !== null && battleGroupName === BATTLE_GROUP_NAME_MOONICK;
  const unlockGrouping = useMemo(
    () => (isMoonick ? MOONICK_UNLOCK_GROUPING : [numPuzzles]),
    [isMoonick, numPuzzles]
  );
  const puzIndex =
    puzzlesInBattleGroup === null
      ? null
      : puzzlesInBattleGroup.findIndex(
          (puzData) => puzData.puzName === selectedPuzName
        );

  const [activeRow, setActiveRow] = useState(0);

  useEffect(() => {
    if (puzIndex === null) return;
    if (battleGroupName === null) return;
    if (puzIndex === null) return;
    let curRow = 0;
    let rowStartIndex = 0;
    while (puzIndex >= rowStartIndex + unlockGrouping[curRow]) {
      rowStartIndex += unlockGrouping[curRow];
      curRow++;
    }
    setActiveRow(curRow);
  }, [puzIndex, battleGroupName, setActiveRow, unlockGrouping]);

  if (
    subscribedTeam === null ||
    battleGroup === null ||
    puzzlesInBattleGroup === null
  )
    return null;

  if (numPuzzles === 1) return null;

  const firstPuz = unlockGrouping
    .slice(0, activeRow)
    .reduce((acc, cur) => acc + cur, 0);
  const puzzlesToShow = puzzlesInBattleGroup
    .concat(Array(numPuzzles).fill(undefined))
    .slice(firstPuz, firstPuz + unlockGrouping[activeRow]);

  const rowDom = puzzlesToShow.map((data, idx) => (
    <SubpuzzleButton
      key={firstPuz + idx}
      puzData={data}
      puzIndex={firstPuz + idx + 1}
    />
  ));

  const nextTooltip = !isMoonick
    ? undefined
    : activeRow >= 3
    ? undefined
    : puzzlesInBattleGroup.length <=
      MOONICK_UNLOCK_GROUPING.slice(0, activeRow + 1).reduce(
        (acc, cur) => acc + cur,
        0
      )
    ? `Solve ${MOONICK_UNLOCK_THRESHOLDS[activeRow]} of ${MOONICK_UNLOCK_GROUPING[activeRow]} to unlock the next phase.`
    : undefined;

  return (
    <div className="prep-puz-selection">
      {activeRow > 0 ? (
        <button
          className="prep-puz-selection-row-btn"
          onClick={() => setActiveRow(activeRow - 1)}
        >
          ¬´
        </button>
      ) : null}
      <div className="prep-puz-selection-row">{rowDom}</div>
      {activeRow < unlockGrouping.length - 1 ? (
        <Tooltip
          text={nextTooltip}
          directRef={(props) => (
            <button
              className="prep-puz-selection-row-btn"
              onClick={() => setActiveRow(activeRow + 1)}
              {...props}
            >
              ¬ª
            </button>
          )}
        ></Tooltip>
      ) : null}
    </div>
  );
}

export default SubpuzzleSelection;
