import { useZoomedCard } from "stores/ZoomCardStore";
import { useInspector, usePlayerPerspective } from "stores/ClientGameStore";
import CardDisplay, { CardSize } from "components/CardDisplay";

function ZoomedCardDisplay() {
  const card = useZoomedCard();
  const inspector = useInspector();
  const playerPerspective = usePlayerPerspective();
  if (!card) {
    return <CardDisplay size={CardSize.LARGE} faceDown={false} />;
  }

  const { cardName, permanentId } = card;
  // The permanentId may refer to a stale permanent, so resolve
  // it first before passing into CardDisplay.
  const permanent =
    permanentId === undefined
      ? null
      : inspector.getPermanentIfExists(permanentId);

  return (
    <CardDisplay
      isOwner={permanent !== null && permanent.owner === playerPerspective}
      size={CardSize.LARGE}
      cardName={cardName}
      permanent={permanent ?? undefined}
      disableHoverZoom={true}
    />
  );
}

export default ZoomedCardDisplay;
