import { makeInitGameState } from "engine/types/game-state";
import { Update, UpdateType } from "engine/types/updates";
import { EnterRoomParams } from "game-server/Room";
import { useEffect } from "react";
import { useClientGameStore } from "stores/ClientGameStore";
import {
  useClientGlobalStateStore,
  getBattleGroup,
  getIsGameSettingsEnabled,
} from "stores/ClientGlobalStateStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useUserPreferencesStore } from "stores/UserPreferencesStore";

// Process the next update every time we see a new update
// appear at the front of the pending updates queue. This
// relies on referential equality.
//
// Note that a series of updates should never be applied all
// at once, or even within a single loop, since updates to
// state outside the client game state (e.g. to the user log)
// may depend on the completion of prior state updates.
// Using useEffect ensures that prior state changes show up in
// gameState by the time applyUpdate is called.
export const useUpdatesQueue = (
  /** Params to enter the room and associate this updates processor with. */
  enterRoomParams: EnterRoomParams | null,
  delays: { [type in UpdateType]?: number } | null,
  /**
   * Async function to process and update. The function should use
   * isStale after every async step to check if it should exit
   * early.
   */
  processUpdateAsync: (upd: Update, isStale: () => boolean) => Promise<void>,
  /** Process multiple updates at once, synchronously. */
  processUpdatesSync: (upds: Update[], isStale: () => boolean) => Promise<void>
) => {
  const serverInterfaceController = useServerInterfaceController();
  const setGameState = useClientGameStore((state) => state.setGameState);

  useEffect(() => {
    if (enterRoomParams === null) return;
    const cleanup = serverInterfaceController.addEnterRoomEffect(
      enterRoomParams,
      (upd, isStale) => {
        return (async () => {
          // timer for timeout
          const { speed: speedPref } = useUserPreferencesStore.getState();
          const update = processUpdateAsync(upd, isStale);

          const { subscribedTeam, subscribedTeamBattle } =
            useClientGlobalStateStore.getState().globalState;
          const roomPuzName = subscribedTeamBattle?.roomPuzName ?? null;
          const roomBattleGroup = getBattleGroup(subscribedTeam, roomPuzName);
          const roomBattleGroupName = roomBattleGroup?.battleGroupName ?? null;
          const isGameSettingsEnabled = getIsGameSettingsEnabled(
            roomPuzName,
            roomBattleGroupName
          );
          const speed = isGameSettingsEnabled ? speedPref : 1;

          // wait for both timeout and update, unless speed is 0
          if (speed === 0) {
            await update;
          } else {
            const timeout = (delays?.[upd.type] ?? 0) / speed;
            const timer = new Promise((res) => setTimeout(res, timeout));
            await Promise.all([timer, update]);
          }

          if (isStale()) return;
          serverInterfaceController.setClientDoneApplyingUpdate();
        })();
      },
      processUpdatesSync
    );
    return () => {
      cleanup();
      setGameState(makeInitGameState("vanilla", {}));
    };
  }, [
    serverInterfaceController,
    enterRoomParams,
    delays,
    processUpdateAsync,
    setGameState,
    processUpdatesSync,
  ]);
};
