export const DeleteTags = {
  TUSK1: "tusk1",
  TUSK2: "tusk2",
  ANTLERS: "antlers",
  WINGS: "wing",
  BLACK_LEG1: "blackLeg1",
  BLACK_LEG2: "blackLeg2",
  BLACK_LEG3: "blackLeg3",
  BLACK_LEG4: "blackLeg4",
  BLACK_LEG5: "blackLeg5",
  UDDERS: "udders",
  WHITE_LEG1: "whiteLeg1",
  WHITE_LEG2: "whiteLeg2",
  WHITE_LEG3: "whiteLeg3",
  WHITE_LEG4: "whiteLeg4",
  DINO_TAIL: "dinoTail",
};

const IMAGE_PATH = "/static/kerodos/";

interface BodyPart {
  name: string;
  zIncr?: number;
  tag?: string;
  isU?: boolean;
  isW?: boolean;
}

type BodyParts = BodyPart[];

const bodySingle: BodyParts = [{ name: "bodysingle.png" }];

const bodySegment: BodyParts = [
  { name: "body1.png" },
  { name: "body2.png" },
  { name: "body3.png" },
  { name: "body4.png" },
  { name: "body5.png" },
  { name: "body6.png" },
  { name: "body7.png" },
  { name: "body8.png" },
  { name: "body9.png" },
  { name: "body10.png" },
];

const head: BodyParts = [
  { name: "head1.png", zIncr: 1 },
  { name: "w.png", zIncr: 0, isW: true },
  { name: "tusk1.png", zIncr: 0, tag: DeleteTags.TUSK1 },
  { name: "tusk2.png", zIncr: 0, tag: DeleteTags.TUSK2 },
  { name: "head2.png", zIncr: 1 },
  { name: "head3.png", zIncr: 1 },
  { name: "u.png", zIncr: 0, isU: true },
  { name: "head4.png", zIncr: 1 },
  { name: "head5.png", zIncr: 1 },
  { name: "head6.png", zIncr: 1 },
  { name: "antler1.png", zIncr: 0, tag: DeleteTags.ANTLERS },
  { name: "antler2.png", zIncr: 0, tag: DeleteTags.ANTLERS },
  { name: "head7.png", zIncr: 1 },
  { name: "head8.png", zIncr: 1 },
  { name: "head9.png", zIncr: 1 },
  { name: "head10.png", zIncr: 1 },
];

const frontWing: BodyParts = [
  { name: "body1.png" },
  { name: "body2.png" },
  { name: "body3.png" },
  { name: "body4.png" },
  { name: "body5.png" },
  { name: "leftwing1.png", zIncr: 0 },
  { name: "rightwing1.png", zIncr: 0 },
];

const backWing: BodyParts = [
  { name: "leftwing2.png", zIncr: 0 },
  { name: "rightwing2.png", zIncr: 0 },
  { name: "body6.png" },
  { name: "body7.png" },
  { name: "body8.png" },
  { name: "body9.png" },
  { name: "body10.png" },
];

const udders: BodyParts = [{ name: "udders.png", zIncr: 0 }];

const blackLeg: BodyParts = [{ name: "leg1.png", zIncr: 0 }];
const whiteLeg: BodyParts = [{ name: "leg2.png", zIncr: 0 }];

const fishTail: BodyParts = [
  { name: "tail1.png", zIncr: 1 },
  { name: "tail2.png", zIncr: 1 },
  { name: "tail3.png", zIncr: 1 },
  { name: "tail4.png", zIncr: 1 },
  { name: "tail5.png", zIncr: 1 },
  { name: "tail6.png", zIncr: 1 },
];

const fps = 60,
  speed = 1 / 500,
  circleWidth = 360,
  circleHeight = 120,
  // Negative half the part image width to center.
  keroX = -111,
  keroY = 200;

interface KeroPartExtraInfo {
  fadeCounter?: number;
  fadeEnable?: boolean;
  zCounter?: number;
  z?: number;
  zIncr?: number;
  deleteTag?: string;
  zDecrement?: number;
}

export class KeroFishyAnim {
  private hasInited = false;
  private internalTime = 0;
  private lastFrameTime = 0;
  private recoilCounter = 0;
  private z = 0;
  private isSadFishy = false;
  private isDed = false;
  private mustStopLoop = false;
  private parent: HTMLDivElement | undefined = undefined;
  private imgElement: HTMLImageElement | undefined = undefined;
  private fishyElement: HTMLDivElement | undefined = undefined;
  private pendingAnimationFrame: number | undefined = undefined;
  private phase = 0;
  private parts: (HTMLImageElement & KeroPartExtraInfo)[] = [];
  private partsLeft = Object.values(DeleteTags);

  init(parentParam: HTMLDivElement, fishyElement: HTMLDivElement, phase = 1) {
    if (this.hasInited) return;
    this.phase = phase;
    this.hasInited = true;
    this.parent = parentParam;
    this.fishyElement = fishyElement;
    this.z = 0;

    this.addPart(head, "head");
    if (phase <= 4) {
      this.addPart(bodySegment, DeleteTags.BLACK_LEG1);
      this.addPart(blackLeg, DeleteTags.BLACK_LEG1);
      this.addPart(whiteLeg, DeleteTags.WHITE_LEG1);
      this.addPart(bodySegment, DeleteTags.WHITE_LEG1);
    }
    if (phase <= 5) {
      this.addPart(frontWing, DeleteTags.WINGS);
    }
    this.addPart(bodySingle, "body");
    if (phase <= 5) {
      this.addPart(backWing, DeleteTags.WINGS);
    }
    if (phase <= 4) {
      this.addPart(bodySegment, DeleteTags.BLACK_LEG2);
      this.addPart(blackLeg, DeleteTags.BLACK_LEG2);
      this.addPart(whiteLeg, DeleteTags.WHITE_LEG2);
      this.addPart(bodySegment, DeleteTags.WHITE_LEG2);
      this.addPart(bodySegment, DeleteTags.UDDERS);
      this.addPart(udders, DeleteTags.UDDERS);
      this.addPart(bodySegment, DeleteTags.UDDERS);
      this.addPart(bodySegment, DeleteTags.BLACK_LEG3);
      this.addPart(blackLeg, DeleteTags.BLACK_LEG3);
      this.addPart(whiteLeg, DeleteTags.WHITE_LEG3);
      this.addPart(bodySegment, DeleteTags.WHITE_LEG3);
      this.addPart(bodySegment, DeleteTags.BLACK_LEG4);
      this.addPart(blackLeg, DeleteTags.BLACK_LEG4);
      this.addPart(whiteLeg, DeleteTags.WHITE_LEG4);
      this.addPart(bodySegment, DeleteTags.WHITE_LEG4);
      this.addPart(bodySegment, DeleteTags.BLACK_LEG5);
      this.addPart(blackLeg, DeleteTags.BLACK_LEG5);
    }
    if (phase <= 1) {
      this.addDinoTail();
    }
    this.addPart(fishTail, "fishTail");

    this.internalTime = Date.now();

    this.pendingAnimationFrame = requestAnimationFrame(() => this.loop());
    if (phase >= 6) {
      this.partsLeft = [];
      this.turnIntoSadFishy();
    }
    if (phase >= 7) {
      this.turnIntoDedFishy();
    }
  }

  calculatePosition(index: number) {
    const recoil = this.piecewiseFn(this.recoilCounter);
    const x =
      Math.cos(
        ((this.internalTime - ((index + recoil) * 1000) / fps) * speed) / 2 +
          Math.PI / 4
      ) *
        circleWidth +
      keroX;
    const y =
      Math.cos((this.internalTime - ((index + recoil) * 1000) / fps) * speed) *
        circleHeight +
      keroY;
    return { x: x, y: y };
  }

  reInit(phase: number) {
    this.hasInited = false;
    if (this.pendingAnimationFrame)
      cancelAnimationFrame(this.pendingAnimationFrame);
    this.imgElement = undefined;
    this.internalTime = 0;
    this.lastFrameTime = 0;
    this.recoilCounter = 0;
    this.z = 0;
    this.isSadFishy = false;
    this.isDed = false;
    this.mustStopLoop = false;
    this.parts = [];
    this.partsLeft = Object.values(DeleteTags);
    if (this.parent && this.fishyElement) {
      while (this.parent.lastChild) {
        this.parent.removeChild(this.parent.lastChild);
      }
      while (this.fishyElement.lastChild) {
        this.fishyElement.removeChild(this.fishyElement.lastChild);
      }
      this.init(this.parent, this.fishyElement, phase);
    }
  }

  addPart(part: BodyParts, tag: string, scale = 1) {
    if (!this.parent) return;
    for (const subpart of part) {
      const img = document.createElement("img") as HTMLImageElement &
        KeroPartExtraInfo;
      if (subpart.zIncr !== undefined) {
        this.z += subpart.zIncr;
        img.zIncr = subpart.zIncr;
      } else {
        this.z += 2;
      }
      img.z = this.z;
      img.classList.add("kerodos-anim-kero-part");
      img.style.zIndex = `${-this.parts.length}`;
      if (scale != 1) {
        img.style.transform = `scale(${scale})`;
      }
      img.src = IMAGE_PATH + subpart.name;
      this.parent.appendChild(img);
      if (subpart.tag) {
        img.deleteTag = subpart.tag;
      } else {
        img.deleteTag = tag;
      }

      const isU = subpart.isU ?? false;
      const isW = subpart.isW ?? false;
      if (isU) {
        img.id = "kerodos-anim-u";
      } else if (isW) {
        img.id = "kerodos-anim-w";
      }
      if (isU || isW) {
        img.classList.add("kerodos-anim-come-in-hot");
      } else {
        img.classList.add("kerodos-anim-slow-fade-in");
      }
      this.parts.push(img);
    }
  }

  addDinoTail() {
    for (let i = 1; i > 0; i -= 0.025) {
      this.addPart(bodySingle, DeleteTags.DINO_TAIL, i);
    }
  }

  recoilFn(x: number) {
    return (
      30 *
      (Math.sin(x / 20 - (Math.PI * 3) / 8) -
        x / (20 * Math.PI) -
        Math.sin((-Math.PI * 3) / 8))
    );
  }

  piecewiseFn(x: number) {
    if (x < 49) {
      return this.recoilFn(x);
    }
    if (x < 69) {
      // pause at the back of the recoil for dramatic effect
      return this.recoilFn(49.5);
    }
    if (x < 100) {
      return this.recoilFn(x - 20);
    }
    return 0;
  }

  loop() {
    this.pendingAnimationFrame = undefined;
    if (!this.parent?.isConnected) return;
    if (!this.mustStopLoop) {
      this.pendingAnimationFrame = requestAnimationFrame(() => this.loop());
    }
    const now = Date.now();
    if (now - this.lastFrameTime < 1000 / fps) return;
    this.lastFrameTime = now;

    if (this.recoilCounter > 0) {
      // Don't move forward when recoiling
      this.recoilCounter--;
      if (this.recoilCounter == 0) {
        this.endRecoil();
      }
    } else {
      // Stop internal time during recoil to prevent going forwards
      this.internalTime += 1000 / fps;
    }

    for (let i = 0; i < this.parts.length; i++) {
      // When we delete a part, we keep it moving for a while so we can fade
      // it out and prevent a jarring transition. We also gradually shift the
      // following parts forward to fill the gap. There's probably a better
      // way to do this.
      if (this.parts[i].fadeCounter) {
        this.parts[i].fadeCounter = (this.parts[i].fadeCounter ?? 0) - 1;
        this.parts[i].style.opacity = `${
          (this.parts[i].fadeCounter ?? 0) / 100
        }`;
        if (this.parts[i].fadeCounter == 0) this.parts[i].remove();
      }
      if (this.parts[i].zCounter) {
        this.parts[i].z =
          (this.parts[i].z ?? 0) -
          (this.parts[i].zDecrement ?? 0) / (this.parts[i].zCounter ?? 1);
        this.parts[i].zDecrement =
          (this.parts[i].zDecrement ?? 0) -
          (this.parts[i].zDecrement ?? 0) / (this.parts[i].zCounter ?? 1);
        this.parts[i].zCounter = (this.parts[i].zCounter ?? 1) - 1;
      }
      const index = Math.max(0, Math.round(this.parts[i].z ?? 0));
      const position = this.calculatePosition(index);
      this.parts[i].style.left = `${position.x}px`;
      this.parts[i].style.top = `${position.y}px`;
    }
  }

  startRecoil() {
    this.recoilCounter = 100;
    const u = document.getElementById("kerodos-anim-u") as HTMLImageElement;
    const w = document.getElementById("kerodos-anim-w") as HTMLImageElement;
    if (u) u.src = IMAGE_PATH + "hurt-u.png";
    if (w) w.src = IMAGE_PATH + "hurt-w.png";
  }

  endRecoil() {
    const u = document.getElementById("kerodos-anim-u") as HTMLImageElement;
    const w = document.getElementById("kerodos-anim-w") as HTMLImageElement;
    if (u) u.src = IMAGE_PATH + "u.png";
    if (w) w.src = IMAGE_PATH + "w.png";
  }

  /**
   * When Kero Dos loses all his extra powers, he turns back into kero uno.
   */
  turnIntoSadFishy() {
    this.isSadFishy = true;
    // remove all remaining parts
    for (let i = 0; i < this.parts.length; i++) {
      if (this.parts[i].fadeEnable == true) continue;
      this.parts[i].fadeEnable = true;
      this.parts[i].fadeCounter = 100;
    }
    // Add sad fishy
    this.imgElement = document.createElement("img");
    this.imgElement.classList.add("kerodos-anim-kero-part");
    this.imgElement.classList.add("kerodos-anim-fade-in");
    this.imgElement.src = IMAGE_PATH + "sadfishy.gif";
    if (this.fishyElement) this.fishyElement.appendChild(this.imgElement);
  }

  /**
   * Only works on sad fishy. Whem sad fishy dies Kero Dos vanishes without a tres.
   */
  turnIntoDedFishy() {
    if (!this.isSadFishy || this.isDed) return;
    this.isDed = true;
    if (this.imgElement) {
      this.imgElement.src = IMAGE_PATH + "ded.png";
      this.imgElement.classList.remove("kerodos-anim-fade-in");
      this.imgElement.classList.add("kerodos-anim-fade-out");
    }
  }

  deletePart(tag: string) {
    this.startRecoil();
    let counter = 0;
    for (let i = 0; i < this.parts.length; i++) {
      if (this.parts[i].deleteTag != tag || this.parts[i].fadeEnable == true)
        continue;
      let z: number | undefined = 2;
      if (this.parts[i].zIncr !== undefined) z = this.parts[i].zIncr;
      this.parts[i].fadeEnable = true;
      this.parts[i].style.rotate = `${Math.floor(Math.random() * 360)}deg`;
      this.parts[i].classList.remove("kerodos-anim-slow-fade-in");
      if (this.parts[i].src.includes("body")) {
        this.parts[i].classList.add("kerodos-anim-splody");
        this.parts[i].style.animationDelay = `${counter++ * 50}ms`;
      } else {
        this.parts[i].classList.add("kerodos-anim-spinny");
      }
      this.parts[i].fadeCounter = 100;
      for (let j = i; j < this.parts.length; j++) {
        this.parts[j].zDecrement = (this.parts[j].zDecrement || 0) + (z ?? 0);
        this.parts[j].zCounter = 100;
      }
    }
  }

  updateState(state: { parts: Set<string>; phase: number }) {
    const { parts, phase } = state;
    if (phase < this.phase) {
      // moved backwards in time
      // (restart or checkpoint);
      this.reInit(phase);
      return;
    }

    this.phase = phase;

    if (this.isSadFishy && parts.size == 0) {
      this.turnIntoDedFishy();
      return;
    }

    for (let i = 0; i < this.partsLeft.length; i++) {
      if (!parts.has(this.partsLeft[i])) {
        this.deletePart(this.partsLeft[i]);
        this.partsLeft.splice(i, 1);
        i--;
      }
      if (this.partsLeft.length == 0) this.turnIntoSadFishy();
    }
  }
}
