import settings from "settings";
import { Player, DevKnobs, makeInitGameState } from "engine/types/game-state";
import { Role } from "engine/types/updates";
import { getClientGameSpec } from "engine/all-client-game-specs";
import { Inspector } from "engine/Inspector";
import { AutoPrepOpts } from "game-server/server-interface/ServerInterfaceController";
import { EnterRoomParams } from "game-server/Room";
import { MasteryTree } from "game-server/masteries";
import { getAllClientSpecs } from "stores/ClientGameStore";

const cardListToCardDict = (
  cardList: ReadonlyArray<string>
): { [cardName: string]: number } => {
  const cardDict: { [cardName: string]: number } = {};
  for (const cardName of cardList) {
    if (cardDict[cardName] === undefined) {
      cardDict[cardName] = 0;
    }
    cardDict[cardName]++;
  }
  return cardDict;
};

export const getDevKnobs = (): DevKnobs => {
  return settings.devKnobs;
};

/**
 * Generate autoprep opts for the initial room join.
 * Autoprep is a dev-only feature used to "fast-forward" to a
 * useful state for testing or testsolving.
 */
export const getInitAutoPrepOpts = (
  teamId: string,
  params: EnterRoomParams
): AutoPrepOpts => {
  const overrideMasteryTrees: { [teamId: string]: MasteryTree[] } = {
    [teamId]: [],
  };
  const timeNow = new Date().getTime();
  const requestedDeck =
    settings.deck === null
      ? undefined
      : {
          id: "__urlparams_deck",
          displayName: "deck from url params",
          cards: cardListToCardDict(settings.deck),
        };
  const requestedOppDeck =
    settings.oppDeck === null
      ? undefined
      : {
          id: "__urlparams_oppdeck",
          displayName: "deck from url params",
          cards: cardListToCardDict(settings.oppDeck),
        };
  if (settings.masteries !== null)
    overrideMasteryTrees[teamId].push({
      placements: {},
      connectedAnswers: [],
      masteriesListOverride: [...settings.masteries],
    });
  if (settings.oppMasteries !== null)
    overrideMasteryTrees[teamId].push({
      placements: {},
      connectedAnswers: [],
      masteriesListOverride: [...settings.oppMasteries],
    });

  const { role, puzName } = params;

  const gameState = makeInitGameState(puzName, getClientGameSpec(puzName));
  const allSpecs = getAllClientSpecs(puzName);
  const gameSpec = allSpecs[1];
  const inspector = new Inspector(gameSpec, allSpecs, {}, {}, gameState);
  const hasAI = inspector.hasAI();
  const p1HasDeckOverride =
    inspector.isAllCardsAvailable() ||
    inspector.getDeckOverride(Player.P1) !== null;
  const p2HasDeckOverride =
    inspector.isAllCardsAvailable() ||
    inspector.getDeckOverride(Player.P2) !== null;

  return {
    readyForBattlePuzName: settings.skipBattlePrep ? puzName : undefined,
    role,
    devKnobs: getDevKnobs(),
    decks: {
      [Player.P1]:
        role !== undefined && role === Role.P2
          ? requestedOppDeck
          : requestedDeck,
      [Player.P2]:
        role !== undefined && role === Role.P2
          ? requestedDeck
          : requestedOppDeck,
    },
    masteryTrees: {
      [Player.P1]: "default",
      [Player.P2]: "default",
    },
  };
};
