import { useEffect, useMemo, useRef, memo } from "react";
import { useClientGameStore } from "stores/ClientGameStore";
import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import { getKeroLayersForFinalBattle } from "engine/cards/card-effects-shared";
import { Puzzle76SharedExtraState } from "engine/game-specs/puzzle-76-shared";
import "/static/kerodos/kerodosAnim.css";
import { KeroFishyAnim } from "./KeroFishyAnim";

const KerodosAnimInternal = () => {
  const gameState = useClientGameStore((state) => state.gameState);
  const subscribedTeam = useSubscribedTeam();

  const keroPhase =
    // There shouldn't be race conditions but put in contingencies
    // just to be safe...
    (gameState.sharedExtraState as Puzzle76SharedExtraState)?.keroPhase ?? 0;

  const keroLayers = useMemo(
    () => getKeroLayersForFinalBattle(keroPhase),
    [keroPhase]
  );

  const fishyAnimRef = useRef(new KeroFishyAnim());

  useEffect(() => {
    fishyAnimRef.current.init(
      document.getElementById("kerodos-anim") as HTMLDivElement,
      document.getElementById("kerodos-fishy") as HTMLDivElement
    );
  }, []);

  useEffect(() => {
    fishyAnimRef.current.updateState(keroLayers);
  }, [keroLayers]);

  if (subscribedTeam === null) return null;

  return (
    <>
      <div id="kerodos-fishy" />
      <div id="kerodos-anim-bg">
        <div id="kerodos-anim-clip">
          <div id="kerodos-anim"></div>
        </div>
      </div>
    </>
  );
};

const KerodosAnim = memo(KerodosAnimInternal);

export default KerodosAnim;
