import "./DeckSelection.css";
import { useNavigate, useSearchParams } from "react-router-dom";
import Twemoji from "react-twemoji";

import { createNewDeck, Deck } from "engine/types/decks";
import { Player } from "engine/types/game-state";
import {
  Check,
  FailedChecks,
  getFailText,
} from "engine/types/action-validation";
import { Role } from "engine/types/updates";
import { WSReqType } from "game-server/ws";
import { useStaticInspector } from "stores/ClientGameStore";
import {
  useSelectedPuzName,
  useSelectedDeckData,
  useSubscribedTeam,
  useSubscribedTeamBattle,
  useSubscribedBattleStatus,
} from "stores/ClientGlobalStateStore";
import { useNavStore, usePuzName } from "stores/NavStore";
import { useServerInterfaceController } from "stores/ServerInteractionStore";
import { useCallback, useMemo } from "react";
import CardDisplay, { CardSize } from "components/CardDisplay";
import { PriorityCard } from "components/DeckCollection";
import Tooltip from "components/Tooltip";
import { PRIORITY_CARD_MASTERY_ID } from "engine/puzzles/mastery-data";
import {
  PUZ_NAME_PVP,
  PUZ_NAME_TUTORIAL_DECKBUILDING,
} from "engine/puzzles/puzzle-data";
import { GlobalStateBattleRoomStatus } from "game-server/global-updates";

interface DeckListDeckProps {
  player: Player;
  deck: Deck;
  slot: number;
  isLocked: boolean;
  isDisabled: boolean;
  secondaryHighlight: boolean;
  highlight: boolean;
  context: string;
}

function DeckListDeck(props: DeckListDeckProps) {
  const {
    deck,
    slot,
    player,
    isLocked,
    isDisabled,
    secondaryHighlight,
    highlight,
    context,
  } = props;

  const [searchParams] = useSearchParams();
  const inspector = useStaticInspector();
  const navigate = useNavigate();
  const puzName = usePuzName();
  const selectedPuzName = useSelectedPuzName();
  const isPvP = puzName === PUZ_NAME_PVP;
  const serverInterfaceController = useServerInterfaceController();
  const setHoveredDeck = useNavStore((state) => state.setHoveredDeck);

  const isEmpty = useMemo(() => Object.keys(deck.cards).length === 0, [deck]);

  const failText = useMemo(() => {
    const failedChecks: FailedChecks = new Set();
    inspector.validateDeck(failedChecks, player, deck);
    const text = getFailText(failedChecks);
    if (
      isPvP &&
      text === getFailText(new Set([Check.DECK_TOO_MANY_LEGENDARIES]))
    ) {
      return "Legendary cards aren‚Äôt allowed in PvP.";
    }
    return text;
  }, [deck, inspector, isPvP, player]);

  const onSelect = useCallback(() => {
    if (puzName === null) return null;
    serverInterfaceController.sendReq({
      type: WSReqType.SELECT_DECK_FOR_BATTLE,
      puzName,
      slot,
    });
  }, [puzName, serverInterfaceController, slot]);

  const className = `deck-list-deck
    ${highlight ? "highlight" : secondaryHighlight ? "secondary-highlight" : ""}
    ${isEmpty ? "empty" : ""}
    ${failText ? "invalid" : ""}`;

  const onEdit = useCallback(() => {
    setHoveredDeck(null);
    navigate({
      pathname: `../deck/${slot}/${puzName}`,
      search: searchParams.toString(),
    });
  }, [navigate, puzName, searchParams, setHoveredDeck, slot]);

  // Don't display a deck selection if no puzzle is selected in
  // the instancer.
  if (selectedPuzName === null) return null;

  return (
    <div
      className={className}
      onMouseEnter={() => setHoveredDeck(deck)}
      onMouseLeave={() => setHoveredDeck(null)}
    >
      <button
        className="deck-list-deck-btn"
        disabled={isDisabled}
        onClick={context == "prep" ? onSelect : onEdit}
      >
        {deck.displayName}
      </button>
      {!isLocked ? null : (
        <Tooltip text="Deck is locked; changes won't affect the current battle.">
          <button className="no-click" disabled>
            <Twemoji>üîí</Twemoji>
          </button>
        </Tooltip>
      )}
      {!failText || isEmpty ? null : (
        <Tooltip text={failText}>
          <button className="no-click" disabled>
            <Twemoji>‚ùå</Twemoji>
          </button>
        </Tooltip>
      )}
      {!isEmpty ? null : (
        <Tooltip text="Deck is empty.">
          <button className="no-click" disabled>
            <Twemoji>üì≠</Twemoji>
          </button>
        </Tooltip>
      )}
      {context != "prep" ? null : (
        <Tooltip text="Edit deck.">
          <button className="deck-list-deck-edit" onClick={onEdit}>
            <Twemoji>‚úèÔ∏è</Twemoji>
          </button>
        </Tooltip>
      )}
    </div>
  );
}

interface DisplayedDeckProps {
  player: Player;
  canSelectDeck: boolean;
  showPriorityCard: boolean;
}

function DisplayedDeck(props: DisplayedDeckProps) {
  const { player, showPriorityCard } = props;
  const { selectedDeck = null } = useSelectedDeckData(player) ?? {};
  const hoveredDeck = useNavStore((state) => state.hoveredDeck);

  const displayedDeck = hoveredDeck ?? selectedDeck;

  return (
    <div className="prep-cards">
      {displayedDeck === null ? (
        "No deck selected."
      ) : (
        <>
          {showPriorityCard ? (
            <PriorityCard priorityCard={displayedDeck?.priorityCard} />
          ) : null}
          {[...Object.keys(displayedDeck.cards)].sort().map((cardName) => {
            return (
              <div key={cardName} className="prep-card-display">
                <div className="prep-card-display-quantity">
                  {displayedDeck.cards[cardName]}√ó
                </div>
                <CardDisplay size={CardSize.SMALL} cardName={cardName} />
              </div>
            );
          })}
        </>
      )}
    </div>
  );
}

interface DeckListProps {
  player: Player;
  disabledExcept: number | null;
  lockedDeckSlot: number | null;
  highlightedDeckSlot: number | null;
  secondaryHighlightedDeckSlot: number | null;
  context: string;
}

export function DeckList(props: DeckListProps) {
  const {
    player,
    disabledExcept,
    highlightedDeckSlot,
    secondaryHighlightedDeckSlot,
    lockedDeckSlot,
    context,
  } = props;
  const { decks } = useSelectedDeckData(player) ?? {};

  return (
    <div className="deck-list">
      {decks
        ? decks.map((optDeck, slot) => {
            const deck = optDeck ?? createNewDeck(slot);
            return (
              <DeckListDeck
                key={slot}
                player={player}
                deck={deck}
                slot={slot}
                isDisabled={disabledExcept !== null && disabledExcept !== slot}
                isLocked={lockedDeckSlot !== null && lockedDeckSlot === slot}
                highlight={
                  highlightedDeckSlot !== null && highlightedDeckSlot === slot
                }
                secondaryHighlight={
                  secondaryHighlightedDeckSlot !== null &&
                  secondaryHighlightedDeckSlot === slot
                }
                context={context}
              />
            );
          })
        : "Decks loading..."}
    </div>
  );
}

interface DeckSelectionProps {
  role: Role;
  player: Player;
}

function DeckSelection(props: DeckSelectionProps) {
  const { role, player } = props;
  const selectedPuzName = useSelectedPuzName();
  const subscribedTeam = useSubscribedTeam();
  const subscribedTeamBattle = useSubscribedTeamBattle();
  const {
    canSelectDeck = false,
    selectedDeckSlot = null,
    isDeckLocked = false,
  } = useSelectedDeckData(player) ?? {};
  const puzName = usePuzName();
  const battleStatus = useSubscribedBattleStatus(puzName);

  const { activeGame } = subscribedTeamBattle ?? {};
  const showPriorityCard = activeGame
    ? activeGame.enabledMasteries[PRIORITY_CARD_MASTERY_ID] ?? false
    : subscribedTeam?.masteries[PRIORITY_CARD_MASTERY_ID].isEnabled ?? false;

  // Don't display a deck selection if no puzzle is selected in
  // the instancer.
  if (selectedPuzName === null) return null;
  if (!canSelectDeck) return null;

  return (
    <>
      <DisplayedDeck
        player={player}
        canSelectDeck={canSelectDeck}
        showPriorityCard={showPriorityCard}
      />
      <DeckList
        player={player}
        disabledExcept={
          isDeckLocked
            ? selectedDeckSlot
            : // HACK: The deckbuilding tutorial accepts no deck, so
            // it needs to disable all decks even though none of
            // them are locked.
            battleStatus === GlobalStateBattleRoomStatus.ACTIVE &&
              selectedPuzName === PUZ_NAME_TUTORIAL_DECKBUILDING
            ? -1
            : null
        }
        highlightedDeckSlot={selectedDeckSlot}
        secondaryHighlightedDeckSlot={null}
        lockedDeckSlot={isDeckLocked ? selectedDeckSlot : null}
        context="prep"
      />
    </>
  );
}

export default DeckSelection;
