import { useState } from "react";
import Twemoji from "react-twemoji";

import {
  Faction,
  capitalize,
  FACTION_RANK_THRESHOLDS,
  getFactionRank,
  factionToColor,
} from "engine/types/factions";
import { SYMBOL_INFO } from "engine/types/card-data";
import { FACTIONS_MASTERY_ID } from "engine/puzzles/mastery-data";
import { WSReqType } from "game-server/ws";
import { TeamSummaryState } from "game-server/monitoring";

import { useServerInteractionStore } from "stores/ServerInteractionStore";
import { useSubscribedTeam } from "stores/ClientGlobalStateStore";
import FactionIcon from "components/FactionIcon";
import { FactionRankDisplay } from "components/TeamDisplayNameDisplay";
import { Spinner } from "components/Loading";

import "./SelectFactionDisplay.css";

function SelectFactionDisplay({ teamData }: { teamData: TeamSummaryState }) {
  const subscribedTeam = useSubscribedTeam();
  const serverInterfaceController = useServerInteractionStore(
    (state) => state.getServerInterfaceController
  )();
  const [userSelectedFaction, setSelectedFaction] = useState<Faction | null>(
    null
  );

  if (subscribedTeam === null || serverInterfaceController === null)
    return <Spinner inline={true} />;

  const {
    faction: teamFaction,
    factionScoreContributions,
    masteries,
  } = subscribedTeam;

  if (!masteries[FACTIONS_MASTERY_ID].isEnabled) return null;

  const isMyTeam = teamData.teamId === subscribedTeam.teamId;
  const selectedFaction = isMyTeam
    ? userSelectedFaction ?? teamFaction
    : teamData.faction;
  const selectedFactionScore = isMyTeam
    ? selectedFaction === null
      ? 0
      : factionScoreContributions[selectedFaction]
    : teamData.factionScoreContribution;
  const selectedFactionRank = getFactionRank(selectedFactionScore);
  const nextRankThreshold =
    FACTION_RANK_THRESHOLDS[selectedFactionRank] ?? null;
  const factionColor = (faction: Faction, level: number) =>
    SYMBOL_INFO[factionToColor(faction)].color(level);

  const factionsDom = Object.values(Faction).map((faction) => {
    const isSelected = selectedFaction === faction;
    return (
      <h2
        key={faction}
        className={`select-faction-faction${isSelected ? " highlight" : ""}${
          isMyTeam ? " selectable" : ""
        }`}
        onClick={
          isMyTeam
            ? () =>
                setSelectedFaction(faction === selectedFaction ? null : faction)
            : undefined
        }
        style={{ backgroundColor: factionColor(faction, 600) }}
      >
        <FactionIcon faction={faction} />
        {capitalize(faction)}
      </h2>
    );
  });
  const factionDetailsDom = (() => {
    if (selectedFaction === null)
      return (
        <>
          <p>
            You can align with a faction. When you do, any points you earn from
            battle also get added to your faction&apos;s points. The more points
            you earn for your faction, the higher your faction rank, and the
            higher your rank, the more icons appear next to your team name!
          </p>
          <p>
            (You can change your faction any time you have the Spark of
            Inspiration mastery activated, but points you gain for one faction
            don&apos;t carry over to another.)
          </p>
        </>
      );
    switch (selectedFaction) {
      case Faction.BOARS:
        return (
          <>
            <p>
              Dryads? Dinos? RUBBISH! All those BACKWARDS TOP-DWELLERS do is SIT
              on their BUMS all day and take up precious CAVE-SPACE instead of
              PUTTING &apos;em LAND to WORK! Now down here, we do things RIGHT!
              Geyser pumps at every corner, hydroboarnics UP to the CEILING,
              AUTOMATION EVERYWHERE because we KNOW what MATTERS and that&apos;s
              EFFICIENCY! And BOARRIES!
            </p>
            <p>
              So what&apos;s there to CHOOSE? STEAM &apos;em PUMPS, CRANK
              &apos;em GEARS, DEFEND our BOARDER from those dastardly BEES
              because the CAVE needs BOAR and BOAR needs YOU!
            </p>
          </>
        );
      case Faction.COWS:
        return (
          <>
            <p>
              The Cow Faction has little need for outsiders, of course, but in
              times like these we&apos;re willing to make some exceptions. See,
              word has come by that a sinister force threatens the safety of
              this cave. Naturally, this cave must be protected, yet on whom
              else but us Cows would this duty befall?
            </p>
            <p>
              Obviously, the other paltry inhabitants of this cave remain
              ignorant of this threat. Not that they would make much of a
              difference, clearly, but they do like to get in our way. Aid us,
              and perhaps you might be worth enough to join us for
              cheese-tasting someday.
            </p>
            <p style={{ textAlign: "right" }}>
              ‚Äî Signed, Grand Secretariat of the Bovine Folk, Head Production
              Manager of the Western and Southern Creameries, Founder of the
              Diplomatic Association of Pasture Enlargement and Other
              Cave-Related Matters, Director of the Critically Acclaimed Moovie
              &ldquo;Herd: A Cattle Story&rdquo; and Ambassador to the Royal Cow
              Academy, Butterton Moo.
            </p>
          </>
        );
      case Faction.BEES:
        return (
          <>
            <p>
              Hey, thanks for buzzzzing by! Here at the hive, we&apos;re all
              about the A Bee Cs and the 1 2 Trees! Yes, T, R, E, E, S! You
              won&apos;t beelieve how much H, O, N, E, Y drips off the trees
              every day, it&apos;s a struggle buggle to collect &apos;em all!
              Anyway, I gotta zip off, there&apos;s just so much H, O, N, E, Y
              to collect! Hope to seeya around!
            </p>
            <p>
              What, flowers? Oh, you&apos;ll wanna ask the D, R, Y, A, Ds for
              that. We don&apos;t care much for flowers around here!
            </p>
          </>
        );
      case Faction.DINOSAURS:
        return (
          <>
            <p>
              I&apos;ll have you know that this cave ain&apos;t what it used to
              be! Back in the day, when we still called the shots, things made a
              mite more sense. We&apos;d divvy up the Foods &apos;mongst the
              Factions, and, if you can picture it, we had more Pie to go around
              than you could shake a stick at!
            </p>
            <p>
              But those days have gone the way of the dodo, and now all them
              young bucks know how to do is bicker and squabble, and them cows,
              oh my stars! Struttin&apos; round like it ain&apos;t nobody&apos;s
              business, not a lick of respect for us Dinosaurs who busted our
              tails to build this place up. If you fancy yourselves any better,
              join us and help get this cave back up to its former glory!
            </p>
          </>
        );
      case Faction.DRYADS:
        return (
          <>
            <Twemoji tag="p">
              ...shrieks, cries... ...corruption spreads... üçÇ ...the flowers
              see... üåº ...the trees whisper...
            </Twemoji>
            <Twemoji tag="p">
              ...an ancient evil... ...a last stand... üåµ ...death,
              destruction... ...our children, slaves... ü•Ä
            </Twemoji>
            <Twemoji tag="p">
              ...must... ...resist... üå∫ ...help accepted...
            </Twemoji>
          </>
        );
    }
  })();
  const factionActionDom = (() => {
    if (!isMyTeam) {
      if (selectedFaction === null)
        return "This team is not aligned with a faction yet.";
      return `This team is currently aligned with the ${capitalize(
        selectedFaction
      )}.`;
    }
    if (selectedFaction !== null && selectedFaction !== teamFaction)
      return (
        <a
          className="btn"
          href="#"
          onClick={(evt) => {
            serverInterfaceController.sendReq({
              type: WSReqType.SET_FACTION,
              faction: selectedFaction,
            });
            evt.preventDefault();
          }}
        >
          Align with the {capitalize(selectedFaction)}
        </a>
      );
    if (teamFaction === null)
      return "You‚Äôre not aligned with a faction yet. Select a faction to learn more!";
    return `You are currently aligned with the ${capitalize(teamFaction)}.`;
  })();
  const factionScoreDom = (() => {
    if (selectedFaction === null || selectedFactionScore === 0) return null;
    return (
      <>
        <div>
          <b>Score</b>
          {selectedFactionScore} <FactionIcon faction={selectedFaction} />
        </div>
        <div>
          <b>Rank</b>
          {selectedFactionRank === 0 ? (
            "None"
          ) : (
            <FactionRankDisplay
              faction={selectedFaction}
              factionRank={selectedFactionRank}
            />
          )}
        </div>
        <div>
          <b>Next rank</b>
          {nextRankThreshold === null ? (
            "(MAX!)"
          ) : (
            <>
              {nextRankThreshold} <FactionIcon faction={selectedFaction} />
            </>
          )}
        </div>
      </>
    );
  })();

  return (
    <div className="select-faction-display">
      <div>
        <h2>Faction</h2>
        {factionsDom}
      </div>
      <div>
        <div className="select-faction-actions">
          <div>{factionActionDom}</div>
          {factionScoreDom}
        </div>
        <div
          className="select-faction-blurb"
          style={{
            backgroundColor:
              selectedFaction === null
                ? "var(--tray-bg-color)"
                : factionColor(selectedFaction, 900),
          }}
        >
          {factionDetailsDom}
        </div>
      </div>
    </div>
  );
}

export default SelectFactionDisplay;
