import { create } from "zustand";

export interface UserLogState {
  userLog: string;
  addToUserLog: (msg: string) => void;
  clearLog: () => void;
}

export const useUserLogStore = create<UserLogState>((set) => ({
  userLog: "",
  addToUserLog: (msg) =>
    set((state) => ({
      userLog: state.userLog + "\n" + msg,
    })),
  clearLog: () => set({ userLog: "" }),
}));
